import * as React from 'react';
import {useState,useEffect} from 'react';
import { Table } from 'antd';
import { Select } from 'antd';
import moment from 'moment';
import { Form } from 'antd';
import {  Col, Input, Row } from 'antd';
import { Tabs } from 'antd';
import { Pagination } from 'antd';
//import swal from 'sweetalert';
import {  useDispatch } from "react-redux";
//import { DatePicker  } from 'antd';
import  {requestApi}  from '../../api/index';
//import FormularioVentaComponent from './FormularioCompra';
 import { DatePicker } from 'antd';
 import {  Button } from 'antd';
const { Option } = Select;



const  GestionReport=()=> {

  const { TabPane } = Tabs;
  const { RangePicker } = DatePicker;
  const columns = [
    
    {
      title: 'Tipo Orden',
      dataIndex: 'typeOrder',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Cantidad',
      dataIndex: 'cantidad',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'total',
      dataIndex: 'total',
      sorter: false,
      render: data => `s./${data}`,
      width: '20%'
    }
    
  ];
  const [loading,setLoading] = useState(false);
  const [listadoDataReporte,setListadoDataReporte] = useState([]);
  const [listadoDataTypeOrden] = useState([
    {
      id:"",
      label:"TODOS"
    },
    {
      id:"VENTA",
      label:"VENTA"
    },
    {
      id:"COMPRA",
      label:"COMPRA"
    },
    {
      id:"ORDEN",
      label:"ORDEN"
    }
    
  ]);
  const listRef = React.createRef();
  const [form] = Form.useForm();
  const [fechaRegistroFiltro, setFechaRegistroFiltro] = useState(null);
  const [total, setTotal] = useState(0);
  useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        if (!isSubscribed) return;
        (async()=>{
          await listarDataKardex();
        })();
      }
      
      return () => isSubscribed = false;
  },[]);

  const refrescarListado=()=>{
    listarDataKardex();
  }

  const listarDataKardex = async() => {
    setLoading(true);
    try{
      let reqFechaInicio=fechaRegistroFiltro==null || fechaRegistroFiltro=="" ? "" : moment(fechaRegistroFiltro[0]).format('DD-MM-YYYY') ;
      let reqFechaFin=fechaRegistroFiltro==null || fechaRegistroFiltro=="" ? "" : moment(fechaRegistroFiltro[1]).format('DD-MM-YYYY') ;
      let typoOrden=form.getFieldValue("typoOrden") ? form.getFieldValue("typoOrden")  : "";
      let respon=await requestApi(
        `report/reportKardex?fechaInicio=${reqFechaInicio}&fechaFin=${reqFechaFin}&typeOrden=${typoOrden}`,
        {},
        "get"
      );
      setListadoDataReporte(respon.state ? respon.data : []  );
      setTotal(respon.state ? respon.total :0);
    }catch(e){
      return e;
    }finally{
      setLoading(false);
    }
   
  };
 

 const refresPagina=async()=>{
  await listarDataKardex();
}

  return (
    <div ref={listRef}   className="contentPago">
      <Tabs defaultActiveKey="1" onChange={(e)=>{console.log("eeeeuuu",e);
        if(e=="1"){
          refrescarListado(); 

        }
      
      }} >
        <TabPane tab="Listado" key="1">
          <>
            <Form
              form={form}
              layout="vertical"
            >
            <Row>
              <Col style={{padding:8}} xs={{ span: 24 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 6 }}>
                    <Form.Item 
                      name="rangos" label="Rangos"
                    
                    >
                      <RangePicker  
                        value={fechaRegistroFiltro==null ? [] : [moment(fechaRegistroFiltro[0], 'YYYY-MM-DD'), moment(fechaRegistroFiltro[1], 'YYYY-MM-DD')]}
                        onChange={(value)=>{
                          if( value==null){
                            setFechaRegistroFiltro(null);
                            return false;
                          }
                          if(value!=undefined ){
                            setFechaRegistroFiltro(value);
                          }else{
                            setFechaRegistroFiltro(null);
                          }
                        }
                        }  />
                  </Form.Item>
              </Col>
              <Col style={{padding:8}} xs={{ span: 24 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 6 }}>
                <Form.Item 
                  name="typoOrden" 
                  label="Tipo Orden">
                    <Select   
                      placeholder={""}  
                      bordered={true}  
                      value={null}
                      name={"typoOrdenSelect"}
                      label={"Seleccione Tipo Orden"}>
                      {
                        (listadoDataTypeOrden).map((item, i) => (
                          (<Option value={item.id} key={i}>{item.label}</Option>)
                      
                        )  
                        )
                      }
                    </Select>
                </Form.Item>
              </Col>
              <Col style={{padding:8}} xs={{ span: 24 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 6 }}>
                <Button style={{marginTop:30}} type="primary" onClick={()=>{refresPagina();}}>Buscar</Button>
    
              </Col>           
            </Row>
            
            </Form>
            <Table pagination={true} dataSource={listadoDataReporte} columns={columns} loading={loading} />
            <span><strong>Total:</strong>S./{parseFloat(total).toFixed(2)}</span>
          </> 
        </TabPane>
        
      </Tabs>
    </div>
    );
 
}
export default GestionReport;