import {useState,useEffect} from 'react';
import  {requestApi}  from '../../api/index';
import swal from 'sweetalert';

import {
  Form,
  Button,
  Select,
} from 'antd';

import { Card } from 'antd';
import * as React from 'react';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../redux/actions';
import { Transfer } from 'antd';

const { Option } = Select;

const  GestionPermisos=(props)=> {
      const mockData = [];
      for (let i = 0; i < 20; i++) {
        mockData.push({
          key: i.toString(),
          title: `content${i + 1}`,
          description: `description of content${i + 1}`,
        });
    }
    const [form] = Form.useForm();
    const [componentSize, setComponentSize] = useState('large');
    const listRef = React.createRef();
    const [sizeWidth, setSizeWidth] = React.useState(window.innerWidth);
    const [large, setLarge] = React.useState(false);
    const [selectedKeys, setSelectedKeys] = useState([]);
    const [opcionesSeleccionadosPermisos, setOpcionesSeleccionadosPermisos] = React.useState([]);
    const [listadoOpciones, setListadoOpciones] = React.useState([]);
    const [cargoDefaultUsuario, setCargoDefaultUsuario] = React.useState(null);
    //const [listadoOpcionesSinAsignar, setListadoOpcionesSinAsignar] = React.useState([]);
    //const [listadoOpcionesAsignados, setListadoOpcionesnAsignados] = React.useState([]);
    const [oneWay, setOneWay] = React.useState(false);
    const initialTargetKeys = mockData.filter(item => +item.key > 10).map(item => item.key);
    const [targetKeys, setTargetKeys] = useState(initialTargetKeys);
    const [listCharge, setChargeData] = React.useState([]);
    const onChange = (newTargetKeys, direction, moveKeys) => {
      setTargetKeys(newTargetKeys);
    };
  
    const dispatch = useDispatch();
    const onFormLayoutChange = ({ size }) => {
      setComponentSize(size);
    };
    useEffect(()=>{
        let isSubscribed = true
        if(listRef?.current!=null){
          reportWindowSize();
          obtenerCargoUsuario();
          cargarCargo();
         
          listarAsignados(null);
          //listadoOpcionesSistema();
          setListadoOpciones([]);
          //listadoOpcionesSistema();
          window.onresize = reportWindowSize;
          if (!isSubscribed) return;
        }
        
        return () => isSubscribed = false;
    },[]);
   
    const cargarCargo = async() => {
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `dataGeneral/loaderCharge`,
          {},
          "get"
        );
        setChargeData(respon.estado ? respon.data : []  );
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }catch(e){
        return [];
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));      
      }
    
    };
    const obtenerCargoUsuario = async() => {
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      
      try{
        let respon=await requestApi(
          `permissions/getUserCharge`,
          {},
          "get"
        );
        if(respon.estado){
          form.setFieldsValue({
            Cargo: respon.data[0].id+""
          });
          listadoOpcionesSistema();
          listarAsignados(respon.data[0].id+"");
        }else{
          form.setFieldsValue({
            Cargo: []
          });
        }
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }catch(e){
        return [];
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));      
      }
    
    };
    const listarAsignados = async(reqIdCargo) => {
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      if(reqIdCargo==null ){
        setTargetKeys([] );
      
        return false;
      }
      try{
        let respon=await requestApi(
          `permissions/loadAssignedPermissions?cargoId=`+reqIdCargo,
          {},
          "get"
        );
        let tempIdArray=[];
        (respon.estado ? respon.data : [] ).forEach(element => {
          tempIdArray.push(parseInt(element.id));
        });
        console.log("tempIdArray",tempIdArray);
        setTargetKeys(tempIdArray );
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }catch(e){
        return [];
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));      
      }
    
    };
    const listadoOpcionesSistema = async() => {
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `dataGeneral/systemOptionsList`,
          {},
          "get"
        );
        setListadoOpciones(respon.estado ? respon.data : []  );
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }catch(e){
        return [];
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));      
      }
    
    };
    const reportWindowSize=(event)=>{
      setSizeWidth(window.innerWidth);
      if(window.innerWidth>=900){
        setLarge(true);
      }else{
        setLarge(false);
      }
    }
    const seleccionarCargo = value => {
        if(value==null){
          setListadoOpciones([]);
        }else{
          listadoOpcionesSistema();
          listarAsignados(value);
        }
    };
  const registrarActualizarPermisos=async()=>{
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
     // let targetKeys;
      let dataRequest={
        "cargoId":form.getFieldValue("Cargo"),
        "opcionesPermisos":targetKeys
        };
      let respon=await requestApi(
        `permissions/asignarPermisosAsignados`,
        dataRequest,
        "post"
      );
      if(respon.estado){
        swal({
          title: "Actualizar Permisos.",
          text: respon.mensaje,
          icon:  respon.estado ? "success":"error",
          button: "aceptar"
        });
      }else{
        swal({
          title: "Actualizar Permisos.",
          text: respon.mensaje,
          icon:  respon.estado ? "success":"error",
          button: "aceptar"
        });
      }
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
   
  }
  
  return (
    <div ref={listRef}   className="contentPago">
      <Card title={"Gestionar Permisos"} bordered={false} style={{ width: "100%" }}>
        <>
        <Form
            labelCol={{
            span: 4,
            }}
            wrapperCol={{
            span: 14,
            }}
            layout="vertical"
            initialValues={{
            size: componentSize,
            }}
            onValuesChange={onFormLayoutChange}
            size={componentSize}
            form={form}
        >
            <Form.Item 
              name={[ 'Cargo']} 
              label="Cargo"
              rules={[{ required: false, message: 'Se Requiere de Cargo.' }]}
              wrapperCol={{ lg: 14,md:14,sm: 24 }}>
              <Select  placeholder="Seleccione Cargo"  style={{ width: "100%" }} onChange={(e)=>{seleccionarCargo(e)}}>
                {listCharge.map(item => (
                  <Option id={item.id} key={item.id}>{item.name}</Option>
                ))}
              </Select>
            </Form.Item>
        </Form>
        <div>
          <Transfer
            titles={['Permiso', 'Permisos Asignados']}
            dataSource={listadoOpciones}
            targetKeys={targetKeys}
            onChange={onChange}
            render={item => item.nombre}
            oneWay={oneWay}
            listStyle={{
              width: "100%",
              height: 300,
            }}
            
          />
          <Button  htmlType="submit" className="btnAsignar" type="primary" onClick={()=>{registrarActualizarPermisos();}}>Asignar Permisos</Button>
          
        </div>
        </>
      </Card>
    </div>
    );
 
}
export default GestionPermisos;