import {useState,useEffect} from 'react';
import  {requestApi}  from '../../api/index';
import swal from 'sweetalert';
import { PageHeader } from 'antd';
import { Card } from 'antd';
import * as React from 'react';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../redux/actions';
import {
    Form,
    Input,
    Button,
    Switch,
  } from 'antd';
const  FormularioEditarUsuario=(props)=> {
    const [form] = Form.useForm();
    const [componentSize, setComponentSize] = useState('large');
    const listRef = React.createRef();
    const [vigencia, setVigencia] = React.useState(true);
    const dispatch = useDispatch();
    const onFormLayoutChange = ({ size }) => {
      setComponentSize(size);
    };
    useEffect(()=>{
          let isSubscribed = true
          if(listRef?.current!=null){
            (async()=>{
              
              if(props.dataEdit!=null){
                
                form.setFieldsValue({
                  nombres: props.dataEdit.name
                });
                setVigencia(props.dataEdit.validity);
              }else{
                form.setFieldsValue({
                  nombres: ""
                });
                setVigencia(true);
                
              }
            })();

            if (!isSubscribed) return;
          }
          
          return () => isSubscribed = false;
    },[]);
   
    const onChangeVigencia= ()=> {
      setVigencia(!vigencia);
    }
   
    
    const registrarUsuario=async()=>{
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let dataRequest={
          "id":props.dataEdit.id,
          "name":form.getFieldValue("nombres"),
          "validity":vigencia
        };
        let respon=await requestApi(
          `brand/updateBrand`,
          dataRequest,
          "post"
        );
        if(respon.estado){
          swal({
            title: "Actualizar Marca.",
            text: respon.mensaje,
            icon:  respon.estado ? "success":"error",
            button: "aceptar"
          });
          props.atras();
          props.refrescarListado();
        }else{
          swal({
            title: "Actualizar Marca.",
            text: respon.mensaje,
            icon:  respon.estado ? "success":"error",
            button: "aceptar"
          });
        }
      }catch(e){
        return e;
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }
    
    }
   
    return (
    <div ref={listRef}   className="contentPago">
      <Card title={<PageHeader
        className="site-page-header"
        onBack={() => props.atras()}
        title="Editar Marca"
        subTitle={null}
      />} bordered={false} style={{ width: "100%" }}>
        <>
        <Form
            labelCol={{
            span: 4,
            }}
            wrapperCol={{
            span: 14,
            }}
            layout="vertical"
            initialValues={{
            size: componentSize,
            }}
            onValuesChange={onFormLayoutChange}
            size={componentSize}
            form={form}
        >
            <Form.Item 
              name={[ 'nombres']}
              label="Nombres"
              max="500"
              rules={[{ required: true, message: 'Se Requiere de Nombre.' }]}
              wrapperCol={{ lg: 14,md:14,sm: 24 }}
              >
                <Input />
            </Form.Item>

            <Form.Item 
              name={[ 'vigencia']} 
              label="Vigencia"
              wrapperCol={{ lg: 14,md:14,sm: 24 }}
            >
                 <Switch checked={vigencia} onChange={onChangeVigencia} />
            </Form.Item>
            <Button  htmlType="submit" className="btnNuevoRegistrar" type="primary" onClick={()=>{ registrarUsuario()}}>Actualizar Categoría</Button>
        </Form>
        </>
      </Card>
    </div>
    );
 
}
export default FormularioEditarUsuario;