import * as React from 'react';
import {useState,useEffect} from 'react';
import { Table } from 'antd';
import { Select } from 'antd';
import moment from 'moment';
import { Form } from 'antd';
import {  Col, Input, Row } from 'antd';
import { Tabs } from 'antd';
import { Pagination } from 'antd';
//import swal from 'sweetalert';
import {  useDispatch } from "react-redux";
//import { DatePicker  } from 'antd';
import  {requestApi}  from '../../api/index';
import FormularioVentaComponent from './FormularioCompra';
 import { DatePicker } from 'antd';
 import {  Button } from 'antd';
const { Option } = Select;



const  GestionCompra=()=> {

  const { TabPane } = Tabs;
  const { RangePicker } = DatePicker;
  const columns = [
    
    {
      title: 'fecha Registro',
      dataIndex: 'registrationDate',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Còdigo Compra',
      dataIndex: 'code',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Ruc Proveedor',
      dataIndex: 'rucProvider',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Razón Social',
      dataIndex: 'businessNameProvider',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Usuario Registro',
      dataIndex: 'idAdminRegister',
      sorter: false,
      render: (data,item) => `${item.names+" "+item.lastName +" "+item.motherLastName}`,
      width: '20%'
    },
    {
      title: 'Estado Compra',
      dataIndex: 'stateSales',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    
  ];
  const [loading,setLoading] = useState(false);
  const dispatch = useDispatch();
  const [listadoDataCompra,setListadoDataCompra] = useState([]);
  const [listadoUsuarioAdmin,setListadoUsuarioAdmin] = useState([]);
  const listRef = React.createRef();
  const [contarTotal, setContarTotal] = useState(0);
  const [paginaActual, setPaginaActual] = useState(1);
  const [cantidadContenido, setCantidadContenido] = useState(10);
  const [form] = Form.useForm();
  const [fechaRegistroFiltro, setFechaRegistroFiltro] = useState(null);
  useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        if (!isSubscribed) return;
        (async()=>{
          await cargarUsuarios();
          await listarDataCompra(paginaActual-1,cantidadContenido);
        })();
      }
      
      return () => isSubscribed = false;
  },[]);

  const refrescarListado=()=>{
    listarDataCompra(paginaActual-1,cantidadContenido);
  }

  const listarDataCompra = async(reqpaginaActual,reqcantidadContenido) => {
    setLoading(true);
    try{
      let idAdminRegister="";
      if(form.getFieldValue("usuarioRegistro")!=null){
        let itemsss=listadoUsuarioAdmin.filter((val)=>{
            return val.numberDocument==form.getFieldValue("usuarioRegistro");
        });
        idAdminRegister=itemsss[0].id;
      }
      let codigoCompra=form.getFieldValue("codigoCompra") ? form.getFieldValue("codigoCompra")  : "";
      let reqRuc=form.getFieldValue("rucProveedor") ? form.getFieldValue("rucProveedor")  : "";
      let reqFechaInicio=fechaRegistroFiltro==null || fechaRegistroFiltro=="" ? "" : moment(fechaRegistroFiltro[0]).format('DD-MM-YYYY') ;
      let reqFechaFin=fechaRegistroFiltro==null || fechaRegistroFiltro=="" ? "" : moment(fechaRegistroFiltro[1]).format('DD-MM-YYYY') ;
      let respon=await requestApi(
        `purchase/listPurchase?code=${codigoCompra}&ruc=${reqRuc}&idAdminRegister=${idAdminRegister}&fechaInicio=${reqFechaInicio}&fechaFin=${reqFechaFin}&paginaActual=${reqpaginaActual}&cantidadContenido=${reqcantidadContenido}`,
        {},
        "get"
      );
      setListadoDataCompra(respon.state ? respon.data : []  );
      setContarTotal(respon.state ? respon.countData : 0);
    }catch(e){
      return e;
    }finally{
      setLoading(false);
    }
   
  };
  const cargarUsuarios = async() => {
   
    try{
      let respon=await requestApi(
        `userAdmin/listUser`,
        {},
        "get"
      );
      setListadoUsuarioAdmin(respon.state ? respon.data : []  );
    }catch(e){
      return e;
    }finally{
    }
   
  };
  const changePagination=async(val,sizePagination)=>{
    setPaginaActual(val);
    setCantidadContenido(sizePagination);
    await listarDataCompra(paginaActual-1,cantidadContenido);
 }
 const refresPagina=async()=>{
  await listarDataCompra(paginaActual-1,cantidadContenido);
}

  return (
    <div ref={listRef}   className="contentPago">
      <Tabs defaultActiveKey="1" onChange={(e)=>{console.log("eeeeuuu",e);
        if(e=="1"){
          refrescarListado(); 

        }
      
      }} >
        <TabPane tab="Listado" key="1">
          <>
            <Form
              form={form}
              layout="vertical"
            >
            <Row>
              <Col style={{padding:8}} xs={{ span: 24 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 6 }}>
                    <Form.Item 
                      name="rangos" label="Rangos"
                    
                    >
                      <RangePicker  
                        value={fechaRegistroFiltro==null ? [] : [moment(fechaRegistroFiltro[0], 'YYYY-MM-DD'), moment(fechaRegistroFiltro[1], 'YYYY-MM-DD')]}
                        onChange={(value)=>{
                          if( value==null){
                            setFechaRegistroFiltro(null);
                            return false;
                          }
                          if(value!=undefined ){
                            setFechaRegistroFiltro(value);
                          }else{
                            setFechaRegistroFiltro(null);
                          }
                        }
                        }  />
                  </Form.Item>
                </Col>
              <Col style={{padding:8}} xs={{ span: 24 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 6 }}>
                  <Form.Item 
                    name="rucProveedor" label="ruc"
                  
                  >
                    <Input name="rucInput"
                        placeholder="" 
                  />
                </Form.Item>
              </Col>
              <Col style={{padding:8}} xs={{ span: 24 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 6 }}>
                  <Form.Item 
                    name="codigoCompra" label="Código Compra"
                  
                  >
                    <Input name=""
                        placeholder="" 
                  />
                </Form.Item>
              </Col>
              <Col style={{padding:8}} xs={{ span: 24 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 6 }}>
                <Form.Item 
                  name="usuarioRegistro" 
                  label="Usuarios Registro ">
                    <Select   
                      placeholder={""}  
                      bordered={true}  
                      allowClear
                      showSearch
                      name={"usuarioRegistroSelect"}
                      label={"Seleccione Usuarios Registro"}>
                      {
                        (listadoUsuarioAdmin).map((itemAddress, i) => (
                          (<Option value={itemAddress.numberDocument} key={itemAddress.numberDocument}>{itemAddress.numberDocument+ " - "+itemAddress.names+" "+itemAddress.lastName+" "+itemAddress.motherLastname}</Option>)
                      
                        )  
                        )
                      }
                    </Select>
                </Form.Item>
              </Col>
              <Col style={{padding:8}} xs={{ span: 24 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 6 }}>
                <Button style={{marginTop:30}} type="primary" onClick={()=>{refresPagina();}}>Buscar</Button>
    
              </Col>           
            </Row>
            
            </Form>
            <Table dataSource={listadoDataCompra} columns={columns} loading={loading} />
            <Pagination current={paginaActual} defaultCurrent={paginaActual} total={contarTotal}  onChange={(val,size)=>{changePagination(val,size)}} />
          </> 
        </TabPane>
        <TabPane tab="Gestionar Compra" key="2">
          <FormularioVentaComponent atras={()=>{refrescarListado();}}/>        
        </TabPane>
      </Tabs>
    </div>
    );
 
}
export default GestionCompra;