import * as React from 'react';
import {useState,useEffect} from 'react';
import { Table,Form, Input,Pagination } from 'antd';
import { DatePicker } from 'antd';
import {  Button ,Select} from 'antd';
import moment from 'moment';
import {  Col, Row } from 'antd';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../redux/actions';
import { List, Typography } from 'antd';
import  {requestApi}  from '../../api/indexTesis';

const { TextArea } = Input;
const  GestionAplicacionesUsadas=()=> {
  const { Option } = Select;
  const { RangePicker } = DatePicker;
 
  const [form] = Form.useForm();
  const dispatch = useDispatch();
  const listRef = React.createRef();
  
  const [inputAplicaciones, setInputAplicaciones] = useState(null);
  const [arrayListDataApp,setArrayListDataApp] = useState([]);
  const [arrayListDataAllApp,setArrayListDataAllApp] = useState([]);
  const [keyApp,setKeyApp] = useState("0");
  const [listadoMac, setListadoMac] = React.useState([]);
 
  const obtenerMac=async()=>{
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
        //(this.host+"listadoHistorial?id="+this.keyApp+"&navegador="+this.selected)
      //selectNavegador
      let response=await requestApi(
        "dataAplicativosMack",
        {},
        "get"
      );
      setListadoMac(response.data);
      //console.log("respon.data",respon.data);
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
  }
  const refrescarPagina=()=>{
    if(keyApp!=null && keyApp!="0"){
      console.log("keyApp-ee",keyApp.split(":").join(""));
      obtenerAplicaciones(keyApp.split(":").join(""));
    }else{
      setArrayListDataApp([]);
    }
  }
  const obtenerAplicaciones=async(dat)=>{
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
      console.log("keyApp",keyApp);
      //let stringKey=keyApp.spit(":").join("");
      let response=await requestApi(
        "listadoAplicaciones?id="+dat,
        {},
        "get"
      );
      setArrayListDataApp(response.data);
      setArrayListDataAllApp(response.data);
      //console.log("respon.data",respon.data);
    }catch(e){
      console.log("e",e);
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
}
useEffect(()=>{
  let isSubscribed = true
  if(listRef?.current!=null){
    if (!isSubscribed) return;
    (async()=>{
      await refrescarPagina();
      await obtenerMac();
    })();
  }
  
  return () => isSubscribed = false;
},[]);

useEffect(()=>{
  let isSubscribed = true
  if(listRef?.current!=null){
    if (!isSubscribed) return;
    (async()=>{
      //inputAplicaciones
      setArrayListDataApp(arrayListDataAllApp.filter( (value)=> {
        
        return value.toLowerCase().includes(inputAplicaciones.toLowerCase());
       })
      );
      

    })();
  }
  
  return () => isSubscribed = false;
},[inputAplicaciones]);
 

  return (
    <div ref={listRef}   className="contentPago">
        <>
        <Row>
            <Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 12 }} >
                <label>Maquina</label>
                <Select onChange={(e)=>{setKeyApp(e) }}
                    value={keyApp}        
                    style={{ width: "100%" }} >
                       <Option id={'0'} key={'0'}>{'Seleccione'}</Option>
                    {listadoMac.map(item => (
                      <Option id={item.r_mac} key={item.r_mac}>{item.r_mac}</Option>
                   ))}
                    
                </Select>            
            </Col>
            <Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 12 }} >
            
            <Button type="primary" style={{ marginTop: "25px",marginLeft: "25px" }} onClick={(e)=>{ refrescarPagina()}}>Buscar</Button>
   
            </Col>

        </Row>
        <TextArea placeholder="Buscar Aplicaciones" autoSize onChange={e => setInputAplicaciones(e.target.value)} />
      
          
          <List
            className="listProduct"
            itemLayout="horizontal"
            dataSource={arrayListDataApp}
            renderItem={item => (
              <List.Item>
                <List.Item.Meta
                  description={(item )}
                />
              </List.Item>
            )}
              />  
        </> 
        
    </div>
    );
 
}
export default GestionAplicacionesUsadas;