import {useState,useEffect} from 'react';
import  {requestApi}  from '../../api/index';
import swal from 'sweetalert';
import moment from 'moment';
import { PageHeader } from 'antd';
import { Card } from 'antd';
import * as React from 'react';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../redux/actions';
import { DatePicker } from 'antd';
import {
    Form,
    Input,
    Button,
    Select,
    Switch,Table,Modal
  } from 'antd';
const { Option } = Select;

const  FormularioCiudadano=(props)=> {
    const columnsPredio = [
      {
        title: 'Acción',
        dataIndex: 'key',
        sorter: false,
        render: data => <div style={{display:"flex"}}><Button className="btnAccion" type="primary" onClick={()=>{editarDireccion(data)}}>Editar</Button></div>
        ,
        width: '40%',
        key: 'accion-01',
      },
      {
        title: 'Código Dirección',
        dataIndex: 'codigoPredio',
        sorter: false,
        render: data => `${data}`,
        width: '20%',
        key: 'dni-01',
      },
      {
        title: 'Dirección',
        dataIndex: 'direccion',
        sorter: false,
        render: data => `${data}`,
        width: '20%',
        key: 'dni-01',
      },
      {
        title: 'Numero Dirección',
        dataIndex: 'numeroDireccion',
        sorter: false,
        width: '20%',
        key: 'nombres-01',
        render: data => `${data}`,
      },
      {
        title: 'Vigencia',
        dataIndex: 'vigenciaDireccionText',
        sorter: false,
        width: '20%',
        key: 'vigencia',
        render: data => `${data}`
      }
    ];

    const [form] = Form.useForm();
    const [formPredio] = Form.useForm();
    const [componentSize, setComponentSize] = useState('large');
    const listRef = React.createRef();
    const [vigencia, setVigencia] = React.useState(true);
    const [vigenciaDireccion, setVigenciaDireccion] = React.useState(true);
    const [generoData, setGeneroData] = React.useState([]);
    const [mensajeErrorDocumento, setMensajeErrorDocumento] = React.useState(null);
    const [fechaNacimiento, setFechaInicio] = useState(null);
    //const [mensajeErrorCorreo, setMensajeErrorCorreo] = React.useState(null);
    const [loading,setLoading] = useState(false);
    const [isModalDirecciones, setIsModalDirecciones] = React.useState(false);
    const [listadoDirecciones, setListadoDirecciones] = React.useState([]);
    const [direccionSeleccionada, setDireccionSeleccionada] = React.useState(null);

    const dispatch = useDispatch();
    const onFormLayoutChange = ({ size }) => {
      setComponentSize(size);
    };
    useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        if (!isSubscribed) return;
      }
      
      return () => isSubscribed = false;
  },[]);
    useEffect(()=>{
        let isSubscribed = true
        if(listRef?.current!=null){
          cargarGenero();
          if (!isSubscribed) return;
        }
        
        return () => isSubscribed = false;
    },[]);

    const onBlur=(val)=>{
      if(val.target.value){
        verificarDni(val.target.value);
      }else{
        setMensajeErrorDocumento(null);
      }
    }
 
    const verificarDni = async(dni) => {
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `datosUsuarioCiudadano/verificarDniUsuarioCiudadano?dni=`+dni,
          {},
          "get"
        );
        setMensajeErrorDocumento(respon.estado ? null : respon.mensaje  );
      }catch(e){
        return e;
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }
    
    };
    const registrarUsuario=async()=>{
  
      form.validateFields()
      .then(async(values) => {
        if(listadoDirecciones.length==0){
          swal({
            title: "Ciudadano",
            text: "Se  requiere  de almenos de un Predio.",
            icon: "warning",
            button: "aceptar"
          });
          return false;
        }
        dispatch(loaderSpiner({
          loaderSpiner:true
        }));
        try{
          let dataRequest={
            "numeroDocumento":form.getFieldValue("dni"),
            "nombre":form.getFieldValue("nombres"),
            "apellidoPaterno":form.getFieldValue("apellidoPaterno"),
            "apellidoMaterno":form.getFieldValue("apellidoMaterno"),
            "fechaNacimiento":form.getFieldValue("fechaNacimiento"),
            "idGenero":form.getFieldValue("genero"),
            "vigencia":vigencia,
            "arrayPredio":listadoDirecciones
          };
          let respon=await requestApi(
            `datosUsuarioCiudadano/registrarDatosCiudadano`,
            dataRequest,
            "post"
          );
          if(respon.estado){
            swal({
              title: "Registrar Ciudadano.",
              text: respon.mensaje,
              icon:  respon.estado ? "success":"error",
              button: "aceptar"
            });
            props.atras();
          }else{
            swal({
              title: "Registrar Ciudadano.",
              text: respon.mensaje,
              icon:  respon.estado ? "success":"error",
              button: "aceptar"
            });
          }
          console.log("respon",respon);
        }catch(e){
          return e;
        }finally{
          dispatch(loaderSpiner({
            loaderSpiner:false
          }));
        }
      })
      .catch((errorInfo) => {
        
      });

    
    }
    const verificarCorreo = async(correo) => {
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `usuarioAdmin/verificarCorreoUsuarioAdmin?correo=`+correo,
          {},
          "get"
        );
        //(respon.estado ? null : respon.mensaje  );
      }catch(e){
        return e;
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }
    
    };
    const cargarGenero = async() => {
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `dataGeneral/cargarGenero`,
          {},
          "get"
        );
        setGeneroData(respon.estado ? respon.data : []  );
      }catch(e){
        return e;
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }
    
  };
  //editarDireccion(data)
  const editarDireccion=(keyRequest)=>{
    let tempFormatList=JSON.parse(JSON.stringify(listadoDirecciones));
    let editValue=tempFormatList.filter((val)=>{
      return val.key==keyRequest;
    });
    setDireccionSeleccionada(keyRequest);
    formPredio.setFieldsValue({
      direccion: editValue[0].direccion,
      numeroDireccion: editValue[0].numeroDireccion,
      codigoPredio: editValue[0].codigoPredio
    });
    setVigenciaDireccion(editValue[0].vigenciaDireccion);
    setIsModalDirecciones(true);
  }
  const handleDirecciones= ()=> {
    formPredio.validateFields()
    .then(async(values) => {
      let tempListadoDirecciones=JSON.parse(JSON.stringify(listadoDirecciones));
      if(direccionSeleccionada==null){
        //codigoPredio
        tempListadoDirecciones.push({
          key:listadoDirecciones.length,
          direccion:formPredio.getFieldValue("direccion"),
          codigoPredio:formPredio.getFieldValue("codigoPredio"),
          numeroDireccion:formPredio.getFieldValue("numeroDireccion"),
          vigenciaDireccion:vigenciaDireccion,
          vigenciaDireccionText:vigenciaDireccion ? "ACTIVO" : "INACTIVO"
        } );
      }else{
        tempListadoDirecciones[direccionSeleccionada]={
          key:direccionSeleccionada,
          codigoPredio:formPredio.getFieldValue("codigoPredio"),
          direccion:formPredio.getFieldValue("direccion"),
          numeroDireccion:formPredio.getFieldValue("numeroDireccion"),
          vigenciaDireccion:vigenciaDireccion,
          vigenciaDireccionText:vigenciaDireccion ? "ACTIVO" : "INACTIVO"
        };
        
      }
     
      setListadoDirecciones(JSON.parse(JSON.stringify(tempListadoDirecciones)));
      setIsModalDirecciones(false);
      //console.log("tempListadoDirecciones-tempListadoDirecciones",JSON.stringify(tempListadoDirecciones));
    })
    .catch((errorInfo) => {
      //console.log("handleDirecciones"+errorInfo);
    });
  }
  const handleCancelDirecciones= (current)=> {
    setIsModalDirecciones(false);
    setDireccionSeleccionada(null);
  }
  const nuevoPredio=()=>{
    setIsModalDirecciones(true);
    formPredio.resetFields();
  }
  const disabledDate= (current)=> {
    return current && ( current> moment().add(-18, 'y').endOf('year')  );
  }
  return (
    <div ref={listRef}   className="contentPago">
      <Modal width={"50%"} 
      centered 
      title= {
        direccionSeleccionada==null ? "Dirección" : "Editar Dirección"
      } 
      visible={isModalDirecciones} 
      onOk={handleDirecciones} onCancel={handleCancelDirecciones}>
      <Form
          labelCol={{
          span: 24,
          }}
          wrapperCol={{
          span: 24,
          }}
          layout="vertical"
          initialValues={{
          size: componentSize,
          }}
          size={componentSize}
          form={formPredio}
          className="formPredio"
          >
              <Form.Item 
                name={['codigoPredio']}
                label="Código Predio"
                max="500"
                rules={[{ required: true, message: 'Se Requiere de Código Predio.' }]}
                wrapperCol={{ lg: 12,md:24,sm: 24 }}
                
              >
                  <Input
                  className="inputrowremove" 
                  type="text" 
                  autoComplete="off"
                />
                
              </Form.Item>
              <Form.Item 
                name={['direccion']}
                label="Dirección"
                max="500"
                rules={[{ required: true, message: 'Se Requiere de Dirección.' }]}
                wrapperCol={{ lg: 12,md:24,sm: 24 }}
                
              >
                  <Input
                  className="inputrowremove" 
                  type="text" 
                  autoComplete="off"
                />
                
              </Form.Item>
              <Form.Item 
                name={['numeroDireccion']}
                label="Numero Dirección"
                max="500"
                rules={[{ required: true, message: 'Se Requiere de Dirección.' }]}
                wrapperCol={{ lg: 12,md:24,sm: 24 }}
                
              >
                  <Input
                  className="inputrowremove" 
                  autoComplete="off"
                  type="number" onInput = {(e) =>{
                  if(!(e.target.value=="" || e.target.value==null) ){  
                    e.target.value = Math.max(0, parseInt(e.target.value) ).toString().slice(0,8)
                  }
                  
                  }}
                />
              </Form.Item>
              <Form.Item 
                name={[ 'vigenciaDir']} 
                label="Vigencia"
                wrapperCol={{ lg: 12,md:14,sm: 24 }}
              >
                  <Switch checked={vigenciaDireccion} onChange={(e)=>{setVigenciaDireccion(e)}} />
              </Form.Item>
          </Form>
      </Modal> 
      <Card title={<PageHeader
        className="site-page-header"
        onBack={() => props.atras()}
        title="Nuevo Tributario"
        subTitle={null}
      />} bordered={false} style={{ width: "100%" }}>
          <>
          <Form
              labelCol={{
              span: 24,
              }}
              wrapperCol={{
              span: 24,
              }}
              layout="vertical"
              initialValues={{
              size: componentSize,
              }}
              onValuesChange={onFormLayoutChange}
              size={componentSize}
              form={form}
          >
              <Form.Item 
                name={['dni']}
                label="dni"
                max="500"
                rules={[{ required: true, message: 'Se Requiere de Dni.' }]}
                wrapperCol={{ lg: 12,md:24,sm: 24 }}
                
              >
                  <Input
                  className="inputrowremove" 
                  autoComplete="off"
                  type="number" onInput = {(e) =>{
                  setMensajeErrorDocumento(null);
                  if(!(e.target.value=="" || e.target.value==null) ){  
                    e.target.value = Math.max(0, parseInt(e.target.value) ).toString().slice(0,8)
                  }
                  
                  }}
                  onBlur={(e)=>{onBlur(e);}}
                />
                
              </Form.Item>
              {
                    mensajeErrorDocumento==null ? null :
                  <span className="errorMensaje">{mensajeErrorDocumento}</span>
                }
              <Form.Item 
                name={[ 'nombres']}
                label="Nombres"
                max="500"
                rules={[{ required: true, message: 'Se Requiere de Nombres.' }]}
                wrapperCol={{ lg: 12,md:24,sm: 24 }}
                >
                  <Input autoComplete="off" />
              </Form.Item>
              <Form.Item 
                  name={['apellidoPaterno']}
                  label="Apellido Paterno"
                  max="500"
                  rules={[{ required: true, message: 'Se Requiere de Apellido Paterno.' }]}
                  wrapperCol={{ lg: 12,md:24,sm: 24 }}>
                  <Input autoComplete="off" />
              </Form.Item>
              <Form.Item 
                
                name={[ 'apellidoMaterno']}
                label="Apellido Materno"
                max="500"
                rules={[{ required: true, message: 'Se Requiere de Apellido Materno.' }]}
                wrapperCol={{ lg: 12,md:24,sm: 24 }}
                >
                  <Input autoComplete="off" />
              </Form.Item>
              <Form.Item 
                name={[ 'fechaNacimiento']}
                label="Fecha Nacimiento"
                max="500"
                rules={[{ required: true, message: 'Se Requiere de Fecha Nacimiento.' }]}
                wrapperCol={{ lg: 12,md:24,sm: 24 }}
                >
                  <DatePicker 
                    placeholder="Fecha Nacimiento"
                    disabledDate={disabledDate}  
                    style={{width:"100%"}}
                    value={fechaNacimiento==null ? null :  moment(fechaNacimiento, 'YYYY-MM-DD')} 
                    onChange={(value)=>{
                      if(value!=null){
                        setFechaInicio(value._d);
                      }else{
                        setFechaInicio(null);
                      }
                    
                    } } />
              </Form.Item>
              <Form.Item 
                name={[ 'genero']} 
                label="Genero"
                rules={[{ required: true, message: 'Se Requiere de Genero.' }]}
                wrapperCol={{ lg: 12,md:14,sm: 24 }}>
                  <Select allowClear placeholder="Seleccione Genero"  style={{ width: "100%" }} >
                  {generoData.map(item => (
                    <Option id={item.id} key={item.id}>{item.descripcion}</Option>
                  ))}
                </Select>
              </Form.Item>
              <Form.Item 
                name={[ 'vigencia']} 
                label="Vigencia"
                wrapperCol={{ lg: 12,md:14,sm: 24 }}
              >
                  <Switch checked={vigencia} onChange={()=>{setVigencia(!vigencia);}} />
              </Form.Item>
              <Button  className="btnNuevoRegistrar" type="primary" onClick={()=>{ nuevoPredio()}}>Nuevo Predio</Button>
              <Table
                className="contentTable"
                scroll={{ x: 'calc(700px + 50%)',  y: 1500 }}
                columns={columnsPredio}
                dataSource={listadoDirecciones}
                rowKey={(record) =>(record.direccion+"-"+record.numeroDireccion)}
                pagination={false}
                loading={loading}
              />
              <Button style={{marginTop:60}} htmlType="submit" className="btnNuevoRegistrar" type="primary" onClick={()=>{ registrarUsuario()}}>Registrar</Button>
          </Form>
          </>
        
        </Card>
    </div>
    );
 
}
export default FormularioCiudadano;