import * as React from 'react';
import {useState,useEffect} from 'react';
import { Table } from 'antd';
import { Select } from 'antd';
import { Card } from 'antd';
import {  Button } from 'antd';
import { Pagination } from 'antd';
import { Menu } from 'antd';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../redux/actions';
import  {requestApi}  from '../../api/index';
import FormularioCategoriaComponent from './FormularioCategoria';
import FormularioEditarCategoriaComponent from './FormularioEditarCategoria';

import { Input } from 'antd';
const { Option } = Select;



const  GestionUsuarios=()=> {
  const menu = (
    <Menu>
      <Menu.Item key="0">
        <a href="https://www.antgroup.com">Excel</a>
      </Menu.Item>
      <Menu.Item key="1">
        <a href="https://www.aliyun.com">Pdf</a>
      </Menu.Item>
    </Menu>
  );

  /*
  
    "id": element.r_id,
        "numberDocument": element.r_numberdocument,
        "email": element.r_correo,
        "names": element.r_names,
        "lastName": element.r_lastname,
        "motherLastName": element.r_motherlastname,
        "validity": element.r_validity==1 ? true : false,
        "Descriptionvalidity": element.r_validity==1 ? "ACTIVO" : "INACTIVO",
        "gender": element.r_idgender,
        "descriptionGender": element.r_descriptiongender,
        "descriptionCharge": element.r_jobtitle,
        "idCharge": element.r_idjobtitle
  */
  const columns = [
    {
      title: 'Acción',
      dataIndex: 'id',
      sorter: false,
      render: data => <Button type="primary" onClick={()=>{verDatos(data)}}>Editar</Button>
      ,
      width: '20%'
    },
    {
      title: 'Nombre',
      dataIndex: 'name',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Vigencia',
      dataIndex: 'validityText',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    }
  ];
  const [loading,setLoading] = useState(false);
  const dispatch = useDispatch();
  const [listadoDataUsuario,setListadoDataUsuario] = useState([]);
  const [countFullData,setCountFullData] = useState(10);
  const listRef = React.createRef();
  const [tipoVista, setTipoVista] = useState(1);
  const [contarTotal, setContarTotal] = useState(0);
  const [paginaActual, setPaginaActual] = useState(1);
  const [cantidadContenido, setCantidadContenido] = useState(10);
  const [categoriaSeleccionada, setCategoriaSeleccionado] = useState(null);
  const [tipoBusqueda, setTipoBusqueda] = useState("completeNames");
  const [inputSearch, setInputSearch] = useState("");
  //const [data,setData] = useState([]);
  useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        if (!isSubscribed) return;
        (async()=>{
          await contarUsuario();
          await listarCategory(paginaActual-1,cantidadContenido);
        })();
      }
      
      return () => isSubscribed = false;
  },[]);
  const verDatos=async(reqId)=>{
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
      let respon=await requestApi(
        `category/seeCategory?id=`+reqId,
        {},
        "get"
      );
      setCategoriaSeleccionado(respon.estado ? respon.data[0] :null);
      setTipoVista(3);
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
  }
  const refrescarListado=()=>{
    listarCategory(paginaActual-1,cantidadContenido);
  }
  useEffect(()=>{
    let isSubscribed = true
    if(listRef?.current!=null){
      if (!isSubscribed) return;
      (async()=>{
        setLoading(true);
        //await contarUsuario();
        await listarCategory(paginaActual-1,cantidadContenido);
        setLoading(false);
      })();
    }
    
    return () => isSubscribed = false;
},[tipoBusqueda,inputSearch]);
  const listarCategory = async(reqpaginaActual,reqcantidadContenido) => {
   
    try{
      //userAdmin/listAdminUser?tipoBusqueda=as&valorBusqueda=&paginaactual=0&cantidadcontenido=10
      let respon=await requestApi(
        `category/listCategory?paginateActive=${reqpaginaActual}&countContent=${reqcantidadContenido}&typeSearch=${tipoBusqueda}&value=${inputSearch}`,
        {},
        "get"
      );
      setListadoDataUsuario(respon.estado ? respon.data : []  );
      setCountFullData(respon.estado ? respon.countDataFull : 0  );
    }catch(e){
      return e;
    }finally{
    }
   
  };
  const contarUsuario = async() => {
  
    try{//paginaActual-1,cantidadContenido
      //userAdmin/countAdminUser?tipoBusqueda=as&valorBusqueda=&paginaactual=0&cantidadcontenido=10
      let respon=await requestApi(
        `userAdmin/countAdminUser?paginaactual=${(paginaActual-1)}&cantidadcontenido=${cantidadContenido}&tipoBusqueda=${tipoBusqueda}&valorBusqueda=${inputSearch}`,
        {},
        "get"
      );
      setContarTotal(respon.estado ? respon.contar : 0  );
    }catch(e){
      return e;
    }finally{
     
    }
   
  };
  const changePagination=async(val,sizePagination)=>{
    setPaginaActual(val);
    setCantidadContenido(sizePagination);
    await contarUsuario();
    await listarCategory(val-1,sizePagination);
 }
  const nuevo=(e)=>{
    setTipoVista(2);
  }
  return (
    <div ref={listRef}   className="contentPago">
      {
        tipoVista==1 ?
          <div>
            <Card title="Gestión de Categoria" bordered={false} style={{ width: "100%" }}>
                <div className="contentBtnNuevo" style={{position:"relative"}}>
                  <Button type="primary"  className="btnNuevo" onClick={nuevo} >Nuevo</Button>
                 </div><br/>
                 <Input.Group style={{    display: "flex",
                    float: "right",
                    margin: 0,
                    padding: 0}} compact>
                  <div className="contentOption"> 
                    <Select className="selectcontentOption" defaultValue={tipoBusqueda} value={tipoBusqueda} onChange={(e)=>{ setTipoBusqueda(e);}}>
                        <Option value="completeNames">Nombre Completos</Option>
                        <Option value="numberDocument">numero Documento</Option>
                        <Option value="charge">cargo</Option>
                        <Option value="email">Correo</Option>
                    </Select>
                    <Input style={{ width: '60%' }} defaultValue={inputSearch}  onChange={(e)=>{ setInputSearch(e.target.value);}}/>
                  </div>
                </Input.Group>
            </Card>
          
            <Table
              className="tableGestionUsuario"
              scroll={{ x: 'calc(700px + 50%)',  y: 1500 }}
              columns={columns}
              dataSource={listadoDataUsuario}
              pagination={false}
              rowKey={record => record.key}
              loading={loading}
            />
           <Pagination  className="paginationTable paginationGestionUsuario" current={paginaActual} defaultCurrent={paginaActual} total={countFullData}  onChange={(val,size)=>{changePagination(val,size)}} />
                     
            </div> :
            null
      }

      {
        tipoVista==2 ? <FormularioCategoriaComponent atras={()=>{refrescarListado();setTipoVista(1)}}/> : null
      }
      {
        tipoVista==3 ? <FormularioCategoriaComponent dataEdit={categoriaSeleccionada} atras={()=>{setTipoVista(1)}} refrescarListado={()=>{refrescarListado()}}/> : null
      }
    </div>
    );
 
}
export default GestionUsuarios;