import React, { useEffect }  from 'react';
import { useHistory } from "react-router-dom";
import { BrowserRouter, Switch } from 'react-router-dom';
import { Route} from 'react-router';
import ListadoPagosComponent from '../components/ListadoPagos';
import GestionCompraComponent from '../components/gestionCompra/GestionCompra';

import ComponentNotFound from '../components/NotFound';
import SincronizacionDatosComponent from '../components/SincronizacionDatos';
import GestionUsuariosComponent from '../components/gestionUsuarios/GestionUsuarios';
import GestionPermisosComponent from '../components/gestionPermisos/GestionPermisos';
//import ListadoGestionImpuestoComponent from '../components/Impuesto/ListadoGestionImpuesto';
import GestionCiudadanosComponent from '../components/gestionCiudadanos/GestionCiudadanos';
//import GestionListadoGestionImpuestoCiudadano from '../components/Impuesto/gestionImpuestoCiudadano/ListadoGestionImpuestoCiudadano';
import GestionCargoComponent from '../components/gestionPermisos/gestionCargo/GestionCargo';
import ComponentContent from '../components/ContentGeneral';
import LoginComponent from '../components/login';
import GestionProductComponent from '../components/gestionProducto/GestionProducto';
import GestionCategoriaComponent from '../components/gestionCategoria/GestionCategoria';
import GestionMarcaComponent from '../components/gestionMarca/GestionMarca';
import GestionVentaComponent from '../components/gestionVenta/GestionVenta';
import GestionOrdenComponent from '../components/gestionOrden/GestionOrden';
import GestionKardexComponent from '../components/report/GestionKardex';
import GestionAplicacionesUsadas from '../components/gestionTesis/GestionAplicacionesUsadas';
import AppsNavegacion from '../components/gestionTesis/AppsNavegacion';
import GestionScreem from '../components/gestionTesis/GestionScreem';
import GestionKeyLogger from '../components/gestionTesis/GestionKeyLogger';

///GestionKeyLogger
const AppPrincipal=(props)=>{
  const history = useHistory();
  useEffect(()=>{
  },[history]);

  return (
    <BrowserRouter >
           
          <Switch>
          <Route
              path="/login"
              exact 
              render={() =><LoginComponent /> } />
            <Route
              path="/"
              exact 
              render={() =><ComponentContent> <ListadoPagosComponent /> </ComponentContent> } />
            <Route
              exact
              path="/pagos"
              render={() =><ComponentContent> <ListadoPagosComponent /> </ComponentContent> } />
            <Route
              exact 
              path="/sincronizacionDatos"
              render={() =><ComponentContent> <SincronizacionDatosComponent  /> </ComponentContent> } />
            <Route
              exact 
              path="/gestionUsuarios"
              render={() =><ComponentContent> <GestionUsuariosComponent  /> </ComponentContent> } />
          
          <Route
              exact 
              path="/gestionPermisos"
              render={() =><ComponentContent> <GestionPermisosComponent /> </ComponentContent> } />
          
            <Route
              exact 
              path="/gestionCiudadanos"
              render={() =><ComponentContent> <GestionCiudadanosComponent  /> </ComponentContent> } />
            <Route
              exact 
              path="/gestionCargo"
              render={() =><ComponentContent> <GestionCargoComponent  /> </ComponentContent> } />  
            <Route
              exact 
              path="/gestionProducto"
              render={() =><ComponentContent> <GestionProductComponent  /> </ComponentContent> } />  
            <Route
              exact 
              path="/gestionCategoria"
              render={() =><ComponentContent> <GestionCategoriaComponent  /> </ComponentContent> } />  

            <Route
              exact 
              path="/gestionMarca"
              render={() =><ComponentContent> <GestionMarcaComponent  /> </ComponentContent> } /> 

            <Route
              exact 
              path="/gestionVentas"
              render={() =><ComponentContent> <GestionVentaComponent  /> </ComponentContent> } />
            <Route
              exact 
              path="/gestionOrden"
              render={() =><ComponentContent> <GestionOrdenComponent  /> </ComponentContent> } />
            <Route
              exact 
              path="/gestionCompras"
              render={() =><ComponentContent> <GestionCompraComponent  /> </ComponentContent> } /> 
            <Route
              exact 
              path="/gestionKardex"
              render={() =><ComponentContent> <GestionKardexComponent  /> </ComponentContent> } /> 
            <Route
              exact 
              path="/gestionAplicacionesUsadas"
              render={() =><ComponentContent> <GestionAplicacionesUsadas  /> </ComponentContent> } /> 
            <Route
              exact 
              path="/historialNavegacion"
              render={() =><ComponentContent> <AppsNavegacion  /> </ComponentContent> } /> 
              <Route
                exact 
                path="/GestionScreem"
                render={() =><ComponentContent> <GestionScreem  /> </ComponentContent> } /> 

              <Route
                exact 
                path="/gestionKeyLogger"
                render={() =><ComponentContent> <GestionKeyLogger  /> </ComponentContent> } /> 



           <Route render={() =><> <ComponentNotFound  /> </> } />
     
          </Switch>
 
        </BrowserRouter>
      );
}
export default AppPrincipal;
//gestionKeyLogger