import {useState,useEffect} from 'react';
import  {requestApi}  from '../../api/index';
import { Table } from 'antd';
import { Modal } from 'antd';
import swal from 'sweetalert';
import { PageHeader } from 'antd';
/*
    "react-swal": "^3.0.0",
    "swal": "^0.1.0",
*/
import { Card } from 'antd';
import * as React from 'react';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../redux/actions';
import { Upload, Space } from 'antd';
import { UploadOutlined } from '@ant-design/icons';

import {
    Form,
    Input,
    Button,
    Select,
    Switch,
  } from 'antd';
const { Option } = Select;

const  FormularioUsuario=(props)=> {
  const columns = [
      {
        title: 'titulo',
        dataIndex: 'title',
        key: 'title',
      },
      {
        title: 'Vigencia',
        dataIndex: 'vigenciaCharacterist',
        key: 'vigenciaCharacterist',
        render: data =><> {data ? "ACTIVO" : "INACTIVO"}
        </>
      },

      
      
      {
        title: 'imagen',
        dataIndex: 'imagenCharacterist',
        key: 'imagenCharacterist',
        render: data =><> 
         <img style={{width:50,height:50}} src={data} alt="Logo" />
        </>
      },
      {
        title: 'Acción',
        dataIndex: 'key',
        sorter: false,
        render: data =><> <Button type="primary" onClick={()=>{editarDatos(data)}}>Editar</Button>
        <Button type="primary" danger onClick={()=>{eliminarCharacterist(data)}}>
          Eliminar
        </Button>
        </>
        ,
        width: '20%'
      },
    ];
    const [form] = Form.useForm();
    const [formModal] = Form.useForm();
    const [listPropety, setListPropety] = React.useState([]);
    const [componentSize, setComponentSize] = useState('large');
    const listRef = React.createRef();
    const [vigencia, setVigencia] = React.useState(true);
    const [vigenciaCharacterist, setVigenciaCharacterist] = React.useState(true);
    const [visibleCharacterist, setVisibleCharacterist] = React.useState(null);
    const [idPropetySelect, setIdPropetySelect] = React.useState(null);
    
    const [listImage, setListImage] = React.useState([]);
    
    /*
    fileList
    */
    const dispatch = useDispatch();
    const onFormLayoutChange = ({ size }) => {
      setComponentSize(size);
    };
    useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        (async()=>{
          if(props.dataEdit!=undefined && props.dataEdit!=null){
            
            form.setFieldsValue({
              nombres: props.dataEdit.name
            });
            setVigencia(props.dataEdit.validity);
            //detailt
            setListPropety(props.dataEdit.detailt);
          }else{
            form.setFieldsValue({
              nombres: ""
            });
            setVigencia(true);
            
          }
        })();

        if (!isSubscribed) return;
      }
      
      return () => isSubscribed = false;
    },[props.dataEdit]);
    useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        if (!isSubscribed) return;
      }
      
      return () => isSubscribed = false;
  },[]);
  const onChangeVigencia= (checked)=> {
    setVigencia(!vigencia);
    console.log(`switch to ${checked}`);
  }
  
  const registrarCategoria=async()=>{
    form.validateFields()
    .then(async(values) => {
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        
        if(props.dataEdit!=undefined && props.dataEdit!=null){
          dispatch(loaderSpiner({
            loaderSpiner:true
          }));
          try{
            let dataRequest={
              "id":props.dataEdit.id,
              "name":form.getFieldValue("nombres"),
              "validity":vigencia,
              listPropety:listPropety
            };
            let respon=await requestApi(
              `category/updateCategory`,
              dataRequest,
              "post"
            );
            if(respon.estado){
              swal({
                title: "Actualizar Usuario.",
                text: respon.mensaje,
                icon:  respon.estado ? "success":"error",
                button: "aceptar"
              });
              props.atras();
              props.refrescarListado();
            }else{
              swal({
                title: "Actualizar Usuario.",
                text: respon.mensaje,
                icon:  respon.estado ? "success":"error",
                button: "aceptar"
              });
            }
          }catch(e){
            return e;
          }finally{
            dispatch(loaderSpiner({
              loaderSpiner:false
            }));
          }
          return false;
        }
        let dataRequest={
          "name":form.getFieldValue("nombres"),
          "validity":vigencia,
          listPropety:listPropety
          
        };
        let respon=await requestApi(
          `category/registerCategory`,
          dataRequest,
          "post"
        );
        if(respon.estado){
          swal({
            title: "Registrar Categoría.",
            text: respon.mensaje,
            icon:  respon.estado ? "success":"error",
            button: "aceptar"
          });
          props.atras();
        }else{
          swal({
            title: "Registrar Categoría.",
            text: respon.mensaje,
            icon:  respon.estado ? "success":"error",
            button: "aceptar"
          });
        }
      }catch(e){
        return e;
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }
    })
    .catch((errorInfo) => {
    });
    
   
  }
  const editarDatos=(key)=>{
    let editPropety=listPropety.filter((val)=>{
      return val.key==key;
    });
    
    setIdPropetySelect(key);
    formModal.setFieldsValue({
      titulo: editPropety[0].title,
      vigencia: editPropety[0].vigenciaCharacterist,
    });
    setVigenciaCharacterist(editPropety[0].vigenciaCharacterist);
   let listDat=[];
    listDat.push( 
      
      {
        uid: -1,
        name: 'image.png',
        status: 'done',
        url: editPropety[0].imagenCharacterist,
        thumbUrl: editPropety[0].imagenCharacterist,
      }
      
      );
    setListImage(listDat);

    setVisibleCharacterist(true);
  }
  const eliminarCharacterist=(key)=>{
    swal({
      title: "Sessión",
      text: " ¿Deseas Eliminar Caracteristica?",
      icon: "warning",
    // buttons: true,
      dangerMode: true,
      buttons: {
        cancel: "Cancelar",
        catch: {
          text: "Aceptar",
          value: "catch",
        }
      }
    })
    .then((willDelete) => {
      if (willDelete) {
        //key
        let filterPropetyChracterist=listPropety.filter((val)=>{
          return val.key!=key;
        });
        setListPropety(filterPropetyChracterist);
      }
    });
  };
  const anadirItemCharacterist=()=>{
    if(idPropetySelect==null){
     formModal.validateFields()
      .then(async(values) => {
        let copiList=JSON.parse(JSON.stringify(listPropety));
        copiList.push({
          key:listPropety.length,
          imagenCharacterist:listImage.length ? listImage[0].url : null,
          title:formModal.getFieldValue("titulo"),
          vigenciaCharacterist:vigenciaCharacterist
        });
        setListPropety(copiList);
        setVisibleCharacterist(false);
      })
      .catch((errorInfo) => {
      });
     
    }else{
      formModal.validateFields()
      .then(async(values) => {
        let copiList=JSON.parse(JSON.stringify(listPropety));
        copiList[idPropetySelect]={
          id:copiList[idPropetySelect].id,
          key:idPropetySelect,
          vigenciaCharacterist:vigenciaCharacterist,
          imagenCharacterist:listImage.length ? listImage[0].url : null,
          title:formModal.getFieldValue("titulo")
        }
        setListPropety(copiList);
        setVisibleCharacterist(false);
      });
      
    }
    
    
  }
  const uploadMedia=(info)=>{
    if(info.file.status=="uploading" ){
      var reader = new FileReader();
      reader.readAsDataURL(info.file.originFileObj);
      reader.onload =  ()=> {
        let listDat=[];
        listDat.push( 
          
          {
            uid: -1,
            name: 'image.png',
            status: 'done',
            url: reader.result,
            thumbUrl: reader.result,
          }
          
          );
        setListImage(listDat);
        
      };
      reader.onerror =  (error)=> {
        console.log('Error: ', error);
      };
    }else{
      let removeList=listImage.filter((dataItem)=>{
          return dataItem.name!=info.file.name
      });
      setListImage(removeList);
    }
    

  }
  return (
    <div ref={listRef}   className="contentPago">
        <Modal  visible={visibleCharacterist} title={idPropetySelect==null ?  
          "Añadir Propiedades" : "Editar Propiedades"} 
          onOk={anadirItemCharacterist} onCancel={()=>{setVisibleCharacterist(!visibleCharacterist)}}>
        <Form
            labelCol={{
            span: 4,
            }}
            wrapperCol={{
            span: 14,
            }}
            layout="vertical"
            initialValues={{
            size: componentSize,
            }}
            size={componentSize}
            form={formModal}
           
        >
           <Form.Item 
              name={['titulo']}
              label="título"
              max="500"
              rules={[{ required: true, message: 'Se Requiere de título.' }]}
              labelCol={{ lg: 24,md:14,sm: 24 }}
              wrapperCol={{ lg: 24,md:14,sm: 24 }}
              >
                <Input maxLength="200" autoComplete="off" />
            </Form.Item>
            <Upload
                beforeUpload={false}
                listType="picture"
                maxCount={1}
                defaultFileList={[...listImage]}
                onChange={uploadMedia}
                fileList={[...listImage]} 
                
                >
                  <Button icon={<UploadOutlined />}>Imagen</Button>
            </Upload>
            <Form.Item 
              name={[ 'vigencia']} 
              label="Vigencia"
              wrapperCol={{ lg: 14,md:14,sm: 24 }}
            >
                 <Switch checked={vigenciaCharacterist} onChange={()=>{setVigenciaCharacterist(!vigenciaCharacterist)}} />
            </Form.Item>
        </Form>
      </Modal>
      <Card title={<PageHeader
          className="site-page-header"
          onBack={() => props.atras()}
          title={(props.dataEdit!=undefined && props.dataEdit!=null) ? "Editar Cátegoria" : "Nuevo Cátegoria" } 
          subTitle={null}
      />} bordered={false} style={{ width: "100%" }}>
        <>
        <Form
            labelCol={{
            span: 4,
            }}
            wrapperCol={{
            span: 14,
            }}
            layout="vertical"
            initialValues={{
            size: componentSize,
            }}
            onValuesChange={onFormLayoutChange}
            size={componentSize}
            form={form}
        >
            <Form.Item 
              name={[ 'nombres']}
              label="Nombres"
              max="500"
              rules={[{ required: true, message: 'Se Requiere de Nombre.' }]}
              wrapperCol={{ lg: 14,md:14,sm: 24 }}
              >
                <Input autoComplete="off" />
            </Form.Item>
            <Form.Item 
              name={[ 'vigencia']} 
              label="Vigencia"
              wrapperCol={{ lg: 14,md:14,sm: 24 }}
            >
                 <Switch checked={vigencia} onChange={onChangeVigencia} />
            </Form.Item>
            <Button  className="btnNuevoRegistrar" type="primary" onClick={()=>{ formModal.resetFields(); setVisibleCharacterist(true);setIdPropetySelect(null);setListImage([]);}}>Añadir Propiedades</Button>
            <Table  dataSource={listPropety} columns={columns} />
            <Button  htmlType="submit" className="btnNuevoRegistrar" type="primary" onClick={()=>{ registrarCategoria()}}>
              
            {(props.dataEdit!=undefined && props.dataEdit!=null) ?  
              "Actualizar Categoria" :"Registrar" }
              </Button>
        </Form>
        </>
      </Card>
    </div>
    );
 
}
export default FormularioUsuario;