import * as React from 'react';
import {useState,useEffect} from 'react';
import { Table,Form, Input,Pagination } from 'antd';

import {  Button ,Select} from 'antd';
import {  Col, Row } from 'antd';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../redux/actions';
import  {requestApi}  from '../../api/indexTesis';
import { Doughnut } from 'react-chartjs-2';

import {AppVerticarBar} from './estatisticaCuadros/VerticalBar';

//import { Doughnut } from 'react-chartjs-2';
//import faker from 'faker';
//ChartJS.register(ArcElement, Tooltip, Legend);
const  GestionAplicacionesUsadas=()=> {
  const { Option } = Select;
  //const data = ;
  const columns = [
    {
      title: 'Fecha de Navegación',
      dataIndex: 'r_fechanave',
      key: 'r_fechanave',
      responsive: ['lg']
    },
    {
      title: 'Fecha de Registro',
      dataIndex: 'r_fecharegistro',
      key: 'r_fecharegistro',
      responsive: ['lg']
    },
    {
      title: 'Navegador',
      dataIndex: 'r_navegador',
      key: 'r_navegador',
      responsive: ['lg']
    },
    {
      title: 'Url',
      dataIndex: 'r_url',
      key: 'r_url',
      responsive: ['lg'],
      render: data =><> 
         <span className='spanName' style={{maxWidth:600,display:"block"}}  > {data}</span>
      </>
    }
  ];
  const [form] = Form.useForm();
  const dispatch = useDispatch();
  const listRef = React.createRef();
  
  const [listadoMac, setListadoMac] = React.useState([]);
  const [selectNavegador, setSelectNavegador] = useState("chrome");
  const [listPropety, setListPropety] = React.useState([]);
  const [keyApp,setKeyApp] = useState("");
  const [paginaActualTabla,setPaginaActualTabla] = useState(1);
  const [paginaActual,setPaginaActual] = useState(1);
  const [labelPaginasMasUsadas,setLabelPaginasMasUsadas] = useState([]);
  const [valorPaginasMasUsadas,setValorPaginasMasUsadas] = useState([]);
  const [navegadoresMasUsados,setNavegadoresMasUsados] = useState(null);
  const [cantidadDatos,setCantidadDatos] = useState(1);
  const [cantidadDatosTabla,setCantidadDatosTabla] = useState(1);
  const [navegadorActivo,setNavegadorActivo] = useState("");
  //navegadorActivo
  const obtenerListadoHistorialCantidad=async(valPaginaActual)=>{
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
      
      let response=await requestApi(
        "listadoPaginasMasUsadas?id="+keyApp+"&paginaActual="+valPaginaActual,
        {},
        "get"
      );
      let cantidadEntera=parseInt(response.data.cantidadTotal);
      let cantidadFlotante=(response.data.cantidadTotal);
      let cantidadFormat=0;
      if(cantidadFlotante>cantidadEntera){
        cantidadFormat=parseInt(cantidadFlotante);
      }else{
        cantidadFormat=cantidadEntera;
      }
      setCantidadDatos((cantidadFormat));
      setValorPaginasMasUsadas(response.data.dataValues);
      setLabelPaginasMasUsadas(response.data.dataLabels);
      //setListPropety(response.data);

      //console.log("respon.data",respon.data);
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
}
const obtenerNavegadorActivos=async()=>{
  dispatch(loaderSpiner({
    loaderSpiner:true
  }));
  try{
    //obtenerNavegadorActivo?id=1&navegador=chrome&paginaActual=1
    let response=await requestApi(
      "obtenerNavegadorActivo?id="+keyApp+"&paginaActual=1",
      {},
      "get"
    );
    if(response.status=="success"){

      setNavegadorActivo((response.data.r_nombre));
    }else{
      
      setNavegadorActivo((""));
    }
 }catch(e){
    return e;
  }finally{
    dispatch(loaderSpiner({
      loaderSpiner:false
    }));
  }
}
const obtenerListadoNavegadoresMasUsados=async()=>{
  dispatch(loaderSpiner({
    loaderSpiner:true
  }));
  try{
      //(this.host+"listadoHistorial?id="+this.keyApp+"&navegador="+this.selected)
    //selectNavegador
    let response=await requestApi(
      "listadoNavegadoresMasUsados?id="+keyApp,
      {},
      "get"
    );
    let dataNavegadores = {
      labels: response.data.labels,
      datasets: [{
        label: 'My First Dataset',
        data: response.data.dataValues,
        backgroundColor: [
          'rgb(255, 99, 132)',
          'rgb(54, 162, 235)',
          'rgb(255, 205, 86)'
        ],
        hoverOffset: 4
      }]
    };
    setNavegadoresMasUsados(dataNavegadores);
    
    //console.log("respon.data",respon.data);
  }catch(e){
    return e;
  }finally{
    dispatch(loaderSpiner({
      loaderSpiner:false
    }));
  }
}
  const obtenerAplicaciones=async(vapaginaActualTabla)=>{
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
        //(this.host+"listadoHistorial?id="+this.keyApp+"&navegador="+this.selected)
      //selectNavegador
      let stringKey=keyApp.split(":").join("");
      let response=await requestApi(
        "listadoHistorial?id="+stringKey+"&navegador="+selectNavegador+"&paginaActual="+vapaginaActualTabla,
        {},
        "get"
      );
      setCantidadDatosTabla(response.cantidadTotal);
      setListPropety(response.data);
      //console.log("respon.data",respon.data);
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
  }

const changePagination=async(val,sizePagination)=>{
  setPaginaActual(val);

  await obtenerListadoHistorialCantidad(val) ;
}
const changePaginationTabla=async(val,sizePagination)=>{
  setPaginaActualTabla(val);

  await obtenerAplicaciones(val) ;
}
useEffect(()=>{
  let isSubscribed = true
  if(listRef?.current!=null){
   // console.log("dataDataSet",dataDataSet);
    if (!isSubscribed) return;
    (async()=>{
      await obtenerMac();
      await obtenerListadoNavegadoresMasUsados();
      await obtenerAplicaciones(paginaActualTabla);
      await obtenerListadoHistorialCantidad(paginaActual);
      obtenerNavegadorActivos();
    })();
  }
  
  return () => isSubscribed = false;
},[]);

const obtenerMac=async()=>{
  dispatch(loaderSpiner({
    loaderSpiner:true
  }));
  try{
      //(this.host+"listadoHistorial?id="+this.keyApp+"&navegador="+this.selected)
    //selectNavegador
    let response=await requestApi(
      "dataAplicativosMack",
      {},
      "get"
    );
    setListadoMac(response.data);
    //console.log("respon.data",respon.data);
  }catch(e){
    return e;
  }finally{
    dispatch(loaderSpiner({
      loaderSpiner:false
    }));
  }
}
 

  return (
    <div ref={listRef}   className="contentPago">
        <>
        

        <Row>
            <Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 12 }} >
                <label>Maquina</label>
                <Select onChange={(e)=>{setKeyApp(e) }}
                    value={keyApp}        
                    style={{ width: "100%" }} >
                      <Option id={''} key={''}>{'Seleccione'}</Option>
                    {listadoMac.map(item => (
                      <Option id={item.r_mac} key={item.r_mac}>{item.r_mac}</Option>
                  ))}
                    
                </Select>            
            </Col>
            <Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 12 }} >
                <label>Navegadores</label>
                <Select onChange={(e)=>{setSelectNavegador(e) }}
                    value={selectNavegador}        
                    style={{ width: "100%" }} >
                    <Option value="chrome">chrome</Option>
                    <Option value="lucy">Firefox</Option>
                    <Option value="Safari">Safari</Option>
                    <Option value="opera">Opera</Option>
                    
                </Select>            
            </Col>
            <Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 12 }} >
            
            <Button type="primary" style={{ marginTop: "25px",marginLeft: "25px" }} onClick={(e)=>{obtenerAplicaciones(1) }}>Buscar</Button>
   
            </Col>

        </Row>
        <Row>
            
            <Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 20 }} >
                <Table  dataSource={listPropety} 
                        columns={columns}  
                        pagination={false}
                        rowKey={(record) => record.r_id}
                />  
                <Pagination  current={paginaActualTabla} total={cantidadDatosTabla*10} onChange={(val,size)=>{changePaginationTabla(val,size)}} showSizeChanger={false} />
           
            </Col>
        </Row>
      
          
        </> 
        
    </div>
    );
 
}
export default GestionAplicacionesUsadas;