import {useState,useEffect} from 'react';
import  {requestApi}  from '../../api/index';
import swal from 'sweetalert';
import { PageHeader } from 'antd';
import { Card } from 'antd';
import * as React from 'react';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../redux/actions';
import {
    Form,
    Input,
    Button,
    Select,
    Switch,
  } from 'antd';
const { Option } = Select;

const  FormularioEditarUsuario=(props)=> {
    const [form] = Form.useForm();
    const [componentSize, setComponentSize] = useState('large');
    const listRef = React.createRef();
    const [vigencia, setVigencia] = React.useState(true);
    const [generoData, setGeneroData] = React.useState([]);
    const [mensajeErrorDocumento, setMensajeErrorDocumento] = React.useState(null);
    const [mensajeErrorCorreo, setMensajeErrorCorreo] = React.useState(null);
    const [cargoData, setCargoData] = React.useState([]);
    const dispatch = useDispatch();
    const onFormLayoutChange = ({ size }) => {
      setComponentSize(size);
    };
    useEffect(()=>{
      console.log("cargoData-n",cargoData);
    },[cargoData]);
    useEffect(()=>{
          let isSubscribed = true
          if(listRef?.current!=null){
            (async()=>{
              
              if(props.dataEdit!=null){
                form.setFieldsValue({
                  correo: props.dataEdit.email
                });
                form.setFieldsValue({
                  dni: props.dataEdit.numberDocument
                });
                form.setFieldsValue({
                  nombres: props.dataEdit.names
                });
                form.setFieldsValue({
                  apellidoPaterno: props.dataEdit.lastName
                });
                form.setFieldsValue({
                  apellidoMaterno: props.dataEdit.motherLastName
                });
                setVigencia(props.dataEdit.validity);
                cargarCargo();
                cargarGenero();

              }else{
                form.setFieldsValue({
                  correo:""
                });
                form.setFieldsValue({
                  dni:""
                });
                form.setFieldsValue({
                  nombres:""
                });
                form.setFieldsValue({
                  apellidoPaterno:""
                });
                form.setFieldsValue({
                  apellidoMaterno:""
                });
                setVigencia(true);
                //form.getFieldValue("genero")
                form.setFieldsValue({
                  genero:null
                });
                
              }
            })();

            if (!isSubscribed) return;
          }
          
          return () => isSubscribed = false;
    },[]);
    const seleccionarGenero = value => {
      console.log("value",value);
    };
    const onChangeVigencia= ()=> {
      setVigencia(!vigencia);
    }
    const onBlur=(val)=>{
      if(val.target.value){
        verificarDni(val.target.value);
      }else{
        setMensajeErrorDocumento(null);
      }
    }
    const onBlurCorreo=(val)=>{
      if(val.target.value){
        verificarCorreo(val.target.value);
      }else{
        setMensajeErrorCorreo(null);
      }
    }
    const cargarCargo = async() => {
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `dataGeneral/loaderCharge`,
          {},
          "get"
        );
        setCargoData(respon.estado ? respon.data : []  );
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));

        if(props.dataEdit!=null){
          let dataTemp=(respon.estado ? respon.data : [] );
          let dataExistCargo=(dataTemp).filter((val)=>{
            return val.id==props.dataEdit.idCharge;
          });
          if(dataExistCargo.length==0){
            dataTemp.push({
              id:props.dataEdit.idCharge,
              nombre:props.dataEdit.descriptionCharge
            });
            
            setCargoData(dataTemp);
          }
          form.setFieldsValue({
            cargo:props.dataEdit.idCharge+""
          });
        }
        return respon.estado ? respon.data : [] ;
      }catch(e){
        return [];
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));      
      }
    
    };
    const verificarDni = async(dni) => {
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `usuarioAdmin/verificarDniUsuarioAdmin?dni=`+dni,
          {},
          "get"
        );
        setMensajeErrorDocumento(respon.estado ? null : respon.mensaje  );
      }catch(e){
        return e;
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }
    
    };
    const registrarUsuario=async()=>{
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let dataRequest={
          "id":props.dataEdit.id,
          "dni":form.getFieldValue("dni"),
          "correo":form.getFieldValue("correo"),
          "contrasenia":null,
          "nombres":form.getFieldValue("nombres"),
          "apellidoPaterno":form.getFieldValue("apellidoPaterno"),
          "apellidoMaterno":form.getFieldValue("apellidoMaterno"),
          "vigencia":vigencia,
          "cargo":form.getFieldValue("cargo"),
          "genero":form.getFieldValue("genero"),
        };
        let respon=await requestApi(
          `userAdmin/updateUserAdmin`,
          dataRequest,
          "post"
        );
        if(respon.estado){
          swal({
            title: "Actualizar Usuario.",
            text: respon.mensaje,
            icon:  respon.estado ? "success":"error",
            button: "aceptar"
          });
          props.atras();
          props.refrescarListado();
        }else{
          swal({
            title: "Actualizar Usuario.",
            text: respon.mensaje,
            icon:  respon.estado ? "success":"error",
            button: "aceptar"
          });
        }
      }catch(e){
        return e;
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }
    
    }
    const verificarCorreo = async(correo) => {
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `usuarioAdmin/verificarCorreoUsuarioAdmin?correo=`+correo,
          {},
          "get"
        );
        setMensajeErrorCorreo(respon.estado ? null : respon.mensaje  );
      }catch(e){
        return e;
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }
    
    };
    const seleccionarCargo = value => {
      console.log("value",value);
    };
    const cargarGenero = async() => {
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `dataGeneral/loaderGender`,
          {},
          "get"
        );
        let dataListGenero=respon.estado ? respon.data : [];
      
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
        if(props.dataEdit!=null){
          let dataExistGenero=dataListGenero.filter((val)=>{
            return val.id==props.dataEdit.idGender;
          });

          
          if(dataExistGenero.length==0){
            dataListGenero.push({
              id:props.dataEdit.idGender,
              descripcion:props.dataEdit.descriptionGender
            });
          
            setGeneroData(dataListGenero);
          }
        
        }
        
        setGeneroData(dataListGenero  );
        console.log((props.dataEdit.idGender+""));
      
        form.setFieldsValue({
          genero:props.dataEdit.idGender+""
        });
        return respon.estado ? respon.data : []; 
      }catch(e){
        return [];
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }
    
    };
  return (
    <div ref={listRef}   className="contentPago">
      <Card title={<PageHeader
        className="site-page-header"
        onBack={() => props.atras()}
        title="Editar Usuario"
        subTitle={null}
      />} bordered={false} style={{ width: "100%" }}>
        <>
        <Form
            labelCol={{
            span: 4,
            }}
            wrapperCol={{
            span: 14,
            }}
            layout="vertical"
            initialValues={{
            size: componentSize,
            }}
            onValuesChange={onFormLayoutChange}
            size={componentSize}
            form={form}
        >
            <Form.Item 
              name={['dni']}
              label="dni"
              max="500"
              rules={[{ required: true, message: 'Se Requiere de Dni.' }]}
              wrapperCol={{ lg: 14,md:14,sm: 24 }}
              
            >
                <Input
                disabled
                className="inputrowremove" 
                type="number" onInput = {(e) =>{
                setMensajeErrorDocumento(null);
                if(!(e.target.value=="" || e.target.value==null) ){  
                  e.target.value = Math.max(0, parseInt(e.target.value) ).toString().slice(0,8)
                }
                
                }}
                onBlur={(e)=>{onBlur(e);}}
              />
              
            </Form.Item>
             {
                  mensajeErrorDocumento==null ? null :
                <span className="errorMensaje">{mensajeErrorDocumento}</span>
              }
            <Form.Item 
              name={['correo']}
              label="Correo"
              max="500"
              rules={[{ required: true, message: 'Se Requiere de Correo.' },{
                type: "email",
                message: "El formato del correo es invalido."
              }]}
              wrapperCol={{ lg: 14,md:14,sm: 24 }}
              
            >
                <Input   
                 disabled
                 onInput = {(e) =>{
                    setMensajeErrorCorreo(null);
                  
                }}
                onBlur={(e)=>{onBlurCorreo(e);}} />
            </Form.Item>
            {
                  mensajeErrorCorreo==null ? null :
                <span className="errorMensaje">{mensajeErrorCorreo}</span>
              }
            <Form.Item 
              name={[ 'nombres']}
              label="Nombres"
              max="500"
              rules={[{ required: true, message: 'Se Requiere de Nombres.' }]}
              wrapperCol={{ lg: 14,md:14,sm: 24 }}
              >
                <Input />
            </Form.Item>
            <Form.Item 
              name={['apellidoPaterno']}
              label="Apellido Paterno"
              max="500"
              rules={[{ required: true, message: 'Se Requiere de Apellido Paterno.' }]}
              wrapperCol={{ lg: 14,md:14,sm: 24 }}>
                <Input />
            </Form.Item>
            <Form.Item 
              name={[ 'apellidoMaterno']}
              label="Apellido Materno"
              max="500"
              rules={[{ required: true, message: 'Se Requiere de Apellido Materno.' }]}
              wrapperCol={{ lg: 14,md:14,sm: 24 }}
              >
                <Input />
            </Form.Item>
            <Form.Item 
              name={[ 'genero']} 
              label="Genero"
              rules={[{ required: true, message: 'Se Requiere de Genero.' }]}
              wrapperCol={{ lg: 14,md:14,sm: 24 }}>
                 <Select allowClear placeholder="Seleccione Genero"  style={{ width: "100%" }} onChange={()=>{seleccionarGenero()}}>
                 {generoData.map(item => (
                  <Option id={item.id} key={item.id}>{item.description}</Option>
                ))}
              </Select>
            </Form.Item>
            <Form.Item 
              name={[ 'cargo']} 
              label="Cargo"
              rules={[{ required: true, message: 'Se Requiere de Cargo.' }]}
              wrapperCol={{ lg: 14,md:14,sm: 24 }}>
                 <Select allowClear placeholder="Seleccione Cargo"  style={{ width: "100%" }} onChange={()=>{seleccionarCargo()}}>
                 {cargoData.map(item => (
                  <Option id={item.id} key={item.id}>{item.name}</Option>
                ))}
              </Select>
            </Form.Item>
            <Form.Item 
              name={[ 'vigencia']} 
              label="Vigencia"
              wrapperCol={{ lg: 14,md:14,sm: 24 }}
            >
                 <Switch checked={vigencia} onChange={onChangeVigencia} />
            </Form.Item>
            <Button  htmlType="submit" className="btnNuevoRegistrar" type="primary" onClick={()=>{ registrarUsuario()}}>Actualizar Usuario</Button>
        </Form>
        </>
      </Card>
    </div>
    );
 
}
export default FormularioEditarUsuario;