import * as React from 'react';
import {useState,useEffect} from 'react';
import { Table } from 'antd';
import { Select } from 'antd';
import { Tabs } from 'antd';
import { Card } from 'antd';
import { Form } from 'antd';
import {  Button } from 'antd';
import moment from 'moment';
//import { Layout } from 'antd';
import { Pagination } from 'antd';
//import swal from 'sweetalert';

import {  Col, Row } from 'antd';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../redux/actions';
import { DatePicker  } from 'antd';
import  {requestApi}  from '../../api/index';
import FormularioVentaComponent from './FormularioVenta';

import { Input } from 'antd';
const { Option } = Select;



const  GestionVenta=()=> {
 
  const { RangePicker } = DatePicker;
  const { TabPane } = Tabs;
  const columns = [
    
    {
      title: 'fecha Registro',
      dataIndex: 'registrationDate',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Còdigo Venta',
      dataIndex: 'code',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Estado Venta',
      dataIndex: 'descriptionStateSales',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Total',
      dataIndex: 'total',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Tipo Documento Cliente',
      dataIndex: 'descriptionTypeDocument',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Numero Documento Cliente',
      dataIndex: 'numberDocumentClient',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Nombres Completos Clientes',
      dataIndex: 'nameFull',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Usuario Registro',
      dataIndex: 'idAdminRegister',
      sorter: false,
      render: (data,item) => `${item.namesAdmin+" "+item.lasNameAdmin +" "+item.motherLastnameAdmin}`,
      width: '20%'
    }
    
  ];
  const [loading,setLoading] = useState(false);
  const listRef = React.createRef();
  const [contarTotal, setContarTotal] = useState(0);
  const [paginaActual, setPaginaActual] = useState(1);
  const [cantidadContenido, setCantidadContenido] = useState(10);
  const [listadoUsuarioAdmin,setListadoUsuarioAdmin] = useState([]);
  const [listadoDataVenta,setListadoDataVenta] = useState([]);
  const [listTypeDocument, setlistTypeDocument] = React.useState([]);
  const [listTypeTicket, setListTypeTicket] = React.useState([]);
  const [form] = Form.useForm();
  const [fechaRegistroFiltro, setFechaRegistroFiltro] = useState(null);
  useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        if (!isSubscribed) return;
        (async()=>{
          await cargarUsuarios();
          await getTypeDocument();
          await loaderTypeTicket();
          await listarDataVenta(paginaActual-1,cantidadContenido);
        })();
      }
      
      return () => isSubscribed = false;
  },[]);
  const loaderTypeTicket=async()=>{
    try{
      let respon=await requestApi(
        `dataGeneral/loaderTicket`,
        {},
        "get"
      );
      let data =respon.state ? respon.data : [];
      setListTypeTicket(data);
      form.setFieldsValue({
        typeSales:null
      });
    }catch(e){
      return e;
    }
    return "ok;"
  }
  const getTypeDocument=async()=>{
    try{
      let respon=await requestApi(
        `dataGeneral/loaderTypeDocument`,
        {},
        "get"
      );
      let data =respon.state ? respon.data : [];
      setlistTypeDocument(data);
    }catch(e){
      return e;
    }
    return "ok;"
  }
  const refresPagina=async()=>{
    await listarDataVenta(paginaActual-1,cantidadContenido);
  }

  const cargarUsuarios = async() => {
   
    try{
      let respon=await requestApi(
        `userAdmin/listUser`,
        {},
        "get"
      );
      setListadoUsuarioAdmin(respon.state ? respon.data : []  );
    }catch(e){
      return e;
    }finally{
    }
   
  };
  const changePagination=async(val,sizePagination)=>{
    setPaginaActual(val);
    setCantidadContenido(sizePagination);
    await listarDataVenta(val-1,sizePagination);
 }
 const listarDataVenta = async(reqpaginaActual,reqcantidadContenido) => {
  setLoading(true);
  try{
    let idAdminRegister="";
    if(form.getFieldValue("usuarioRegistro")!=null){
      let itemsss=listadoUsuarioAdmin.filter((val)=>{
          return val.numberDocument==form.getFieldValue("usuarioRegistro");
      });
      idAdminRegister=itemsss[0].id;
    }
    let document=form.getFieldValue("document") ? form.getFieldValue("document")  : "";
    let typeDocument=form.getFieldValue("typeDocument") ? form.getFieldValue("typeDocument")  : "";
    let typeSales=form.getFieldValue("typeSales") ? form.getFieldValue("typeSales")  : "";
    let selectType="";
    if(!(typeSales=="" || typeSales==null)){
      selectType=listTypeTicket.filter((val)=>{
        return val.code==typeSales;
      })[0].id;
    }
    let codigoVenta=form.getFieldValue("codigoVenta") ? form.getFieldValue("codigoVenta")  : "";
    let reqFechaInicio=fechaRegistroFiltro==null || fechaRegistroFiltro=="" ? "" : moment(fechaRegistroFiltro[0]).format('DD-MM-YYYY') ;
    let reqFechaFin=fechaRegistroFiltro==null || fechaRegistroFiltro=="" ? "" : moment(fechaRegistroFiltro[1]).format('DD-MM-YYYY') ;//typeDocument
    let respon=await requestApi(
      `sales/listSales?paginaActual=${reqpaginaActual}&cantidadContenido=${reqcantidadContenido}&reqIdtypeSales=${selectType}&typeDocument=${document ? typeDocument : ""}&docClient=${document}&fechaInicio=${reqFechaInicio}&fechaFin=${reqFechaFin}&code=${codigoVenta}&idAdminRegister=${idAdminRegister}`,
      {},
      "get"
    );
    setListadoDataVenta(respon.state ? respon.data : []  );
    setContarTotal(respon.state ? respon.cantidad : 0);
  }catch(e){
    return e;
  }finally{
    setLoading(false);
  }
 
};
  return (
    <div ref={listRef}   className="contentPago">
      <Tabs defaultActiveKey="1" onChange={async(e)=>{console.log("eeeeuuu",e);
        if(e=="1"){
          await refresPagina();

        }
      
      }} >
        <TabPane tab="Listado" key="1">
        <>
            <Form
              form={form}
              layout="vertical"
            >
            <Row>
              <Col xs={{ span: 24 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 6 }}>
                    <Form.Item 
                      name="rangos" label="Rangos"
                    
                    >
                    <RangePicker  
                      value={fechaRegistroFiltro==null ? [] : [moment(fechaRegistroFiltro[0], 'YYYY-MM-DD'), moment(fechaRegistroFiltro[1], 'YYYY-MM-DD')]}
                      onChange={(value)=>{
                        if( value==null){
                          setFechaRegistroFiltro(null);
                          return false;
                        }
                        if(value!=undefined ){
                          setFechaRegistroFiltro(value);
                        }else{
                          setFechaRegistroFiltro(null);
                        }
                      }
                      }  />
                  </Form.Item>
              </Col>
              <Col className="formProduct" xs={{ span: 24 }} sm={{ span: 24 }}
               md={{ span: 24 }} lg={{ span: 4 }} >
                <Form.Item 
                  name={[ 'typeSales']}
                  label="Tipo Venta"
                  wrapperCol={{ lg: 24,md:24,sm: 24 }}
                  labelCol={{ lg: 24,md:24,sm: 24 }}
                  >
                  <Select
                    
                    placeholder="Seleccionar Tipo  venta"
                    style={{ width: '100%' }}
                  >
                    <Option value={null}  >{'Todos'}</Option>
                    {listTypeTicket.map((item, i) => (
                    (<Option value={item.code} key={i} >{item.description}</Option>)

                    )  
                )}
                  </Select>
                  </Form.Item>
              </Col>
              <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 6 }} >
                  <Form.Item

                    label="Tipo Documento"
                    wrapperCol={{ lg: 24,md:24,sm: 24 }}
                    labelCol={{ lg: 24,md:24,sm: 24 }}
                    >
                      <Select   
                        className="formControlform formControl"
                        placeholder="Seleccione Tipo Documento"  
                        defaultValue={null}
                        >
                        <Option value={null}  >{'Todos'}</Option>
                        {listTypeDocument.map((item, i) => (
                            (<Option value={item.id} key={i} >{item.description}</Option>)

                            )  
                        )}
                        
                      </Select>
                    </Form.Item>
                  
              </Col>
              <Col style={{marginLeft:8}} xs={{ span: 24 }} sm={{ span: 12 }} md={{ span: 12 }} 
              lg={{ span: 6 }}>
                  <Form.Item 
                    name="document" label="Numero Documento"
                  
                  >
                    <Input name="documentDoc"
                        placeholder="" 
                  />
                </Form.Item>
              </Col>
              <Col  xs={{ span: 24 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 6 }}>
                  <Form.Item 
                    name="codigoVenta" label="Código Venta"
                  
                  >
                    <Input name=""
                        placeholder="" 
                  />
                </Form.Item>
              </Col>
              <Col  xs={{ span: 24 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 6 }}>
                <Form.Item 
                  name="usuarioRegistro" 
                  label="Usuarios Registro ">
                    <Select   
                      placeholder={""}  
                      bordered={true}  
                      allowClear
                      showSearch
                      name={"usuarioRegistroSelect"}
                      label={"Seleccione Usuarios Registro"}>
                      {
                        (listadoUsuarioAdmin).map((itemAddress, i) => (
                          (<Option value={itemAddress.numberDocument} key={itemAddress.numberDocument}>{itemAddress.numberDocument+ " - "+itemAddress.names+" "+itemAddress.lastName+" "+itemAddress.motherLastname}</Option>)
                      
                        )  
                        )
                      }
                    </Select>
                </Form.Item>
              </Col>
              <Col style={{padding:8}} xs={{ span: 24 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 6 }}>
                <Button style={{marginTop:22}} type="primary" onClick={()=>{refresPagina();}}>Buscar</Button>
    
              </Col>           
            </Row>
            
            </Form>
            <Table 
              rowKey={record => record.id}
              pagination={false} 
              dataSource={listadoDataVenta} 
              columns={columns} 
              loading={loading} />
            <Pagination current={paginaActual} defaultCurrent={paginaActual} 
            total={contarTotal}  onChange={(val,size)=>{changePagination(val,size)}} />
          </> 
        </TabPane>
        <TabPane tab="Gestionar Venta" key="2">
            <FormularioVentaComponent atras={()=>{}}/>
        </TabPane>
      </Tabs>
       
    </div>
    );
 
}
export default GestionVenta;