
import * as React from 'react';
import swal from 'sweetalert';
import {useState,useEffect} from 'react';
import { DownOutlined } from '@ant-design/icons';
import { Row, Col } from 'antd';
import { Card } from 'antd';
import { Layout } from 'antd';
import { Menu, Dropdown } from 'antd';
import {  Button } from 'antd';
import { ImportOutlined} from '@ant-design/icons';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../redux/actions';
import  {requestApi}  from '../../api/index';
import { Table } from 'antd';
const SincronizacionDatos = (props) => {
  const columns = [
    {
      title: 'Dni',
      dataIndex: 'dni',
      sorter: false,
      render: data => `${data}`,
      width: '20%',
      key: 'dni-01',
    },
    {
      title: 'Nombres',
      dataIndex: 'nombres',
      sorter: false,
      width: '20%',
      key: 'nombres-01',
      render: data => `${data}`,
    },
    {
      title: 'Apelido Paterno',
      dataIndex: 'apellidopaterno',
      sorter: false,
      width: '20%',
      key: 'apellidoPaterno-01',
      render: data => `${data}`
    },
    {
      title: 'Apelido Materno',
      dataIndex: 'apellidomaterno',
      sorter: false,
      key: 'apellidoMaterno-01',
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Genero',
      dataIndex: 'genero',
      sorter: false,
      key: 'descripcionGenero-01',
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Fecha Nacimiento',
      dataIndex: 'fechanacimiento',
      key: 'fechaNacimiento',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Vigencia',
      dataIndex: 'vigenciaText',
      key: 'vigenciaText',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
  ];
    const {  Content } = Layout;
    const [loading,setLoading] = useState(false);
    const [listadoDataCiudadano,setListadoDataCiudadano] = useState([]);
    const [mensajeError,setMensajeError] = useState(null);
    const [importoDataCorrectaMente,setImportoDataCorrectaMente] = useState(null);
    const listRef = React.createRef();
    const dispatch = useDispatch();
    React.useEffect(()=>{
      console.log(" <SettingOutlined/>");

    },[]);
    useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        if (!isSubscribed) return;
        (async()=>{
         // await contarUsuario();
         // await listarUsuario(paginaActual-1,cantidadContenido);
        })();
      }
      
      return () => isSubscribed = false;
  },[]);

  const menu = (
    <Menu>
      <Menu.Item key="0">
        <a href="#">Excel</a>
      </Menu.Item>
      <Menu.Item key="1">
        <a href="#">Pdf</a>
      </Menu.Item>
    </Menu>
  );
  const seleccionarArchivo=async(val)=>{
    if(
      !( (val.target.files[0].type==".csv") ||
      (val.target.files[0].type=="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet")
      ||
      (val.target.files[0].type=="application/vnd.ms-excel"))

      ){
        swal({
          title: "Importacion  de Ciudadanos.",
          text:"El archivo seleccionado no es excel",
          icon:"error",
          button: "aceptar"
        });
        return false;
    }
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    var bodyFormData = new FormData();
    bodyFormData.append('excelciudadano', val.target.files[0]);
    try{
      let respon=await requestApi(
        `datosUsuarioCiudadano/importarCiudadanosExcel`,
        bodyFormData,
        "post",
        {
          "Content-Type":'multipart/form-data'
        }
      );
      if(respon.estado){
        setImportoDataCorrectaMente(true);
        setListadoDataCiudadano(respon.estado ? respon.data  : [] );
        setMensajeError(null);
      }else{
        setMensajeError(respon.mensaje);
        setImportoDataCorrectaMente(false);
      }
      //setMensajeErrorDocumento(respon.estado ? null : respon.mensaje  );
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
  }
  const importarCiudadanos=()=>{
    const element = document.createElement('input');
    element.type="file";
    element.accept=".xlsx,.xls,.csv";
    element.click();
    element.onchange=seleccionarArchivo;
  }
  const guardarInformacion=()=>{
    swal({
      title: "Importación Masiva de Ciudadano",
      text: " ¿Deseas Guardar Información ?",
      icon: "warning",
     // buttons: true,
      dangerMode: true,
      buttons: {
        cancel: "Cancelar",
        catch: {
          text: "Aceptar",
          value: "catch",
        }
      }
    })
    .then(async(willDelete) => {
      if (willDelete) {
        dispatch(loaderSpiner({
          loaderSpiner:true
        }));
        try{
          let respon=await requestApi(
            `datosUsuarioCiudadano/registrarMasivoDatosCiudadano`,
            {
              arrayData:listadoDataCiudadano
            },
            "post"
          );
          swal({
            title: "Importacion  de Ciudadanos.",
            text:respon.estado==false ? "Hubo un error Al realizar la Importación." : respon.mensaje,
            icon:respon.estado==false ? "error" : "success",
            button: "aceptar"
          });
          if(respon.estado==true){
            setImportoDataCorrectaMente(null);
            setListadoDataCiudadano([] );
            setMensajeError(null);
  
          }
          

          /*if(respon.estado){
              
          }else{
          }*/
        }catch(e){
          return e;
        }finally{
          dispatch(loaderSpiner({
            loaderSpiner:false
          }));
        }
      }
    });
  }
  return (<div>
      <Card title={null} bordered={false} style={{ width: "100%" }}>
      <Content> 
        <Row>
          <Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 12 }} >
            <div className="formGroup">
              <Button type="primary" onClick={()=>{importarCiudadanos()}}  icon={<ImportOutlined />} size={50} >
                Seleccionar Archivo Excel 
              </Button >
              <div className="errorMensajeText">{mensajeError}</div>
              {
                importoDataCorrectaMente==true ?
                <>
                  <a className="linkSaveInfo" onClick={()=>{guardarInformacion();}}>Guardar Información Hay {listadoDataCiudadano.length} Registro(s) </a>
                </> :
                <>
                </>
              }
            </div>                
          </Col>
          <Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 12 }} >
           
          </Col>
          
        </Row>
      </Content>
     
      <div className="contentFiltroBtn" style={{marginBottom:30}}>
      </div>
      <Table
        scroll={{ x: 'calc(700px + 50%)',  y: 1500 }}
        columns={columns}
        dataSource={listadoDataCiudadano}
        rowKey={record => record.dni}
        pagination={true}
        loading={loading}
      />
  </Card>

  </div>
  );
};

export default SincronizacionDatos;