import {useState,useEffect} from 'react';
import  {requestApi}  from '../../api/index';
import { Table } from 'antd';
import swal from 'sweetalert';
import { PageHeader } from 'antd';
import { Card } from 'antd';
import * as React from 'react';
import {  useDispatch } from "react-redux";
import {  Modal } from 'antd';
import { Row, Col } from 'antd';
import {
    Form,
    Input,
    Button,
    Select,
  } from 'antd';

import {loaderSpiner} from '../../redux/actions';  
const { Option } = Select;
const  FormularioOrden=(props)=> {
    const columnsOrden = [
      {
        title: 'Product',
        dataIndex: 'product',
        key: 'product',
      },
      {
        title: 'Cantidad',
        dataIndex: 'amount',
        key: 'amount',
      },
      {
        title: 'Costo del Producto',
        dataIndex: 'costProduct',
        key: 'costProduct',
      },
      {
        title: 'Estado',
        dataIndex: 'descriptionStatus',
        key: 'descriptionStatus',
      },
      {
        title: 'Acción',
        dataIndex: 'id',
        sorter: false,
        render: (data,item) =><> 
        <Button  type="primary"  onClick={()=>{verDetailt(item)
          }}>
          Editar
        </Button>
        </>
        ,
        width: '20%'
      },
    ];
    const [form] = Form.useForm();
    const [formModal] = Form.useForm();
    const listRef = React.createRef();
    const [visibleModal, setVisibleModal] = React.useState(null);
    const [listadoDetailtDataOrden,setListadoDetailtDataOrden] = useState([]);
    const [listStateOrderDetail, setListStateOrderDetail] = React.useState([]);
    const [listStateOrder, setListStateOrder] = React.useState([]);
    const [idDetailSeleccionado, setIdDetailSeleccionado] = React.useState([]);
    const [idItem, setIdItem] = React.useState(null);
    const dispatch = useDispatch();
    useEffect(()=>{
      if(props.dataSeleccionada!=null){
        //fechaRegistro
        (async()=>{
          await  loaderStateOrder();
          await loaderStateOrderDetailt();
        })();
        //props.dataSeleccionada.id
        setIdItem(props.dataSeleccionada.id);
        form.setFieldsValue({
          "fechaRegistro":props.dataSeleccionada.dateRegister,
          "estadoPago":props.dataSeleccionada.statusPayment,
          "numeroDocumento":props.dataSeleccionada.documentIdentity,
          "cliente":props.dataSeleccionada.nameFull,
          "numeroCelular":props.dataSeleccionada.celPhone,
          "ubicacion":props.dataSeleccionada.ubigeo,
          "direccion":props.dataSeleccionada.street+" "+props.dataSeleccionada.numberStreet,
          "sending":props.dataSeleccionada.sending ? "ENVIAR" : "RECOJO",
          "apartamento":props.dataSeleccionada.apartament ,
          "personaRecibir":props.dataSeleccionada.personReceive ,
          "estadoOrden":props.dataSeleccionada.stateOrder 
        });
      }//stateOrder
      setListadoDetailtDataOrden(props.dataSeleccionada.detailtFormat );
      console.log("props.dataSeleccionada",props.dataSeleccionada.documentIdentity);
    },[props.dataSeleccionada]);

    const verDetailt=async(item)=>{
      console.log("item",item);
      formModal.setFieldsValue({
        producto:item.product,
        cantidad:item.amount,
        costProduct:item.costProduct,
        estado:item.statusDetailtOrder
      });
      setVisibleModal(!visibleModal);
      setIdDetailSeleccionado(item.id);
    }
    const loaderStateOrder=async()=>{
      try{
        let respon=await requestApi(
          `dataGeneral/loaderStatusData`,
          {},
          "get"
        );
        let data =respon.state ? respon.data : [];
        setListStateOrder(data);
        
      }catch(e){
        return e;
      }
      return "ok;"
    }

    const loaderStateOrderDetailt=async()=>{
      try{
        let respon=await requestApi(
          `dataGeneral/loaderDetailtStatusDetailtData`,
          {},
          "get"
        );
        let data =respon.state ? respon.data : [];
        setListStateOrderDetail(data);
        
      }catch(e){
        return e;
      }
      return "ok;"
    }
    const registrarDetalle=()=>{
      let tempNewFormat=JSON.parse(JSON.stringify(listadoDetailtDataOrden));
      tempNewFormat.forEach(element => {
          if(element.id==idDetailSeleccionado){
            element.statusDetailtOrder=formModal.getFieldValue("estado");
            element.descriptionStatus=listStateOrderDetail.filter((itemStateOrderDetail)=>{
                return itemStateOrderDetail.id==parseInt(formModal.getFieldValue("estado"));
            })[0].description;
          }
      });
      setListadoDetailtDataOrden(tempNewFormat);
      setVisibleModal(!visibleModal);
    }
    const registerChangeOrder=async()=>{
      try{
        let arrayIdDetail=[];
        let arrayIdStatusDetail=[];
        listadoDetailtDataOrden.forEach(element => {
          arrayIdDetail.push(element.id);
          arrayIdStatusDetail.push(element.statusDetailtOrder);
        });
        let dataRequest={
          "idOrder":idItem,
          "idStatusOrder":form.getFieldValue("estadoOrden"),
          "arrayIdDetail":arrayIdDetail,
          "arrayIdstatus":arrayIdStatusDetail
        };
        let respon=await requestApi(
          `order/registerOrderState`,
          dataRequest,
          "put"
        );
        if(respon.state){
          swal({
            title: "Gestion Orden.",
            text: respon.message,
            icon:  respon.state ? "success":"error",
            button: "aceptar"
          });
          props.atras();
        }else{
          swal({
            title: "Gestion Orden.",
            text: respon.message,
            icon:  respon.state ? "success":"error",
            button: "aceptar"
          });
        }
      }catch(e){
        return e;
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }
    }
    return (
    <div ref={listRef}   className="content">
      <Modal  
      visible={visibleModal} 
      className="modalContent" 
      bodyStyle={{height:"60vh"}} 
      style={{ top: 0,left: 0,right:0 }} 
      centered 
      width={"80%"} 
      title={'Detalle'}  
      onOk={() =>{ registrarDetalle();}}
      onCancel={()=>{setVisibleModal(!visibleModal)}}>
        <Form
          labelCol={{
          span: 4,
          }}
          wrapperCol={{
          span: 14,
          }}
          layout="vertical"
          form={formModal}
          style={{position:"relative",top:-19}}
        >
          <Row>
              <Col  
                xs={{ span: 24 }} 
                sm={{ span: 24 }} 
                md={{ span: 24 }} 
                lg={{ span: 12 }}
                style={{padding:20}} >
                <Form.Item
                  wrapperCol={{ lg: 24,md:24,sm: 24 }}
                  labelCol={{ lg: 24,md:24,sm: 24 }}
                  label={'Producto'}
                  name={'producto'}
                  rules={[
                    {
                      required: false,
                      message: null,
                    },
                  ]}
                  disabled
                  >
                    <Input disabled autoComplete="off" placeholder="" allowClearx />
                  </Form.Item>
                </Col>
                <Col  
                xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 12 }}
                style={{padding:20}} >
                <Form.Item
                  wrapperCol={{ lg: 24,md:24,sm: 24 }}
                  labelCol={{ lg: 24,md:24,sm: 24 }}
                  label={'Cantidad'}
                  name={'cantidad'}
                  rules={[
                    {
                      required: false,
                      message: null,
                    },
                  ]}
                  disabled
                  >
                    <Input disabled autoComplete="off" placeholder="" allowClearx />
                  </Form.Item>
                </Col>
                <Col  
                  xs={{ span: 24 }} 
                  sm={{ span: 24 }} 
                  md={{ span: 24 }} 
                  lg={{ span: 12 }} 
                  style={{padding:20}}>
                  <Form.Item
                    wrapperCol={{ lg: 24,md:24,sm: 24 }}
                    labelCol={{ lg: 24,md:24,sm: 24 }}
                    label={'Costo del Producto'}
                    name={'costProduct'}
                    rules={[
                      {
                        required: false,
                        message: null,
                      },
                    ]}
                    disabled
                    >
                      <Input disabled autoComplete="off" placeholder="" allowClearx />
                    </Form.Item>
                </Col>
                <Col  
                  xs={{ span: 24 }} 
                  sm={{ span: 24 }} 
                  md={{ span: 24 }} 
                  lg={{ span: 12 }}
                  style={{padding:20}} >
                  <Form.Item
                    wrapperCol={{ lg: 24,md:24,sm: 24 }}
                    labelCol={{ lg: 24,md:24,sm: 24 }}
                    label={'Estado'}
                    name={'estado'}
                    rules={[
                      {
                        required: false,
                        message: null,
                      },
                    ]}
                    disabled
                    >
                    <Select   
                      className="formControlform formControl"
                      placeholder="Seleccione Estado Orden"  
                      bordered={true}  
                      onChange={(e)=>{} }>
                      <Option value={null} key={-1} >{'Seleccione Estado Orden'}</Option>
                      {listStateOrderDetail.map((item, i) => (
                          (<Option value={item.id} key={i} >{item.description}</Option>)

                          )  
                      )}
                      
                    </Select>
                    </Form.Item>
                </Col>
          </Row>
        </Form> 
      </Modal>
       
      <Card title={<PageHeader
        className="site-page-header"
        onBack={() => props.atras()}
        title={ "Gestionar  Orden"}
        subTitle={null}
      />} bordered={false} style={{ width: "100%" }}>
        <>
        <Form
          labelCol={{
          span: 4,
          }}
          wrapperCol={{
          span: 14,
          }}
          layout="vertical"
          form={form}
          
      >
            <Row>
                <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                  <Form.Item
                     wrapperCol={{ lg: 24,md:24,sm: 24 }}
                     labelCol={{ lg: 24,md:24,sm: 24 }}
                     label={'Fecha Registro'}
                     name={'fechaRegistro'}
                     rules={[
                      {
                        required: false,
                        message: null,
                      },
                     ]}
                     disabled
                    >
                      <Input disabled autoComplete="off" placeholder="" allowClearx />
                    </Form.Item>
                </Col>
                <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                  <Form.Item
                     wrapperCol={{ lg: 24,md:24,sm: 24 }}
                     labelCol={{ lg: 24,md:24,sm: 24 }}
                     label={'Estado Pago'}
                     name={'estadoPago'}
                     rules={[
                      {
                        required: false,
                        message: null,
                      },
                     ]}
                     disabled
                    >
                      <Input disabled autoComplete="off" placeholder="" allowClearx />
                    </Form.Item>
                </Col>
                <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                  <Form.Item
                     wrapperCol={{ lg: 24,md:24,sm: 24 }}
                     labelCol={{ lg: 24,md:24,sm: 24 }}
                     label={'Numero Documento'}
                     name={'numeroDocumento'}
                     rules={[
                      {
                        required: false,
                        message: null,
                      },
                     ]}
                     disabled
                    >
                      <Input disabled autoComplete="off" placeholder="" allowClearx />
                    </Form.Item>
                </Col>
                <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                  <Form.Item
                     wrapperCol={{ lg: 24,md:24,sm: 24 }}
                     labelCol={{ lg: 24,md:24,sm: 24 }}
                     label={'Recojo o Envio'}
                     name={'sending'}
                     rules={[
                      {
                        required: false,
                        message: null,
                      },
                     ]}
                     disabled
                    >
                      <Input disabled autoComplete="off" placeholder="" allowClearx />
                    </Form.Item>
                </Col>
                <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                  <Form.Item
                     wrapperCol={{ lg: 24,md:24,sm: 24 }}
                     labelCol={{ lg: 24,md:24,sm: 24 }}
                     label={'Cliente'}
                     name={'cliente'}
                     rules={[
                      {
                        required: false,
                        message: null,
                      },
                     ]}
                     disabled
                    >
                      <Input disabled autoComplete="off" placeholder="" allowClearx />
                    </Form.Item>
                </Col>
                <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                  <Form.Item
                     wrapperCol={{ lg: 24,md:24,sm: 24 }}
                     labelCol={{ lg: 24,md:24,sm: 24 }}
                     label={'Numero Celular'}
                     name={'numeroCelular'}
                     rules={[
                      {
                        required: false,
                        message: null,
                      },
                     ]}
                     disabled
                    >
                      <Input disabled autoComplete="off" placeholder="" allowClearx />
                    </Form.Item>
                </Col>
                <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                  <Form.Item
                     wrapperCol={{ lg: 24,md:24,sm: 24 }}
                     labelCol={{ lg: 24,md:24,sm: 24 }}
                     label={'Ubicación'}
                     name={'ubicacion'}
                     rules={[
                      {
                        required: false,
                        message: null,
                      },
                     ]}
                     disabled
                    >
                      <Input disabled autoComplete="off" placeholder="" allowClearx />
                    </Form.Item>
                </Col>
                <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                  <Form.Item
                     wrapperCol={{ lg: 24,md:24,sm: 24 }}
                     labelCol={{ lg: 24,md:24,sm: 24 }}
                     label={'Dirección'}
                     name={'direccion'}
                     rules={[
                      {
                        required: false,
                        message: null,
                      },
                     ]}
                     disabled
                    >
                      <Input disabled autoComplete="off" placeholder="" allowClearx />
                    </Form.Item>
                </Col>
                <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                  <Form.Item
                     wrapperCol={{ lg: 24,md:24,sm: 24 }}
                     labelCol={{ lg: 24,md:24,sm: 24 }}
                     label={'Apartamento'}
                     name={'apartamento'}
                     rules={[
                      {
                        required: false,
                        message: null,
                      },
                     ]}
                     disabled
                    >
                      <Input disabled autoComplete="off" placeholder="" allowClearx />
                    </Form.Item>
                </Col>  
                <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                  <Form.Item
                     wrapperCol={{ lg: 24,md:24,sm: 24 }}
                     labelCol={{ lg: 24,md:24,sm: 24 }}
                     label={'persona Recibir'}
                     name={'personaRecibir'}
                     rules={[
                      {
                        required: false,
                        message: null,
                      },
                     ]}
                     disabled
                    >
                      <Input disabled autoComplete="off" placeholder="" allowClearx />
                    </Form.Item>
                </Col>  
                <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                <Form.Item
                  wrapperCol={{ lg: 24,md:24,sm: 24 }}
                  labelCol={{ lg: 24,md:24,sm: 24 }}
                  label="Estado Orden"
                  name={'estadoOrden'}
                  rules={[
                    {
                      required: true,
                      message: 'Seleccione Estado Orden',
                    },
                  ]}
                  >
                    <Select   
                      className="formControlform formControl"
                      placeholder="Seleccione Estado Orden"  
                      bordered={true}  
                      onChange={(e)=>{} }>
                      <Option value={null} key={-1} >{'Seleccione Estado Orden'}</Option>
                      {listStateOrder.map((item, i) => (
                          (<Option value={item.id} key={i} >{item.description}</Option>)

                          )  
                      )}
                      
                    </Select>
                  </Form.Item>
                </Col>
          </Row> 
          <Table scroll={{ x: 'calc(700px + 50%)', y: 640 }}
            dataSource={listadoDetailtDataOrden} 
            columns={columnsOrden} 
            pagination={false}/>
            
            
            <Button  htmlType="submit" className="btnNuevoRegistrar" type="primary" onClick={()=>{registerChangeOrder(); }}>Guardar Cambios</Button>
        </Form>
        </>
      </Card>
    </div>
    );
 
}
export default FormularioOrden;