import * as React from 'react';
import {useState,useEffect} from 'react';
import { Table,Form, Input,Pagination } from 'antd';
import { Modal } from 'antd';
import {  Button ,Select} from 'antd';
import {  Col, Row } from 'antd';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../redux/actions';
import  {requestApi}  from '../../api/indexTesis';
const { TextArea } = Input;

//import { Doughnut } from 'react-chartjs-2';
//import faker from 'faker';
//ChartJS.register(ArcElement, Tooltip, Legend);
const  GestionKeyLogger=()=> {
  const { Option } = Select;
  //const data = ;
  const columns = [
    {
      title: 'Fecha de Log',
      dataIndex: 'r_fechalog',
      key: 'r_fechalog',
      responsive: ['lg']
    },
    {
      title: 'Mac',
      dataIndex: 'r_mac',
      key: 'r_mac',
      responsive: ['lg']
    },
    {
      title: 'log',
      responsive: ['lg'],
      render: data =><> 
         <a className='spanName' style={{maxWidth:600,display:"block"}} onClick={()=>{abrirLog(data)}}  > {'ver log'}</a>
      </>
    }
  ];
  const [form] = Form.useForm();
  const dispatch = useDispatch();
  const listRef = React.createRef();
  const [listadoMac, setListadoMac] = React.useState([]);
  const [selectMaquina, setSelectMaquina] = useState("0");
  const [listDataKeyLogger, setListDataKeyLogger] = React.useState([]);
  const [valorLog,setValorLog] = useState("");
  const [paginaActualTabla,setPaginaActualTabla] = useState(1);
  const [paginaActual,setPaginaActual] = useState(1);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const [cantidadDatos,setCantidadDatos] = useState(1);
  //navegadorActivo
  const obtenerListadoKeyLogger=async(valPaginaActual)=>{
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
      //http://localhost:5001/listadoKeylogger?pagina=1&mac=
      let response=await requestApi(
        "listadoKeylogger?mac="+(selectMaquina==0 ? "" : selectMaquina)+"&pagina="+valPaginaActual,
        {},
        "get"
      );
      let cantidadEntera=parseInt(response.cantidad);
      let cantidadFlotante=(response.cantidad);
      let cantidadFormat=0;
      if(cantidadFlotante>cantidadEntera){
        cantidadFormat=parseInt(cantidadFlotante);
      }else{
        cantidadFormat=cantidadEntera;
      }
      setCantidadDatos((cantidadFormat));
      console.log("response.data",response.data);
      setListDataKeyLogger(response.data);
      //setListPropety(response.data);

      //console.log("respon.data",respon.data);
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
}
const handleOk = () => {
  setIsModalOpen(false);
};
const abrirLog=(dataVal)=>{
  console.log("dataVal",dataVal);
  console.log("dataVal",dataVal.r_data);
  setValorLog(dataVal.r_data);
  setIsModalOpen(true);

}
const handleCancel = () => {
  setIsModalOpen(false);
};

const obtenerMac=async()=>{
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
        //(this.host+"listadoHistorial?id="+this.keyApp+"&navegador="+this.selected)
      //selectNavegador
      let response=await requestApi(
        "dataAplicativosMack",
        {},
        "get"
      );
      setListadoMac(response.data);
      //console.log("respon.data",respon.data);
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
}


const changePaginationTabla=async(val,sizePagination)=>{
  setPaginaActualTabla(val);

  await obtenerListadoKeyLogger(val) ;
  //await obtenerAplicaciones(val) ;
}
const refrescarPagina=async()=>{
  //setPaginaActualTabla(val);

  await obtenerListadoKeyLogger(paginaActual) ;
  //await obtenerAplicaciones(val) ;
}
useEffect(()=>{
  let isSubscribed = true
  if(listRef?.current!=null){
   // console.log("dataDataSet",dataDataSet);
    if (!isSubscribed) return;
    (async()=>{
      //await obtenerListadoNavegadoresMasUsados();
      await obtenerMac();
      await obtenerListadoKeyLogger(paginaActual);
      //obtenerNavegadorActivos();
    })();
  }
  
  return () => isSubscribed = false;
},[]);

 

  return (
    <div ref={listRef}   className="contentPago">
        <>
        <Modal title="Log" open={isModalOpen} onOk={handleOk} onCancel={handleCancel}>
        <TextArea showCount defaultValue={valorLog} value={valorLog} rows="21" style={{ minHeight: 420 }} maxLength={99500}  disabled />

      </Modal>
          <Row>
            <Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 12 }} >
                <label>Maquina</label>
                <Select onChange={(e)=>{setSelectMaquina(e) }}
                    value={selectMaquina}        
                    style={{ width: "100%" }} >
                       <Option id={'0'} key={'0'}>{'Todas'}</Option>
                    {listadoMac.map(item => (
                      <Option id={item.r_mac} key={item.r_mac}>{item.r_mac}</Option>
                   ))}
                    
                </Select>            
            </Col>
            <Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 12 }} >
            
            <Button type="primary" style={{ marginTop: "25px",marginLeft: "25px" }} onClick={(e)=>{ refrescarPagina()}}>Buscar</Button>
   
            </Col>

          </Row>
        <Row>
            
            <Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 20 }} >
               
                <Table  dataSource={listDataKeyLogger} 
                        columns={columns}  
                        pagination={false}
                        rowKey={(record) => record.r_id}
                />  
                <Pagination  current={paginaActualTabla} total={cantidadDatos} onChange={(val,size)=>{changePaginationTabla(val,size)}} showSizeChanger={false} />
           
            </Col>
        </Row>
      
          
        </> 
        
    </div>
    );
 
}
export default GestionKeyLogger;