import {useState,useEffect} from 'react';
import  {requestApi}  from '../../../api/index';
import swal from 'sweetalert';
import { PageHeader } from 'antd';
import { Card } from 'antd';
import * as React from 'react';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../../redux/actions';
import {
    Form,
    Input,
    Button,
    Select,
    Switch,
  } from 'antd';
const { Option } = Select;

const  FormularioCargo=(props)=> {
    const [form] = Form.useForm();
    const [componentSize, setComponentSize] = useState('large');
    const listRef = React.createRef();
    const [vigencia, setVigencia] = React.useState(true);
    const [generoData, setGeneroData] = React.useState([]);
    const [cargoData, setCargoData] = React.useState([]);
    const [mensajeErrorDocumento, setMensajeErrorDocumento] = React.useState(null);
    const [mensajeErrorCorreo, setMensajeErrorCorreo] = React.useState(null);
    const dispatch = useDispatch();
    const onFormLayoutChange = ({ size }) => {
      setComponentSize(size);
    };
    
  useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
   
        if (!isSubscribed) return;
      }
      
      return () => isSubscribed = false;
  },[]);


  const registerCharge=async()=>{
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
      let dataRequest={
        "name":form.getFieldValue("nombres"),
        "validity":vigencia
      };
      let respon=await requestApi(
        `charge/registerCharge`,
        dataRequest,
        "post"
      );
      if(respon.estado){
        swal({
          title: "Registrar Cargo.",
          text: respon.mensaje,
          icon:  respon.estado ? "success":"error",
          button: "aceptar"
        });
        props.refrescarListado();

      }else{
        swal({
          title: "Registrar Cargo.",
          text: respon.mensaje,
          icon:  respon.estado ? "success":"error",
          button: "aceptar"
        });
      }
      //setMensajeErrorCorreo(respon.estado ? null : respon.mensaje  );
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
   
  }
  return (
    <div ref={listRef}   className="contentCargo">
      <Card title={<PageHeader
    className="site-page-header"
    onBack={() => props.atras()}
    title="Nuevo Cargo"
    subTitle={null}
  />} bordered={false} style={{ width: "100%" }}>
        <>
        <Form
            labelCol={{
            span: 4,
            }}
            wrapperCol={{
            span: 14,
            }}
            layout="vertical"
            initialValues={{
            size: componentSize,
            }}
            onValuesChange={onFormLayoutChange}
            size={componentSize}
            form={form}
        >
            <Form.Item 
              name={[ 'nombres']}
              label="Nombre"
              max="500"
              rules={[{ required: true, message: 'Se Requiere de Nombre.' }]}
              wrapperCol={{ lg: 14,md:14,sm: 24 }}
              >
                <Input autoComplete="off" />
            </Form.Item>

            <Form.Item 
              name={[ 'vigencia']} 
              label="Vigencia"
              wrapperCol={{ lg: 14,md:14,sm: 24 }}
            >
                 <Switch checked={vigencia} onChange={(e)=>{console.log("setVigencia",e);setVigencia(e);}} />
            </Form.Item>
            <Button  htmlType="submit" className="btnNuevoRegistrar" type="primary" onClick={()=>{ registerCharge()}}>Registrar</Button>
        </Form>
        </>
      </Card>
    </div>
    );
 
}
export default FormularioCargo;