import {useState,useEffect} from 'react';
import  {requestApi}  from '../../api/index';
import  env  from '../../env.json';
import swal from 'sweetalert';
import { PageHeader } from 'antd';
import { Card } from 'antd';
import * as React from 'react';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../redux/actions';
import {
    Form,
    Input,
    Button,
    Select,
  } from 'antd';
import { InputNumber } from 'antd';
import { Pagination } from 'antd';
import { Row, Col } from 'antd';
import { Table } from 'antd';
import {  Modal } from 'antd';
import { FilterOutlined } from '@ant-design/icons';
const { Option } = Select;
const  FormularioVenta=(props)=> {
    const columnsFilterProduct = [
      {
        title: 'Producto',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Precio',
        dataIndex: 'price',
        key: 'price',
      },
      {
        title: 'Cantidad Disponible',
        dataIndex: 'stock',
        key: 'stock',
      },
      {
        title: 'Categoria',
        dataIndex: 'descriptionCategory',
        key: 'descriptionCategory',
      },
      
      {
        title: 'Marca',
        dataIndex: 'nameBrand',
        key: 'nameBrand',
      },
      
      {
        title: 'Caracteristicas',
        dataIndex: 'nameCharacterist',
        key: 'nameCharacterist',
      },
      {
        title: 'Acción',
        dataIndex: 'id',
        sorter: false,
        render: (data, row) =><> <Button type="primary" onClick={()=>{seleccionarDatos(data)}}>{row["existProductSelect"]===true  ? "fue Seleccionado" : "Seleccionar"}</Button>
        </>
        ,
        width: '20%'
      },
    ];
 
    const columnsSales = [
      {
        title: 'Producto',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Cantidad',
        dataIndex: 'count',
        key: 'count',
      },
      {
        title: 'Precio Unitario',
        dataIndex: 'price',
        key: 'price',
      },
      {
        title: 'Sub Total',
        dataIndex: 'subtotal',
        key: 'subtotal'
       ,
      },
      {
        title: 'Acción',
        dataIndex: 'key',
        sorter: false,
        render: data =><> 
         <Button type="primary" danger onClick={()=>{deleteProductDetail(data)}}>
          Eliminar
        </Button>
        </>
        ,
        width: '20%'
      },
    ];
    const [form] = Form.useForm();
    const [componentSize, setComponentSize] = useState('large');
    const listRef = React.createRef();
    const [dataCategory, setDataCategory] = React.useState([]);
    const [dataBrand, setDataBrand] = React.useState([]);
  
    const [visibleFilterProduct, setVisibleFilterProduct] = React.useState(null);

    const [formRef, setformRef] = useState(null);
    const [listCountry, setListCountry] = React.useState([]);
    const [listTypeTicket, setListTypeTicket] = React.useState([]);
    const [listTypeDocument, setlistTypeDocument] = React.useState([]);
    const [listTypeDocumentAll, setlistTypeDocumentAll] = React.useState([]);
    const [selectCountry, setSelectCountry] = React.useState(-1);
    const [listGeoForm, setListGeoForm] = React.useState([]);
    const [listProductFilter, setListProductFilter] = React.useState([]);
    const [loading, setLoading] = React.useState(false);
    const [listProduct, setListProduct] = React.useState([]);
    const [listProductAll, setListProductAll] = React.useState([]);
    const [selectProduct, setSelectProduct] = React.useState(null);
    const [typeTicketSelect, setTypeTicketSelect] = React.useState(null);
    const [labelNumberDocumentDefault/*, setLabelNumberDocumentDefault*/] = React.useState("Numbero Documento");
    const [labelNumberDocument, seLlabelNumberDocument] = React.useState(labelNumberDocumentDefault);
    const [labelNameDocumentDefault/*, setLabelNameDocumentDefault*/] = React.useState("Nombres");
    const [labelNameDocument, setLabelNameDocument] = React.useState(labelNameDocumentDefault);
    const [listDetailtSales, setListDetailtSales] = React.useState([]);
    const [countProduct/*,setCountProduct*/] = React.useState(1);
    const [inputSearch, setInputSearch] = useState("");
    const [listBrandSelect, setListBrandSelect] = React.useState([]);
    const [listCategorySelect, setListCategorySelect] = React.useState([]);
    const [maxNumberStock, setMaxNumberStock] = React.useState(10);
    //maxNumberStock
    const [codeTypeDocument, setCodeTypeDocument] = useState(null);
    const [paginaActual, setPaginaActual] = useState(1);
    const [contarTotal, setContarTotal] = useState(0);
    const [cantidadContenido, setCantidadContenido] = useState(10);
    const dispatch = useDispatch();
    
    //listGeoForm
    useEffect(()=>{
      console.log("listGeoForm",listGeoForm);
    },[listGeoForm]);
    useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        getCountry();
        let formatKeyIndex={};
        //formatKeyIndex["Provincia"]=parseInt(6);
        form.setFieldsValue(formatKeyIndex);  
        loaderTypeTicket();
        cargarFilterProduct(paginaActual-1,cantidadContenido);
        cargarCategory();
        cargarBrand();
        getProducts();
        getTypeDocument();
        form.setFieldsValue({
          countProduct:1
        });
        if (!isSubscribed) return;
      }
      
      return () => isSubscribed = false;
    },[]);
    useEffect(()=>{
      if(form.getFieldValue("typeSales")=="F"){
        let tempTypeDocumentList=listTypeDocumentAll.filter((val)=>{
            return val.code=="NUMBDOCRUC";
        });
        setlistTypeDocument(JSON.parse(JSON.stringify(tempTypeDocumentList)));
      }else{
        let tempTypeDocumentList=listTypeDocumentAll;
        setlistTypeDocument(JSON.parse(JSON.stringify(tempTypeDocumentList)));
      }
      form.setFieldsValue({
        "typeDocument":null
      });
      form.setFieldsValue({
        names:null,
        documento:null,
        phoneNumber:null,
        lastName:null,
        motherlastName:null,
        contry:null,
        address:null,
        numberAddress:null,
        referenciesAddress:null
      });
      listGeoForm.forEach(element => {
        let itemFor={};
        itemFor[element.name]=null;
        form.setFieldsValue(itemFor);
      });
      setListGeoForm([]);
      return;
    },[form.getFieldValue("typeSales")]);
    useEffect(()=>{
      console.log("listProductAll",listProductAll);
      return;
    },[listProductAll]);
    useEffect(()=>{
        (async()=>{
          await cargarFilterProduct(paginaActual-1,cantidadContenido);
        })();
      return;
    },[listBrandSelect,listCategorySelect,inputSearch]);
    const getProducts=async()=>{
      try{
        let respon=await requestApi(
          `product/loaderProductSales`,
          {},
          "get"
        );
        let data =respon.state ? respon.data : [];
        setListProduct(data);
        setListProductAll(data);
        console.log("data",data);
      }catch(e){
        return e;
      }
      return "ok;"
    }

    const getTypeDocument=async()=>{
      try{
        let respon=await requestApi(
          `dataGeneral/loaderTypeDocument`,
          {},
          "get"
        );
        let data =respon.state ? respon.data : [];
        setlistTypeDocument(data);
        setlistTypeDocumentAll(data);
      }catch(e){
        return e;
      }
      return "ok;"
    }
    const getCountry=async()=>{
      try{
        let respon=await requestApi(
          `dataGeneral/getCountry`,
          {},
          "get"
        );
        let data =respon.state ? respon.data : [];
        setListCountry(data);
      }catch(e){
        return e;
      }
      return "ok;"
    }
    const loaderTypeTicket=async()=>{
      try{
        let respon=await requestApi(
          `dataGeneral/loaderTicket`,
          {},
          "get"
        );
        let data =respon.state ? respon.data : [];
        setListTypeTicket(data);
        form.setFieldsValue({
          typeSales:"B"
        });
      }catch(e){
        return e;
      }
      return "ok;"
    }
    const deleteProductDetail=(key)=>{
      let temlistDetailt=listDetailtSales.filter((val)=>{
          return val.key!=key;
      });
      temlistDetailt=temlistDetailt.map((val,i)=>{
        val["key"]=i;
        return val;
      });
      let temSelectlistDetailt=listDetailtSales.filter((val)=>{
        return val.key==key;
      });
      setListDetailtSales(JSON.parse(JSON.stringify(temlistDetailt)));
      let itemSelectProduct=listProductAll.filter((vaal)=>{
        return vaal.id==temSelectlistDetailt[0].idProduct;
      });
      let copiProductList=JSON.parse(JSON.stringify(listProduct));
      copiProductList.push(itemSelectProduct[0]);
      setListProduct(copiProductList);
    
    }
    
    const seleccionarDatos=async(id)=>{
      let selectProductFront=await listProductAll.filter((val)=>{
        return val.id==id;
      });
      let valueDetail=await listDetailtSales.filter((val)=>{
          return val.idProduct==id;
      });
      setVisibleFilterProduct(!visibleFilterProduct);
      if(valueDetail.length>0){
        swal({
          title:  "Realizar Venta.",
          text:"El producto ya fue Agregado.",
          icon: "warning",
          button: "aceptar"
        });
        return false;
      }
      if(selectProductFront.length){
        form.setFieldsValue({
          filtroProducto: selectProductFront[0].id
        });
      }else{
        form.setFieldsValue({
          filtroProducto: undefined
        });
      }
     
     
    }
    const registerSales=async()=>{
      console.log("listDetailtSales",listDetailtSales);
      if(listDetailtSales.length==0){
        swal({
          title:  "Realizar Venta.",
          text:"Se requiere al menos de un producto.",
          icon: "warning",
          button: "aceptar"
        });
        return false;
      }
  
      form.validateFields()
      .then(async(values) => {
        dispatch(loaderSpiner({
          loaderSpiner:true
        }));
        try{
         let arrayDetailFormat= listDetailtSales.map((val)=>{
            return  {
              idProduct:val.idProduct,
              amount:val.count,
            }
         });
          let idTicketSelect=null;
          let tempDa=listTypeTicket.filter((val)=>{
            return val.code==form.getFieldValue("typeSales");
          });
          idTicketSelect=tempDa.length ? tempDa[0].id: null;
          let registerReq={
            "names":form.getFieldValue("names"),
            "lastName":form.getFieldValue("lastName"),
            "motherLastname":form.getFieldValue("motherlastName"),
            "referenciesAddress":form.getFieldValue("referenciesAddress"),
            "legalRepresentative":form.getFieldValue("legalRepresentative"),
            "idTypeSales":idTicketSelect,
            "numberAddress":form.getFieldValue("numberAddress"),
            "address":form.getFieldValue("address"),
            "numberDocument":form.getFieldValue("documento"),
          
            "phoneNumber":form.getFieldValue("phoneNumber"),
            "idUbigeo":form.getFieldValue(listGeoForm[listGeoForm.length-1].name),
            "idTypeDocument":form.getFieldValue("typeDocument"),
            "detailSales":arrayDetailFormat
          };
          let respon=await requestApi(
            `sales/registerSales`,
            registerReq,
            "post"
          );
          if(respon.state){
            setTimeout(function(){
              swal({
                title:  "Registrar Venta.",
                text: respon.message,
                icon:  respon.state ? "success":"error",
                button: "aceptar"
              }).then(async()=>{
                await getProducts();
                window.open(respon.urlTicket, '_blank');
                dispatch(loaderSpiner({
                  loaderSpiner:false
                }));
              });
            },3000);
           
           
          }else{
            swal({
              title:   "Registrar Venta.",
              text: respon.message,
              icon:  respon.state ? "success":"error",
              button: "aceptar"
            });
          }
          form.resetFields();
          setListDetailtSales([]);
        }catch(e){
          dispatch(loaderSpiner({
            loaderSpiner:false
          }));
          return e;
        }finally{
          
        }
      })
      .catch((errorInfo) => {
      });
      
    
    }
    const cargarCategory = async() => {
    
      try{
        let respon=await requestApi(
          `dataGeneral/loaderCategory`,
          {},
          "get"
        );
        setDataCategory(respon.estado ? respon.data : []  );
      }catch(e){
        return e;
      }finally{
      
      }
    
    };
    const cargarBrand = async() => {
      
      try{
        let respon=await requestApi(
          `dataGeneral/loaderBrand`,
          {},
          "get"
        );
        setDataBrand(respon.estado ? respon.data : []  );
        
      }catch(e){
        return e;
      }finally{
        
      }
    
    };

    const cargarFilterProduct = async(reqpaginaActual,reqcantidadContenido) => {
      let valueListBrandSelect=listBrandSelect==undefined ? "" : listBrandSelect;
      let valueListCategorySelect=listCategorySelect==undefined ? "" : listCategorySelect;
      setLoading(true);
      try{
        let respon=await requestApi(
          `product/listProductSales?paginateActive=${reqpaginaActual}&countContent=${reqcantidadContenido}&categoryId=${valueListCategorySelect}&brandId=${valueListBrandSelect}&name=${inputSearch}`,
          {},
          "get"
        );
        setListProductFilter(respon.estado ? respon.data : []  );
        setContarTotal(respon.estado ? respon.countData : 0);
        
      }catch(e){
        return e;
      }finally{
        setLoading(false);
      }
    
    };
    const changePagination=async(val,sizePagination)=>{
      setPaginaActual(val);
      setCantidadContenido(sizePagination);
      await cargarFilterProduct(val-1,sizePagination);
    }

    const getFomStateGeo=async(ridcountri,ridparent)=>{
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `dataGeneral/getGeoParentId?idContry=${ridcountri}&idParent=${ridparent}`,
          {},
          "get"
        );
        let data =respon.state ? respon.data : [];
        return data;
      }catch(e){
        return [];
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }
    }
    const selectTypeDocumentMeth=async(item)=>{
      //item
      form.setFieldsValue({
        names:null,
        lastName:null,
        documento:null,
        motherlastName:null,
        contry:null,
        address:null,
        numberAddress:null,
        referenciesAddress:null,
        phoneNumber:null
      });
      listGeoForm.forEach(element => {
        let itemFor={};
        itemFor[element.name]=null;
        form.setFieldsValue(itemFor);
      });
      setListGeoForm([]);

      if(item==null){
        seLlabelNumberDocument(labelNumberDocumentDefault);
        setLabelNameDocument(labelNameDocumentDefault );

        return false;
      }
      let selectItem=listTypeDocument.filter((itemSelect)=>{
        return itemSelect.id==item;
      });
      setCodeTypeDocument(selectItem[0].etiqueta==null ? null : selectItem[0].code);
      seLlabelNumberDocument(selectItem[0].etiqueta==null ? labelNumberDocumentDefault : selectItem[0].etiqueta );
      setLabelNameDocument(selectItem[0].etiquetaName==null ? labelNameDocumentDefault : selectItem[0].etiquetaName );
    };
    const selectContryMeth=async(rId)=>{
      form.setFieldsValue({
        "contry":rId
      }); 
    
      setSelectCountry(rId);
      if(rId==null){
        setListGeoForm([]);
      }else{
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `dataGeneral/getUbigeoLevel?idContry=`+rId,
          {},
          "get"
        );
        let data =respon.state ? respon.data : [];
        if(data.length>0){
          let tempDataSelect=await getFomStateGeo(rId,"");
          
          data.forEach((element,position) => {
            
            if(position==0){
              element["listGeoAddress"]=tempDataSelect;
            }
            let jsonDat={};
            jsonDat[element.name]=null;
          });
          setListGeoForm(data);
          return data;
        }else{
          setListGeoForm([]);
          return [];
        }
      }catch(e){
        return e;
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }
    }
    }
    const selectGeoIndex=async(index,item)=>{
      try{
      let templistGeoForm=[...listGeoForm];
      if(templistGeoForm.length>=(index+1)){
        let tempDataSelect=[];
        if(item!=null){
          tempDataSelect=await getFomStateGeo(selectCountry,item);
        }
        templistGeoForm.forEach((element,position) => {
          if(position==index){
            element["selectGeo"]=item;
          }
          if(position==index+1){
            //if(idAddressSelect==null){
              let tempFor={};
              tempFor[element.name]=null;
              form.setFieldsValue(tempFor);
              element["selectGeo"]=null;
              
          // }
            element["listGeoAddress"]=tempDataSelect;
          }
          if(position>=index+1){
            let tempFor={};
            tempFor[element.name]=null;
            form.setFieldsValue(tempFor);
            element["selectGeo"]=null;
          }
          
        });
        setListGeoForm(templistGeoForm);
      }
      

      }catch(e){
        return e;
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }
      return "ok;"
    }

    
    const addDetailt=()=>{

        if(form.getFieldValue("filtroProducto")==null || form.getFieldValue("filtroProducto")==null){
          swal({
            title:"Añadir Producto.",
            text: "Se requiere de Producto",
            icon:  "warning",
            button: "aceptar"
          });
          return false;
        }
        if(form.getFieldValue("countProduct")==null || form.getFieldValue("countProduct")==undefined || form.getFieldValue("countProduct")==""){
          swal({
            title:"Añadir Producto.",
            text: "Se requiere de Producto",
            icon:  "warning",
            button: "aceptar"
          });
          return false;
      }
      let copiList=JSON.parse(JSON.stringify(listDetailtSales));
      let selectItem=listProduct.filter((itemSelect)=>{
        return itemSelect.id==form.getFieldValue("filtroProducto");
      });
      //selectItem[0].stock
      let countProduct=parseInt(form.getFieldValue("countProduct"));
      if(selectItem[0].stock<countProduct){
        swal({
          title:"Añadir Producto.",
          text: "No cuenta con suficiente stock.",
          icon:  "warning",
          button: "aceptar"
        });
        return false;
      }
      copiList.push({
        key:listDetailtSales.length,
        name:selectItem[0].name,
        price:selectItem[0].price,
        subtotal:selectItem[0].price*form.getFieldValue("countProduct"),
        idProduct:form.getFieldValue("filtroProducto"),
        count:form.getFieldValue("countProduct"),
      });
      let tempListProduct=listProduct.filter((val)=>{
        return val.id!=form.getFieldValue("filtroProducto");
      });
      setListProduct(JSON.parse(JSON.stringify(tempListProduct)));
      form.setFieldsValue({
        filtroProducto: undefined,
        countProduct:1
      });
      setListDetailtSales(copiList);
    }
    const abrirModal=()=>{
      let templistProductFilter=listProductFilter.map((val)=>{
        val["existProductSelect"]=null;
        let existProduct=listDetailtSales.filter((valFilter)=>{
            return valFilter.idProduct==val.id;
        });
        val["existProductSelect"]=existProduct.length ? true : false;
        return val;
      });
      setListProductFilter(JSON.parse(JSON.stringify(templistProductFilter)));
      setVisibleFilterProduct(!visibleFilterProduct);
    }
  
    const getContryMeth2=async(itemDat)=>{
      setSelectCountry(itemDat.idCountry);
      if(itemDat.idCountry==null){
        setListGeoForm([]);
      }else{
        dispatch(loaderSpiner({
          loaderSpiner:true
        }));
        try{
          let respon=await requestApi(
            `dataGeneral/getUbigeoLevel?idContry=`+itemDat.idCountry,//idCountry
            {},
            "get"
          );
          let data =respon.state ? respon.data : [];
          if(data.length>0){
            let tempDataSelect=await getFomStateGeo(itemDat.idCountry,"");
            
            await data.forEach(async(element,position) => {
              
              if(position==0){
                element["selectGeo"]=parseInt(itemDat.geo.split(",")[position]);
                let jsonDat={};
                jsonDat[element.name]=parseInt(itemDat.geo.split(",")[position]);
                form.setFieldsValue(jsonDat);
                element["listGeoAddress"]=tempDataSelect;
              }else{
                let tempFor=await  getFomStateGeo(itemDat.idCountry,itemDat.geo.split(",")[position-1]);
                let jsonDat={};
                element["listGeoAddress"]=tempFor;
                jsonDat[element.name]=parseInt(itemDat.geo.split(",")[position]);
                console.log(element.name,itemDat.geo.split(",")[position]);
                form.setFieldsValue(jsonDat);
              }
              setListGeoForm(JSON.parse(JSON.stringify(data)));
            });
            return data;
          }else{
            setListGeoForm([]);
            form.setFieldsValue({ sights: [] });
            return [];
          }
        }catch(e){
          return e;
        }finally{
          dispatch(loaderSpiner({
            loaderSpiner:false
          }));
        }
      }
    }
    
    const searchClient=(e)=>{
      console.log("e",e.target.value);
      if(form.getFieldValue("typeDocument")==null){
        swal({
          title:  "Busqueda Cliente.",
          text:"Se requiere de tipo de documento.",
          icon: "warning",
          button: "aceptar"
        });
        return false;
      }
      if(form.getFieldValue("documento")==null || form.getFieldValue("documento")=="" || form.getFieldValue("documento")==undefined){
        swal({
          title:  "Busqueda Cliente.",
          text:"Se requiere de numero de documento.",
          icon: "warning",
          button: "aceptar"
        });
        return false;
      }
      searchResponseClient(form.getFieldValue("typeDocument"),form.getFieldValue("documento"));
    }
    const selectProductSales =(val)=>{
      let selectItem=listProduct.filter((itemSelect)=>{
        return itemSelect.id==form.getFieldValue("filtroProducto");
      });
      setSelectProduct(val);
      if(val!=null){
        setMaxNumberStock(selectItem[0].stock);
      }
    }
    const searchResponseClient = async(typeDocument,numberDocument) => {
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `userClients/searchClient?typeDocument=${typeDocument}&numberDocument=${numberDocument}`,
          {},
          "get"
        );
        //return respon.state ? respon.data : respon.state;
        console.log("respon",respon);
        if(respon.state){
          
            await getCountry();
            form.setFieldsValue({
              contry: respon.data.idCountry,
            });
            await getContryMeth2(respon.data);
            console.log("respon.data.numberAddress:",respon.data.numberAddress);
            form.setFieldsValue({
              phoneNumber:respon.data.phoneNumber,
              names:respon.data.names,
              lastName:respon.data.lastName,
              motherlastName:respon.data.motherLastName,
              address:respon.data.address,
              numberAddress:respon.data.numberAddress,
              referenciesAddress:respon.data.referencieAddress,
            });
        
        }else{
          form.setFieldsValue({
            names:null,
            phoneNumber:null,
            lastName:null,
            motherlastName:null,
            contry:null,
            address:null,
            numberAddress:null,
            referenciesAddress:null
          });
          listGeoForm.forEach(element => {
            let itemFor={};
            itemFor[element.name]=null;
            form.setFieldsValue(itemFor);
          });
          setListGeoForm([]);
        }
        //setMensajeErrorCorreo(respon.estado ? null : respon.mensaje  );
      }catch(e){
        return e;
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }
    
    };
  return (
    <div ref={listRef}   className="content">
      <Modal className="modalContent" bodyStyle={{height:"60vh"}} style={{ top: 0,left: 0,right:0 }} centered width={"100%"} visible={visibleFilterProduct} title={'Seleccionar Producto'}  onCancel={()=>{setVisibleFilterProduct(!visibleFilterProduct)}}>
          <Row>
            <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 6 }} >
              <label> Categoria</label>
              <Select
                  mode="single"
                  showSearch
                  allowClear
                  filterOption={(input, option) =>
                    option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                  }
                  value={listCategorySelect}
                  style={{ width: '100%' }}
                  placeholder="Categoria"
                  onChange={(val)=>{setListCategorySelect(val);}}
                  
                >
                  {dataCategory.map((item, i) => (
                      (<Option value={item.id} key={i} >{item.name}</Option>)

                      )  
                  )}
                </Select>
            </Col>
            <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 6 }} >
              <label> Marca</label>
              <Select
                  mode="single"
                  showSearch
                  allowClear
                  filterOption={(input, option) =>
                    option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                  }
                  style={{ width: '100%' }}
                  placeholder="Marca"
                  value={listBrandSelect}
                  onChange={(val)=>{setListBrandSelect(val)}}
                >
                  {dataBrand.map((item, i) => (
                      (<Option value={item.id} key={i} >{item.name}</Option>)

                      )  
                  )}
                </Select>
            </Col>
            <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 6 }} >
              <div className="contentFilterName">
                <label> Nombre</label>
                <Input style={{ width: '100%' }} defaultValue={inputSearch}  onChange={(e)=>{ setInputSearch(e.target.value);}}/>
              
              </div>
                   
            </Col>

          </Row>
        <div className="tableContent">
          <Table
            scroll={{ x: 'calc(700px + 50%)', y: 340 }}
            columns={columnsFilterProduct}
            rowKey={record => record.id}
            dataSource={listProductFilter}
            pagination={false}
            loading={loading}
          />
          <Pagination  className="paginationTable paginationGestionUsuario" current={paginaActual} defaultCurrent={paginaActual} total={contarTotal}  onChange={(val,size)=>{changePagination(val,size)}} />
           
          </div>
      </Modal>

      <Card title={<PageHeader
        className="site-page-header"
        onBack={() => props.atras()}
        title={ "Gestionar  Venta"}
        subTitle={null}
      />} bordered={false} style={{ width: "100%" }}>
        <>
        <Form
            labelCol={{
            span: 4,
            }}
            wrapperCol={{
            span: 14,
            }}
            layout="vertical"
            size={componentSize}
            form={form}
            ref={(e) => {
              setformRef(e );
             }}
        >
           <Row> 
              <Col className="formProduct" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 24 }} >
                <Form.Item 
                  name={[ 'typeSales']}
                  label="Tipo Venta"
                  wrapperCol={{ lg: 12,md:24,sm: 24 }}
                  labelCol={{ lg: 24,md:24,sm: 24 }}
                  >
                  <Select
                    
                    placeholder="Seleccionar Tipo  venta"
                    value={typeTicketSelect}
                    onChange={(val)=>{setTypeTicketSelect(val)}}
                    style={{ width: '100%' }}
                  >
                    {listTypeTicket.map((item, i) => (
                    (<Option value={item.code} key={i} >{item.description}</Option>)

                    )  
                )}
                  </Select>
                  </Form.Item>
              </Col>
              <Col className="formProduct" xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 12 }} >
                <Form.Item 
                  name={[ 'filtroProducto']}
                  label="productos"
                  wrapperCol={{ lg: 24,md:24,sm: 24 }}
                  labelCol={{ lg: 24,md:24,sm: 24 }}
                  
                  >
                  <Select
                    allowClear
                    showSearch
                    placeholder="Seleccionar Producto"
                    value={selectProduct}
                    onChange={(val)=>{selectProductSales(val);}}
                    style={{ width: '100%' }}
                  >
                    {listProduct.map((item, i) => (
                    (<Option value={item.id} key={i} >{item.name}</Option>)

                    )  
                )}
                  </Select>
                  </Form.Item>
                    <Button className="filterProductAdavance" type="primary" onClick={()=>{ abrirModal(); }}><FilterOutlined /></Button>
              </Col>
              <Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 12 }} >
                <div className="contentBtnVenta" style={{width:"100%"}}>
                  <Form.Item 
                   
                    name={[ 'countProduct']}
                    label={"Cantidad"}
                    wrapperCol={{ lg: 24,md:24,sm: 24 }}
                    labelCol={{ lg: 24,md:24,sm: 24 }}
                    >
                      <InputNumber type="number"  value={countProduct} min={1} max={maxNumberStock} style={{width:"100%"}} onChange={()=>{}} />
                    </Form.Item>
                    <Button type="primary" onClick={()=>{addDetailt()}}>Añadir</Button>

                </div>
              </Col>
           </Row>            
            <Table scroll={{ x: 'calc(700px + 50%)', y: 340 }} dataSource={listDetailtSales} columns={columnsSales} />
            <Row>
                <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                  <Form.Item
                     wrapperCol={{ lg: 24,md:24,sm: 24 }}
                     labelCol={{ lg: 24,md:24,sm: 24 }}
                    label="Tipo Documento"
                    name={'typeDocument'}
                    rules={[
                      {
                        required: true,
                        message: 'Seleccione Tipo Documento',
                      },
                    ]}
                    >
                      <Select   
                        className="formControlform formControl"
                        placeholder="Seleccione Tipo Documento"  
                        bordered={true}  
                        allowClear
                        onChange={(e)=>{selectTypeDocumentMeth(e) } }>
                        {listTypeDocument.map((item, i) => (
                            (<Option value={item.id} key={i} >{item.description}</Option>)

                            )  
                        )}
                        
                      </Select>
                    </Form.Item>
                  
                </Col>
                <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                  <Form.Item
                     wrapperCol={{ lg: 24,md:24,sm: 24 }}
                     labelCol={{ lg: 24,md:24,sm: 24 }}
                    label={labelNumberDocument}
                    name={'documento'}
                    rules={[
                      {
                        required: true,
                        message: 'Se Requiere de '+labelNumberDocument,
                      },
                    ]}
                    >
                      <Input autoComplete="off" placeholder="" allowClear onBlur={(e)=>{searchClient(e) } } />
                    </Form.Item>
                </Col>
                <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                  <Form.Item
                     wrapperCol={{ lg: 24,md:24,sm: 24 }}
                     labelCol={{ lg: 24,md:24,sm: 24 }}
                    label={labelNameDocument}
                    name={'names'}
                    rules={[
                      {
                        required: true,
                        message: 'Se Requiere de '+labelNameDocument,
                      },
                    ]}
                    >
                        <Input autoComplete="off" placeholder="" allowClear  />
                    </Form.Item>
                </Col>
                {
                  codeTypeDocument=="NUMBDOCRUC" ?
                  <>
                    <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                      <Form.Item
                        wrapperCol={{ lg: 24,md:24,sm: 24 }}
                        labelCol={{ lg: 24,md:24,sm: 24 }}
                        label={"Representante Legal"}
                        name={'legalRepresentative'}
                        rules={[
                          {
                            required: true,
                            message: 'Se Requiere de Representante legal.',
                          },
                        ]}
                        >
                            <Input autoComplete="off" placeholder="" allowClear  />
                        </Form.Item>
                    </Col>
                  </> :
                  <>
                     <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                      <Form.Item
                        wrapperCol={{ lg: 24,md:24,sm: 24 }}
                        labelCol={{ lg: 24,md:24,sm: 24 }}
                        label={"Apellidos Paterno"}
                        name={'lastName'}
                        rules={[
                          {
                            required: true,
                            message: 'Se Requiere de Apellido Paterno',
                          },
                        ]}
                        >
                            <Input autoComplete="off" placeholder="" allowClear  />
                        </Form.Item>
                    </Col> 
                    <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                      <Form.Item
                        wrapperCol={{ lg: 24,md:24,sm: 24 }}
                        labelCol={{ lg: 24,md:24,sm: 24 }}
                        label={"Apellidos Materno"}
                        name={'motherlastName'}
                        rules={[
                          {
                            required: true,
                            message: 'Se Requiere de Apellido Materno',
                          },
                        ]}
                        >
                            <Input autoComplete="off" placeholder="" allowClear  />
                        </Form.Item>
                    </Col>  
                  </>
                }
                <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                  <Form.Item
                  wrapperCol={{ lg: 24,md:24,sm: 24 }}
                  labelCol={{ lg: 24,md:24,sm: 24 }}
                  label="Pais"
                  name={'contry'}
                  rules={[
                    {
                      required: true,
                      message: 'Seleccione Pais',
                    },
                  ]}
                  >
                    <Select   
                      className="formControlform formControl"
                      placeholder="Seleccione Pais"  
                      bordered={true}  
                      onChange={(e)=>{selectContryMeth(e) } }>
                      <Option value={null} key={-1} >{'Seleccione Pais'}</Option>
                      {listCountry.map((item, i) => (
                          (<Option value={item.id} key={i} >{item.name}</Option>)

                          )  
                      )}
                      
                    </Select>
                  </Form.Item>
                </Col>
                
                {listGeoForm.map((item, i) => (
                  (<Col key={i} xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                    <Form.Item
                    wrapperCol={{ lg: 24,md:24,sm: 24 }}
                    labelCol={{ lg: 24,md:24,sm: 24 }}
                    
                    label={item.name}
                    name={item.name}
                    rules={[
                      {
                        required: true,
                        message: 'seleccione '+item.name,
                      },
                    ]}
                  >
                    
                    <Select   
                      placeholder={"seleccione "+item.name}  
                      bordered={true}  
                      name={item.name}
                      label={item.name}
                      onChange={(e)=>{selectGeoIndex(i,e)} }>
                      <Option value={null} key={"-"+item.name}>{'seleccione '+item.name}</Option>
                        {
                          (item.listGeoAddress==undefined ? [] : item.listGeoAddress).map((itemAddress, iAddress) => (
                            (<Option value={itemAddress.id} key={itemAddress.id+"-"+i+"-"+iAddress}>{itemAddress.name}</Option>)
                        
                          )  
                          )
                        }
                    </Select>
                  </Form.Item></Col>
                  )

                  )  
                )}
               <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                  <Form.Item
                    wrapperCol={{ lg: 24,md:24,sm: 24 }}
                    labelCol={{ lg: 24,md:24,sm: 24 }}
                    label={"Numero Celular"}
                    name={'phoneNumber'}
                    rules={[
                      {
                        required: true,
                        message: 'Se Requiere de Numero Celular.',
                      },
                    ]}
                    >
                        <Input autoComplete="off" placeholder="" allowClear  />
                    </Form.Item>
                </Col>          
              <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                <Form.Item
                  wrapperCol={{ lg: 24,md:24,sm: 24 }}
                  labelCol={{ lg: 24,md:24,sm: 24 }}
                  label={"dirección"}
                  name={'address'}
                  rules={[
                    {
                      required: false,
                      message: 'Se Requiere de Dirección',
                    },
                  ]}
                  >
                      <Input autoComplete="off" placeholder="" allowClear  />
                  </Form.Item>
              </Col>
              <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                <Form.Item
                   wrapperCol={{ lg: 24,md:24,sm: 24 }}
                   labelCol={{ lg: 24,md:24,sm: 24 }}
                  label={"Numero Dirección"}
                  name={'numberAddress'}
                  rules={[
                    {
                      required: false,
                      message: 'Se Requiere de numero dirección',
                    },
                  ]}
                  >
                      <Input type="number" maxLength={6} autoComplete="off" placeholder="" allowClear  />
                </Form.Item>
            </Col>
            <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 8 }} >
                <Form.Item
                  wrapperCol={{ lg: 24,md:24,sm: 24 }}
                  labelCol={{ lg: 24,md:24,sm: 24 }}
                  label={"Referencia Dirección"}
                  name={'referenciesAddress'}
                  rules={[
                    {
                      required: false,
                      message: 'Se Requiere de Referencia dirección',
                    },
                  ]}
                  >
                      <Input autoComplete="off" placeholder="" allowClear  />
                  </Form.Item>
            </Col>
            </Row> 

            
            <Button  htmlType="submit" className="btnNuevoRegistrar" type="primary" onClick={()=>{ registerSales()}}>Realizar Venta</Button>
        </Form>
        </>
      </Card>
    </div>
    );
 
}
export default FormularioVenta;