import {useState,useEffect} from 'react';
import  {requestApi}  from '../../api/index';
import swal from 'sweetalert';
import { PageHeader } from 'antd';
import { Card } from 'antd';
import * as React from 'react';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../redux/actions';
import {
    Form,
    Input,
    Button,
    Select,
    Switch,
  } from 'antd';
  
import { Table } from 'antd';
import {  Space } from 'antd';
import { Upload, Modal } from 'antd';
import { PlusOutlined } from '@ant-design/icons';
const { TextArea } = Input;
const { Option } = Select;

const  FormularioUsuario=(props)=> {
    const columns = [
      {
        title: 'titulo',
        dataIndex: 'title',
        key: 'title',
      },
      {
        title: 'Descripción',
        dataIndex: 'description',
        key: 'description',
      },
      {
        title: 'Acción',
        dataIndex: 'key',
        sorter: false,
        render: data =><> <Button type="primary" onClick={()=>{editarDatos(data)}}>Editar</Button>
         <Button type="primary" danger onClick={()=>{eliminarCharacterist(data)}}>
          Eliminar
        </Button>
        </>
        ,
        width: '20%'
      },
    ];
    const [form] = Form.useForm();
    const [formCharacterist] = Form.useForm();
    const [componentSize, setComponentSize] = useState('large');
    const listRef = React.createRef();
    const [vigencia, setVigencia] = React.useState(true);
    const [dataCategory, setDataCategory] = React.useState([]);
    const [dataBrand, setDataBrand] = React.useState([]);
    const [dataCharacterist, setDataCharacterist] = React.useState([]);
    const [previewVisible, setPreviewVisible] = React.useState(null);
    const [visibleCharacterist, setVisibleCharacterist] = React.useState(null);
    const [previewImage, setPreviewImage] = React.useState(null);
    const [fileList, setFileList] = React.useState([]);
    const [previewTitle, setPreviewTitle] = React.useState(null);
    const [formRef, setformRef] = useState(null);
    const [formRefCharacterist, setFormRefCharacterist] = useState(null);
    const [listCountry, setListCountry] = React.useState([]);
    const [listPropety, setListPropety] = React.useState([]);
    const [idPropetySelect, setIdPropetySelect] = React.useState(null);
    const [selectCountry, setSelectCountry] = React.useState(-1);
    const [listGeoForm, setListGeoForm] = React.useState([]);
  
    const dispatch = useDispatch();
    
    
    useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        cargarCategory();
        cargarBrand();
        //cargarCharacterist();
        //loaderCharge();
        if (!isSubscribed) return;
      }
      
      return () => isSubscribed = false;
    },[]);
    useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        if(props.dataEdit!=undefined && props.dataEdit!=null){
          (async()=>{
            await getCountry();
            await seleccionarUbigeo(props.dataEdit);
            if(props.dataEdit!=null){
              let listImages=[];
              listImages.push({
                uid: '-1',
                name: props.dataEdit.imageName,
                status: 'done',
                url: props.dataEdit.images
                ,thumbUrl: props.dataEdit.thumbUrl
              });
              setFileList(listImages);
              form.setFieldsValue({
                nombres: props.dataEdit.name
              });
              form.setFieldsValue({
                precio: props.dataEdit.price
              });
              form.setFieldsValue({
                descripcion: props.dataEdit.descriptionProduct
              });
              setListPropety(props.dataEdit.detailt);
              cargarCategory();
              cargarBrand();
              changeCategory(props.dataEdit.idCategory);
              //cargarCharacterist();
              setVigencia(props.dataEdit.validity);
    
            
    
              
            }else{
            }
          })();
        }
        

        if (!isSubscribed) return;
      }
      
      return () => isSubscribed = false;
  },[]);

  useEffect(()=>{
    if(!(formRef==null)){
      (async()=>{
        await getCountry();
      
      })();
    }else{
    }
    return;
  },[formRef]);
  const seleccionarUbigeo=async(item)=>{
    try {
      form.setFieldsValue({
        pais: item.idCountry,
      });
      
      let tempDataSelectTemp=await getContryMeth(item.idCountry);
   
      let dataLevel1=await getFomStateGeo(item.idCountry,"");
      tempDataSelectTemp[0]["selectGeo"]=""+item.geo.split(",")[0]+"";
  
      tempDataSelectTemp[0]["listGeoAddress"]=dataLevel1;
      let formatKey={};
      formatKey[tempDataSelectTemp[0].name]=parseInt(tempDataSelectTemp[0]["selectGeo"]);
    
      form.setFieldsValue(formatKey);
      await item.geo.split(",").forEach(async (element,indexPost) => {
      
        if(indexPost>0){
          let tempFor=await  getFomStateGeo(item.idCountry,item.geo.split(",")[indexPost-1]);
          console.log("tempFor",tempFor);
          tempDataSelectTemp[indexPost]["listGeoAddress"]=tempFor;
          let formatKeyIndex={};
          formatKeyIndex[tempDataSelectTemp[indexPost].name]=parseInt(element);
          form.setFieldsValue(formatKeyIndex);
        }
  
      });
      
      setListGeoForm(tempDataSelectTemp);
    } catch (error) {
      console.log("error",error);
    }
  }
  const getContryMeth=async(rId)=>{
   
    setSelectCountry(rId);
    if(rId==null){
      setListGeoForm([]);
    }else{
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `dataGeneral/getUbigeoLevel?idContry=`+rId,
          {},
          "get"
        );
        let data =respon.state ? respon.data : [];
        if(data.length>0){
          let tempDataSelect=await getFomStateGeo(rId,"");
          
          data.forEach((element,position) => {
            
            if(position==0){
              element["selectGeo"]=null;
              let jsonDat={};
              jsonDat[element.name]=null;
              form.setFieldsValue(jsonDat);
              element["listGeoAddress"]=tempDataSelect;
            }
          });
          setListGeoForm(data);
          
          form.setFieldsValue({ sights: data });
          return data;
        }else{
          setListGeoForm([]);
          form.setFieldsValue({ sights: [] });
          return [];
        }
        /*

        
        */
      }catch(e){
        return e;
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }
    }
  }
  const getCountry=async()=>{
    try{
      let respon=await requestApi(
        `dataGeneral/getCountry`,
        {},
        "get"
      );
      let data =respon.state ? respon.data : [];
      setListCountry(data);
    }catch(e){
      return e;
    }
    return "ok;"
  }
  const getProduct=async()=>{
    try{
      let respon=await requestApi(
        `product/loaderProduct`,
        {},
        "get"
      );
      let data =respon.state ? respon.data : [];
      //setProduc(data);
    }catch(e){
      return e;
    }
    return "ok;"
  }
  const onChangeVigencia= (checked)=> {
    setVigencia(!vigencia);
    console.log(`switch to ${checked}`);
  }
  const eliminarCharacterist=(key)=>{
    swal({
      title: "Sessión",
      text: " ¿Deseas Eliminar Propiedad?",
      icon: "warning",
    // buttons: true,
      dangerMode: true,
      buttons: {
        cancel: "Cancelar",
        catch: {
          text: "Aceptar",
          value: "catch",
        }
      }
    })
    .then((willDelete) => {
      if (willDelete) {
        //key
        let filterPropetyChracterist=listPropety.filter((val)=>{
          return val.key!=key;
        });
        setListPropety(filterPropetyChracterist);
      }
    });
  };
  const editarDatos=(key)=>{
    let editPropety=listPropety.filter((val)=>{
      return val.key==key;
    });
    setIdPropetySelect(key);
    formCharacterist.setFieldsValue({
      titulo: editPropety[0].title,
      descripcion: editPropety[0].description,
      vigencia: editPropety[0].validity,
    });
    setVisibleCharacterist(true);
  }
  const registrarProducto=async()=>{
    form.validateFields()
    .then(async(values) => {
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{//form.getFieldValue("precio")
        let dataRequest={
          "id":props.dataEdit!=undefined && props.dataEdit!=null ? props.dataEdit.id : null,
          "name":form.getFieldValue("nombres"),
          "idBrand":form.getFieldValue("marca"),
          "idGeo":form.getFieldValue(listGeoForm[listGeoForm.length-1].name),
          "idCategory":form.getFieldValue("categoria"),
          "idCharacterist":form.getFieldValue("carateristica"),
          "price":form.getFieldValue("precio"),
          "description":form.getFieldValue("descripcion"),
          "validity":vigencia,
          "images":fileList,
          "listPropety":listPropety
        };
        //props.dataEdit!=undefined && props.dataEdit!=null
        let respon=null;
        if( props.dataEdit!=undefined && props.dataEdit!=null){
          respon=await requestApi(
            `product/updateProduct`,
            dataRequest,
            "post"
          );
        }else{
          respon=await requestApi(
            `product/registerProduct`,
            dataRequest,
            "post"
          );
        }
        

        if(respon.estado){
          swal({
            title: props.dataEdit!=undefined && props.dataEdit!=null ? "Actualizar Producto." :  "Registrar Producto.",
            text: respon.mensaje,
            icon:  respon.estado ? "success":"error",
            button: "aceptar"
          });
          props.atras();
        }else{
          swal({
            title:  props.dataEdit!=undefined && props.dataEdit!=null ? "Actualizar Producto." :  "Registrar Producto.",
            text: respon.mensaje,
            icon:  respon.estado ? "success":"error",
            button: "aceptar"
          });
        }
      }catch(e){
        return e;
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }
    })
    .catch((errorInfo) => {
    });
    
   
  }
  const cargarCategory = async() => {
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
      let respon=await requestApi(
        `dataGeneral/loaderCategory`,
        {},
        "get"
      );
      setDataCategory(respon.estado ? respon.data : []  );
      if(props.dataEdit!=undefined && props.dataEdit!=null){
        let dataTemp=respon.estado ? respon.data : []  ;
        let dataExistGenero=dataTemp.filter((val)=>{
          return val.id==props.dataEdit.idCategory;
       });

        
        if(dataExistGenero.length==0){
          dataTemp.push({
            id:props.dataEdit.idCategory,
            name:props.dataEdit.descriptionCategory
          });
        }
        form.setFieldsValue({
          categoria:props.dataEdit.idCategory+""
        });
      }
      

    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
   
  };
  const cargarBrand = async() => {
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
      let respon=await requestApi(
        `dataGeneral/loaderBrand`,
        {},
        "get"
      );
      setDataBrand(respon.estado ? respon.data : []  );
      if(props.dataEdit!=undefined && props.dataEdit!=null){
        let dataTemp=respon.estado ? respon.data : []  ;
        let dataExistTemp=dataTemp.filter((val)=>{
          return val.id==props.dataEdit.idBrand;
        });

        
        if(dataExistTemp.length==0){
          dataTemp.push({
            id:props.dataEdit.idBrand,
            name:props.dataEdit.nameBrand
          });
        }
        form.setFieldsValue({
          marca:props.dataEdit.idBrand+""
        });
      }
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
   
  };

  const cargarCharacterist = async(val) => {
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
      let respon=await requestApi(
        `dataGeneral/loaderCharacteristic?idCategory=${val}`,
        {},
        "get"
      );
      setDataCharacterist(respon.estado ? respon.data : []  );
      if(props.dataEdit!=undefined && props.dataEdit!=null){
        let dataTemp=respon.estado ? respon.data : []  ;
        let dataExistTemp=dataTemp.filter((val)=>{
          return val.id==props.dataEdit.idCharacterist;
        });

        
        if(dataExistTemp.length==0){
          dataTemp.push({
            id:props.dataEdit.idCharacterist,
            name:props.dataEdit.nameCharacterist
          });
        }
        form.setFieldsValue({
          carateristica:props.dataEdit.idCharacterist+""
        });

      }
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
   
  };

  const handleChange=({ fileList })=>{
    console.log("fileList",fileList);
    setFileList(fileList);
  }

  const getBase64= (file)=> {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = error => reject(error);
    });
  }
  const handleCancel = () => setPreviewVisible(false) ;

  const handlePreview = async file => {
    if (!file.url && !file.preview) {
      file.preview = await getBase64(file.originFileObj);
    }
    setPreviewImage(file.url || file.preview);
    setPreviewVisible(true);
    setPreviewTitle(file.name || file.url.substring(file.url.lastIndexOf('/') + 1));
  };
  const getFomStateGeo=async(ridcountri,ridparent)=>{
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
      let respon=await requestApi(
        `dataGeneral/getGeoParentId?idContry=${ridcountri}&idParent=${ridparent}`,
        {},
        "get"
      );
      let data =respon.state ? respon.data : [];
      return data;
    }catch(e){
      return [];
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
  }
  const selectContryMeth=async(rId)=>{
    form.setFieldsValue({
      "Pais":rId
    }); 
   
    setSelectCountry(rId);
    if(rId==null){
      setListGeoForm([]);
    }else{
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `dataGeneral/getUbigeoLevel?idContry=`+rId,
          {},
          "get"
        );
        let data =respon.state ? respon.data : [];
        if(data.length>0){
          let tempDataSelect=await getFomStateGeo(rId,"");
          
          data.forEach((element,position) => {
            
            if(position==0){
              element["listGeoAddress"]=tempDataSelect;
            }
            let jsonDat={};
            jsonDat[element.name]=null;
          });
          setListGeoForm(data);
          return data;
        }else{
          setListGeoForm([]);
          return [];
        }
      }catch(e){
        return e;
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }
    }
  }
  const selectGeoIndex=async(index,item)=>{
    try{
    let templistGeoForm=[...listGeoForm];
    if(templistGeoForm.length>=(index+1)){
      let tempDataSelect=[];
      if(item!=null){
        tempDataSelect=await getFomStateGeo(selectCountry,item);
      }
      templistGeoForm.forEach((element,position) => {
        if(position==index){
          element["selectGeo"]=item;
        }
        if(position==index+1){
          //if(idAddressSelect==null){
            let tempFor={};
            tempFor[element.name]=null;
            form.setFieldsValue(tempFor);
            element["selectGeo"]=null;
            
         // }
          element["listGeoAddress"]=tempDataSelect;
        }
        if(position>=index+1){
          let tempFor={};
          tempFor[element.name]=null;
          form.setFieldsValue(tempFor);
          element["selectGeo"]=null;
        }
        
      });
      setListGeoForm(templistGeoForm);
    }
    

    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
    return "ok;"
  }
  const anadirItemCharacterist=()=>{
    if(idPropetySelect==null){
     formCharacterist.validateFields()
      .then(async(values) => {
        let copiList=JSON.parse(JSON.stringify(listPropety));
        copiList.push({
          key:listPropety.length,
          title:formCharacterist.getFieldValue("titulo"),
          description:formCharacterist.getFieldValue("descripcion")
        });
        setListPropety(copiList);
        setVisibleCharacterist(false);
      })
      .catch((errorInfo) => {
        console.log("errorInfo",errorInfo);
      });
     
    }else{
      formCharacterist.validateFields()
      .then(async(values) => {
        let copiList=JSON.parse(JSON.stringify(listPropety));
        copiList[idPropetySelect]={
          key:idPropetySelect,
          title:formCharacterist.getFieldValue("titulo"),
          description:formCharacterist.getFieldValue("descripcion")
        }
        setListPropety(copiList);
        setVisibleCharacterist(false);
      });
      
    }
    
    
  }
  const changeCategory=(val)=>{
    if(val==null){
      setDataCharacterist([]);
      form.setFieldsValue({
        carateristica:undefined
      });
      return false;
    }
    cargarCharacterist(val);
  }
  //
  const uploadButton = (
    <div>
      <PlusOutlined />
      <div style={{ marginTop: 8 }}>Añadir Foto</div>
    </div>
  );
  return (
    <div ref={listRef}   className="contentPago">
        <Modal  visible={visibleCharacterist} title={idPropetySelect==null ?  
          "Añadir Propiedades" : "Editar Propiedades"+idPropetySelect} 
          onOk={anadirItemCharacterist} onCancel={()=>{setVisibleCharacterist(!visibleCharacterist)}}>
        <Form
            labelCol={{
            span: 4,
            }}
            wrapperCol={{
            span: 14,
            }}
            layout="vertical"
            initialValues={{
            size: componentSize,
            }}
            size={componentSize}
            form={formCharacterist}
            ref={(e) => {
              setFormRefCharacterist(e );
             }}
        >
           <Form.Item 
              name={['titulo']}
              label="título"
              max="500"
              rules={[{ required: true, message: 'Se Requiere de título.' }]}
              labelCol={{ lg: 24,md:14,sm: 24 }}
              wrapperCol={{ lg: 24,md:14,sm: 24 }}
              >
                <Input maxLength="200" autoComplete="off" />
            </Form.Item>
            <Form.Item 
              name={[ 'descripcion']}
              label="Descripción"
              max="500"
              rules={[{ required: true, message: 'Se Requiere de Descripción.' }]}
              labelCol={{ lg: 24,md:14,sm: 24 }}
              wrapperCol={{ lg: 24,md:14,sm: 24 }}
              >
                <TextArea maxLength="500" autoComplete="off" />
            </Form.Item>
        </Form>
        </Modal>

      <Modal
          visible={previewVisible}
          title={previewTitle}
          footer={null}
          onCancel={handleCancel}
        >
          <img alt="example" style={{ width: '100%' }} src={previewImage} />
      </Modal>
      <Card title={<PageHeader
        className="site-page-header"
        onBack={() => props.atras()}
        title={props.dataEdit!=undefined && props.dataEdit!=null ? "Editar Producto" : "Nuevo Producto"}
        subTitle={null}
      />} bordered={false} style={{ width: "100%" }}>
        <>
        <Form
            labelCol={{
            span: 4,
            }}
            wrapperCol={{
            span: 14,
            }}
            layout="vertical"
            initialValues={{
            size: componentSize,
            }}
            size={componentSize}
            form={form}
            ref={(e) => {
              setformRef(e );
             }}
        >
           <Space direction="vertical" style={{ width: '100%' }} size="large">
              <Upload
              beforeUpload={() => false}
              listType="picture-card"
              fileList={fileList}
              onPreview={handlePreview}
              onChange={handleChange}
            >
              {fileList.length >= 5 ? null : uploadButton}
            </Upload>
            </Space>
            <Form.Item 
              name={[ 'nombres']}
              label="Nombres"
              max="500"
              rules={[{ required: true, message: 'Se Requiere de Nombres.' }]}
              wrapperCol={{ lg: 14,md:14,sm: 24 }}
              >
                <Input />
            </Form.Item>
            <Form.Item 
              name={['precio']}
              label="Precio"
              max="500"
              rules={[{ required: true, message: 'Se Requiere de Precio.' }]}
              wrapperCol={{ lg: 14,md:14,sm: 24 }}>
                <Input />
            </Form.Item>

            <Form.Item 
              name={[ 'categoria']} 
              label="Categoria"
              rules={[{ required: true, message: 'Se Requiere de Categoria.' }]}
              wrapperCol={{ lg: 14,md:14,sm: 24 }}>
                 <Select allowClear placeholder="Seleccione Categoria"  
                 style={{ width: "100%" }} onChange={(val)=>{changeCategory(val)}}>
                {dataCategory.map(item => (
                  <Option id={item.id} key={item.id}>{item.name}</Option>
                ))}
              </Select>
            </Form.Item>
            <Form.Item 
              name={[ 'marca']} 
              label="Marca"
              rules={[{ required: true, message: 'Se Requiere de Marca.' }]}
              wrapperCol={{ lg: 14,md:14,sm: 24 }}>
                 <Select allowClear placeholder="Seleccione Marca"  style={{ width: "100%" }} onChange={()=>{}}>
                {dataBrand.map(item => (
                  <Option id={item.id} key={item.id}>{item.name}</Option>
                ))}
              </Select>
            </Form.Item>
            <Form.Item 
              name={[ 'carateristica']} 
              label="Caracteristica"
              rules={[{ required: true, message: 'Se Requiere de Caracteristica.' }]}
              wrapperCol={{ lg: 14,md:14,sm: 24 }}>
                 <Select allowClear placeholder="Seleccione Caracteristica"  style={{ width: "100%" }}>
                {dataCharacterist.map(item => (
                  <Option id={item.id} key={item.id}>{item.name}</Option>
                ))}
              </Select>
            </Form.Item>
            <Form.Item
              wrapperCol={{ lg: 14,md:14,sm: 24 }}
              label="Pais"
              name={'pais'}
              rules={[
                {
                  required: true,
                  message: 'Seleccione Pais',
                },
              ]}
              >
                <Select   
                  className="formControlform formControl"
                  placeholder="Seleccione Pais"  
                  bordered={true}  
                  onChange={(e)=>{selectContryMeth(e) } }>
                  <Option value={null} key={-1} >{'Seleccione Pais'}</Option>
                  {listCountry.map((item, i) => (
                    (<Option value={item.id} key={i} >{item.name}</Option>)

                    )  
                )}
                  
                </Select>
              </Form.Item>
              {listGeoForm.map((item, i) => (
                    (
                      <Form.Item
                      labelCol={{ sm: 24 }}
                      key={item.name}
                      label={item.name}
                      name={item.name}
                      rules={[
                        {
                          required: true,
                          message: 'seleccione '+item.name,
                        },
                      ]}
                      wrapperCol={{ lg: 14,md:14,sm: 24 }}
                    >
                      <Select   
                        placeholder={"seleccione "+item.name}  
                        bordered={true}  
                        name={item.name}
                        label={item.name}
                        onChange={(e)=>{selectGeoIndex(i,e)} }>
                        <Option value={null} key={"-"+item.name}>{'seleccione '+item.name}</Option>
                          {
                            (item.listGeoAddress==undefined ? [] : item.listGeoAddress).map((itemAddress, iAddress) => (
                              (<Option value={itemAddress.id} key={itemAddress.id+"-"+i+"-"+iAddress}>{itemAddress.name}</Option>)
                          
                            )  
                            )
                          }
                      </Select>
                    </Form.Item>
                    )

                    )  
                )}
            
            
                <Button  className="btnNuevoRegistrar" type="primary" onClick={()=>{ formCharacterist.resetFields(); setVisibleCharacterist(true);setIdPropetySelect(null);}}>Añadir Propiedades</Button>
                <Table  dataSource={listPropety} columns={columns} />
                <Form.Item 
                  name={['descripcion']}
                  label="Descripción"
                  max="500"
                  rules={[{ required: false, message: 'Se Requiere de Descripción.' }]}
                  wrapperCol={{ lg: 14,md:14,sm: 24 }}>
                  <TextArea placeholder="" allowClear  />
                </Form.Item>
                <Form.Item 
                  name={[ 'vigencia']} 
                  label="Vigencia"
                  wrapperCol={{ lg: 14,md:14,sm: 24 }}
                >
                    <Switch checked={vigencia} onChange={onChangeVigencia} />
                </Form.Item>
                <Button  htmlType="submit" className="btnNuevoRegistrar" type="primary" onClick={()=>{ registrarProducto()}}>Guardar</Button>
        </Form>
        </>
      </Card>
    </div>
    );
 
}
export default FormularioUsuario;