import * as React from 'react';
import {useState,useEffect} from 'react';
import { Table,Form, Input,Pagination } from 'antd';
import { DatePicker } from 'antd';
import {  Button ,Select} from 'antd';
import moment from 'moment';
import {  Col, Row } from 'antd';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../redux/actions';
import { List, Typography } from 'antd';

import  {requestApi,hostUrl}  from '../../api/indexTesis';
import { Image } from 'antd';
const { TextArea } = Input;
const  GestionScreem=()=> {
  const { Option } = Select;
 
  const [form] = Form.useForm();
  const dispatch = useDispatch();
  const listRef = React.createRef();
  const [obtenerPantallazo, setObtenerPantallazo] = useState(false);
  const [imagenScreem, setImagenScreem] = useState(null);
  const [listadoMac, setListadoMac] = React.useState([]);
  
  const [keyApp,setKeyApp] = useState("");

 const obtenerPantalla=function (event) {
    streamenData();
};
const streamenData=async ()=> {
  dispatch(loaderSpiner({
    loaderSpiner:true
  }));
  let stringkeyApp="";
  if(keyApp=="" && keyApp!="0" && keyApp!=null){
    stringkeyApp="";
  }else{
    console.log(keyApp.split(":"));
    stringkeyApp=keyApp.split(":").join("");
  }
  console.log(stringkeyApp);
  try{
    let response=await requestApi(
      "streamenData?id="+stringkeyApp,
      {},
      "get"
    );
     console.log("response.data",response);
    setImagenScreem(hostUrl+response);
    setObtenerPantallazo(true); 
  }catch(e){
    setObtenerPantallazo(false);
    return e;
  }finally{
    
    dispatch(loaderSpiner({
      loaderSpiner:false
    }));
  }
  /*
axios.get(this.host+"streamenData?id="+this.keyApp)
.then( (response)=> {
    this.image=this.host+response.data;
})
.catch(function (error) {
    // handle error
    console.log(error);
})
.then(function () {
    // always executed
});

*/

}
const refrescarPagina=()=>{

}
const obtenerMac=async()=>{
  dispatch(loaderSpiner({
    loaderSpiner:true
  }));
  try{
      //(this.host+"listadoHistorial?id="+this.keyApp+"&navegador="+this.selected)
    //selectNavegador
    let response=await requestApi(
      "dataAplicativosMack",
      {},
      "get"
    );
    setListadoMac(response.data);
    //console.log("respon.data",respon.data);
  }catch(e){
    return e;
  }finally{
    dispatch(loaderSpiner({
      loaderSpiner:false
    }));
  }
}
useEffect(()=>{
  let isSubscribed = true
  if(listRef?.current!=null){
    obtenerMac();
    if (!isSubscribed) return;
    (async()=>{
    })();
  }
  
  return () => isSubscribed = false;
},[]);


 

  return (
    <div ref={listRef}   className="contentPago">
        <>
         <Row>

          <Row>
            <Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 24 }} >
                <label>Maquina</label>
                <Select onChange={(e)=>{setKeyApp(e) }}
                    value={keyApp}        
                    style={{ width: "100%" }} >
                      <Option id={''} key={''}>{'Seleccione'}</Option>
                    {listadoMac.map(item => (
                      <Option id={item.r_mac} key={item.r_mac}>{item.r_mac}</Option>
                  ))}
                    
                </Select>            
            </Col>
            </Row>
            <Col span={24}>
              <center>
                <Button  type="primary"   onClick={()=>{obtenerPantalla()}} >Obtener Pantallazos</Button>
              </center> 

            </Col>
            <Col span={24}>
              <center style={{marginTop:50}}>
                {
                  obtenerPantallazo ?
                  <Image
                  width={400}
                  src={imagenScreem}
  
                /> : 
                <Image
                  width={400}
                  src="error"
                  fallback="data:image/png;base64,"

                />
                }

              </center>

            </Col>
            
         </Row>


      
        </> 
        
    </div>
    );
 
}
export default GestionScreem;