import * as React from 'react';
import {useState,useEffect} from 'react';
import { Table } from 'antd';
import { Select } from 'antd';
//import reqwest from 'reqwest';
import { Card } from 'antd';
//import { Form } from 'antd';
import {  Button } from 'antd';
//import { Layout } from 'antd';
import { Pagination } from 'antd';
import { Menu } from 'antd';
//import swal from 'sweetalert';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../redux/actions';
//import { DatePicker  } from 'antd';
import  {requestApi}  from '../../api/index';
import FormularioUsuarioComponent from './FormularioProducto';

import { Input } from 'antd';
const { Option } = Select;



const  GestionProducto=()=> {
  const menu = (
    <Menu>
      <Menu.Item key="0">
        <a href="https://www.antgroup.com">Excel</a>
      </Menu.Item>
      <Menu.Item key="1">
        <a href="https://www.aliyun.com">Pdf</a>
      </Menu.Item>
    </Menu>
  );


  const columns = [
    {
      title: 'Acción',
      dataIndex: 'id',
      sorter: false,
      render: data => <Button type="primary" onClick={()=>{verProducto(data)}}>Editar</Button>
      ,
      width: '20%'
    },
    {
      title: 'Nombre',
      dataIndex: 'name',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Stock',
      dataIndex: 'stock',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Precio',
      dataIndex: 'price',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Categoria',
      dataIndex: 'descriptionCategory',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Caracteristica',
      dataIndex: 'nameCharacterist',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Ubicación Geografica',
      dataIndex: 'descriptionGeoGraphiclocation',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    //r_descriptiongeographiclocation
    {
      title: 'Marca',
      dataIndex: 'nameBrand',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    
    {
      title: 'Vigencia',
      dataIndex: 'validityText',
      sorter: false,
      render: data => `${data}`,
      
      width: '20%'
    },
  ];
  const [loading,setLoading] = useState(false);
  const dispatch = useDispatch();
  const [listadoDataUsuario,setListadoDataUsuario] = useState([]);
  const listRef = React.createRef();
  const [tipoVista, setTipoVista] = useState(1);
  const [contarTotal, setContarTotal] = useState(0);
  const [paginaActual, setPaginaActual] = useState(1);
  const [cantidadContenido, setCantidadContenido] = useState(10);
  const [dataSeleccionada, setDataSeleccionado] = useState(null);
  const [tipoBusqueda, setTipoBusqueda] = useState("nameProduct");
  const [inputSearch, setInputSearch] = useState("");
  //const [data,setData] = useState([]);
  useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        if (!isSubscribed) return;
        (async()=>{
          await listarUsuario(paginaActual-1,cantidadContenido);
        })();
      }
      
      return () => isSubscribed = false;
  },[]);
  const verProducto=async(reqId)=>{
    
    //product/seeProduct?id=5
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
      let respon=await requestApi(
        `product/seeProduct?id=`+reqId,
        {},
        "get"
      );
    
      setDataSeleccionado(respon.estado ? respon.data :null);
      console.log("respon",(respon.estado ? respon.data :null));
      setTipoVista(3);
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
  }
  const refrescarListado=()=>{
    listarUsuario(paginaActual-1,cantidadContenido);
  }
  useEffect(()=>{
    let isSubscribed = true
    if(listRef?.current!=null){
      if (!isSubscribed) return;
      (async()=>{
        setLoading(true);
        await listarUsuario(paginaActual-1,cantidadContenido);
        setLoading(false);
      })();
    }
    
    return () => isSubscribed = false;
},[tipoBusqueda,inputSearch]);
  const listarUsuario = async(reqpaginaActual,reqcantidadContenido) => {
   
    try{
      //product/listProductsAdmin?paginateActive=0&countContent=10&typeSearch=nameProduct&value=
      let respon=await requestApi(
        `product/listProductsAdmin?paginateActive=${reqpaginaActual}&countContent=${reqcantidadContenido}&typeSearch=${tipoBusqueda}&value=${inputSearch}`,
        {},
        "get"
      );
      setListadoDataUsuario(respon.estado ? respon.data : []  );
      setContarTotal(respon.estado ? respon.countData : 0);
    }catch(e){
      return e;
    }finally{
    }
   
  };

  const changePagination=async(val,sizePagination)=>{
    setPaginaActual(val);
    setCantidadContenido(sizePagination);
    //countData
    await listarUsuario(val-1,sizePagination);
 }
  const nuevo=(e)=>{
    setTipoVista(2);
  }
  return (
    <div ref={listRef}   className="contentPago">
      {
        tipoVista==1 ?
          <div>
            <Card title="Gestión de Producto" bordered={false} style={{ width: "100%" }}>
                <div className="contentBtnNuevo" style={{position:"relative"}}>
                  <Button type="primary"  className="btnNuevo" onClick={nuevo} >Nuevo</Button>
                 </div><br/>
                 <Input.Group style={{    display: "flex",
                    float: "right",
                    margin: 0,
                    padding: 0}} compact>
                  <div className="contentOption"> 
                    <Select className="selectcontentOption" defaultValue={tipoBusqueda} value={tipoBusqueda} onChange={(e)=>{ setTipoBusqueda(e);}}>
                        <Option value="nameProduct">Nombre</Option>
                        <Option value="category">Categoria</Option>
                        <Option value="CharacteristName">Caracteristica</Option>
                        <Option value="GeographicLocationName">Ubicación Geográfica</Option>
                    </Select>

                
                    <Input style={{ width: '60%' }} defaultValue={inputSearch}  onChange={(e)=>{ setInputSearch(e.target.value);}}/>
                  </div>
                </Input.Group>
            </Card>
          
            <Table
              className="tableGestionUsuario"
              scroll={{ x: 'calc(700px + 50%)',  y: 1500 }}
              columns={columns}
              dataSource={listadoDataUsuario}
              pagination={false}
              rowKey={record => record.id}
              loading={loading}
            />
          
           <Pagination  className="paginationTable paginationGestionUsuario" current={paginaActual} defaultCurrent={paginaActual} total={contarTotal}  onChange={(val,size)=>{changePagination(val,size)}} />
            </div> :
            null
      }

      {
        tipoVista==2 ? <FormularioUsuarioComponent atras={()=>{refrescarListado();setTipoVista(1)}}/> : null
      }
      {
        tipoVista==3 ? <FormularioUsuarioComponent dataEdit={dataSeleccionada} atras={()=>{setTipoVista(1)}} refrescarListado={()=>{refrescarListado()}}/> : null
      }
    </div>
    );
 
}
export default GestionProducto;