import * as React from 'react';
import {useState,useEffect} from 'react';
import { Table } from 'antd';
import { Select } from 'antd';
import { Card } from 'antd';
import {  Button } from 'antd';
import { Pagination } from 'antd';
import { Menu } from 'antd';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../../redux/actions';
import  {requestApi}  from '../../../api/index';
import FormularioCargoComponent from './FormularioCargo';
import FormularioEditarCargoComponent from './FormularioEditarCargo';
import { Input } from 'antd';
//import { Col, Row,  InputNumber, DatePicker, AutoComplete, Cascader } from 'antd';

const { Option } = Select;



const  GestionCargo=()=> {
  const menu = (
    <Menu>
      <Menu.Item key="0">
        <a href="https://www.antgroup.com">Excel</a>
      </Menu.Item>
      <Menu.Item key="1">
        <a href="https://www.aliyun.com">Pdf</a>
      </Menu.Item>
    </Menu>
  );
  const columns = [
    {
      title: 'Acción',
      dataIndex: 'id',
      sorter: false,
      render: data => <Button type="primary" onClick={()=>{editarDatos(data)}}>Editar</Button>
      ,
      width: '20%'
    },
    {
      title: 'nombre',
      dataIndex: 'name',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'vigencia',
      dataIndex: 'validityText',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    }
  ];
  const [loading,setLoading] = useState(false);
  const dispatch = useDispatch();
  const [listadoDataCargo,setListadoDataCargo] = useState([]);
  const listRef = React.createRef();
  const [tipoVista, setTipoVista] = useState(1);
  const [cargoSeleccionado, setCargoSeleccionado] = useState(null);
  const [inputSearch, setInputSearch] = useState("");
  const [tipoBusqueda, setTipoBusqueda] = useState("Nombre");
  //const [data,setData] = useState([]);
  useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        if (!isSubscribed) return;
        (async()=>{
          await listarCargo();
        })();
      }
      
      return () => isSubscribed = false;
  },[]);
  const editarDatos=async(reqId)=>{
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
      let respon=await requestApi(
        `charge/seeCharge?id=`+reqId,
        {},
        "get"
      );
      setCargoSeleccionado(respon.estado ? respon.data :null);
      setTipoVista(3);
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
  }

  const refrescarListado=async()=>{
    await listarCargo();
  }
  const listarCargo = async() => {
    setLoading(true);
    try{
      let respon=await requestApi(
        `charge/listCharge?tipoBusqueda=${tipoBusqueda}&valorBusqueda=${inputSearch}`,
        {},
        "get"
      );
      setListadoDataCargo(respon.estado ? respon.data : []  );
    }catch(e){
      return e;
    }finally{
      setLoading(false);
    }
   
  };
 
  const nuevo=(e)=>{
    setTipoVista(2);
  }
  useEffect(()=>{
    let isSubscribed = true
    if(listRef?.current!=null){
      if (!isSubscribed) return;
      (async()=>{
        await listarCargo();
      })();
    }
    
    return () => isSubscribed = false;
},[tipoBusqueda,inputSearch]);
  return (
    <div ref={listRef}   className="contentPago">
      {
        tipoVista==1 ?
          <div>
            <Card title="Gestión de Cargo" bordered={false} style={{ width: "100%" }}>
                <div className="contentBtnNuevo" style={{position:"relative"}}>
                  <Button type="primary"  className="btnNuevo" onClick={nuevo} >Nuevo</Button>
                 </div><br/>
                 <Input.Group style={{    display: "flex",
                    float: "right",
                    margin: 0,
                    padding: 0}} compact>
                  <div className="contentOption"> 
                    <Select defaultValue={tipoBusqueda} value={tipoBusqueda} onChange={(e)=>{ setTipoBusqueda(e);}}>
                        <Option value="Nombre">Nombre</Option>
                    </Select>
                    <Input style={{ width: '60%' }} defaultValue={inputSearch}  onChange={(e)=>{ setInputSearch(e.target.value);}}/>
                  </div>
                </Input.Group>
            </Card>
          
            <Table
              className="contentTable"
              scroll={{ x: 'calc(700px + 50%)',  y: 1500 }}
              columns={columns}
              dataSource={listadoDataCargo}
              pagination={true}
              rowKey={record => record.key}
              loading={loading}
            />
                     
            </div> :
            null
      }

      {
        tipoVista==2 ? <FormularioCargoComponent refrescarListado={()=>{setTipoVista(1);refrescarListado()}}/> : null
      }
      {
        tipoVista==3 ? <FormularioEditarCargoComponent dataEdit={cargoSeleccionado} atras={()=>{setTipoVista(1)}} refrescarListado={()=>{refrescarListado()}}/> : null
      }
    </div>
    );
 
}
export default GestionCargo;