import * as React from 'react';
import {useState,useEffect} from 'react';
import { Table } from 'antd';
import { Select } from 'antd';
import { Card } from 'antd';
import {  Button } from 'antd';
import { Pagination } from 'antd';
import { Menu } from 'antd';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../redux/actions';
import  {requestApi}  from '../../api/index';
import FormularioUsuarioComponent from './FormularioCiudadano';
import FormularioEditarCiudadano from './FormularioEditarCiudadano';
import SincronizacionDatosComponent from './SincronizacionDatos';
import SincronizacionDatosDirecciones  from './SincronizacionDatosDirecciones';
import { Tabs } from 'antd';
import { Input } from 'antd';
const { Option } = Select;

const { TabPane } = Tabs;
const  GestionUsuarios=()=> {
  const menu = (
    <Menu>
      <Menu.Item key="0">
        <a href="https://www.antgroup.com">Excel</a>
      </Menu.Item>
      <Menu.Item key="1">
        <a href="https://www.aliyun.com">Pdf</a>
      </Menu.Item>
    </Menu>
  );
  const columns = [
    {
      title: 'Acción',
      dataIndex: 'id',
      sorter: false,
      render: data => <div style={{display:"flex"}}><Button className="btnAccion" type="primary" onClick={()=>{verRegistro(data)}}>Editar</Button></div>
      ,
      width: '40%',
      key: 'accion-01',
    },
    {
      title: 'Dni',
      dataIndex: 'numeroDocumento',
      sorter: false,
      render: data => `${data}`,
      width: '20%',
      key: 'dni-01',
    },
    {
      title: 'Nombres',
      dataIndex: 'nombres',
      sorter: false,
      width: '20%',
      key: 'nombres-01',
      render: data => `${data}`,
    },
    {
      title: 'Apelido Paterno',
      dataIndex: 'apellidoPaterno',
      sorter: false,
      width: '20%',
      key: 'apellidoPaterno-01',
      render: data => `${data}`
    },
    {
      title: 'Apelido Materno',
      dataIndex: 'apellidoMaterno',
      sorter: false,
      key: 'apellidoMaterno-01',
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Genero',
      dataIndex: 'descripcionGenero',
      sorter: false,
      key: 'descripcionGenero-01',
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Fecha Nacimiento',
      dataIndex: 'fechaNacimiento',
      key: 'fechaNacimiento',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Vigencia',
      dataIndex: 'vigenciaText',
      key: 'vigenciaText',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
  ];
  const [loading,setLoading] = useState(false);
  const dispatch = useDispatch();
  const [listadoDataCiudadano,setListadoDataCiudadano] = useState([]);
  const listRef = React.createRef();
  const [tipoVista, setTipoVista] = useState(1);
  const [contarTotal, setContarTotal] = useState(0);
  const [paginaActual, setPaginaActual] = useState(1);
  const [cantidadContenido, setCantidadContenido] = useState(10);
  const [ciudadanoSeleccionado, setCiudadanoSeleccionado] = useState(null);
  const [inputSearch, setInputSearch] = useState("");
  const [tipoBusqueda, setTipoBusqueda] = useState("dni");
  //const [data,setData] = useState([]);
  useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        if (!isSubscribed) return;
        (async()=>{
          await contarCiudadano();
          await listarUsuario(paginaActual-1,cantidadContenido);
        })();
      }
      
      return () => isSubscribed = false;
  },[]);

  useEffect(()=>{
    let isSubscribed = true
    if(listRef?.current!=null){
      if (!isSubscribed) return;
      (async()=>{
        setLoading(true);
        await contarCiudadano();
        await listarUsuario(paginaActual-1,cantidadContenido);
        setLoading(false);
      })();
    }
    
    return () => isSubscribed = false;
},[inputSearch,tipoBusqueda]);
  const verRegistro=async(reqId)=>{
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
      let respon=await requestApi(
        `datosUsuarioCiudadano/verUsuarioCiudadano?id=`+reqId,
        {},
        "get"
      );
      setCiudadanoSeleccionado(respon.estado ? respon.data :null);
      setTipoVista(3);
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
  }
  
  const refrescarListado=async()=>{
    await contarCiudadano();
    await listarUsuario(paginaActual-1,cantidadContenido);
  }
  const listarUsuario = async(reqpaginaActual,reqcantidadContenido) => {
   
    try{
      let respon=await requestApi(
        `datosUsuarioCiudadano/listarUsuarioCiudadano?paginaactual=${reqpaginaActual}&cantidadcontenido=${reqcantidadContenido}&tipoBusqueda=${tipoBusqueda}&valorBusqueda=${inputSearch}`,
        {},
        "get"
      );
      setListadoDataCiudadano(respon.estado ? respon.data : []  );
    }catch(e){
      return e;
    }finally{
     
    }
   
  };
  const contarCiudadano = async() => {
   
    try{
      let respon=await requestApi(
        `datosUsuarioCiudadano/contarUsuarioCiudadano?tipoBusqueda=${tipoBusqueda}&valorBusqueda=${inputSearch}`,
        {},
        "get"
      );
      console.log("respon",respon);
      setContarTotal(respon.estado ? respon.cantidad : 0  );
    }catch(e){
      return e;
    }finally{
     
    }
   
  };
  const changePagination=async(val,sizePagination)=>{
    setPaginaActual(val);
    setCantidadContenido(sizePagination);
    await contarCiudadano();
    await listarUsuario(val-1,sizePagination);
 }
  
  const nuevo=(e)=>{
    setTipoVista(2);
  }
  return (<div>
    <Tabs defaultActiveKey="1" onChange={(e)=>{console.log("eeeeuuu",e);
        if(e=="1"){
          refrescarListado(); 

        }
      
      }} >
        <TabPane tab="Listado y Nuevo" key="1">
          <div ref={listRef}   className="contentCiudadano">
            {
              tipoVista==1 ?
                <div>
                  <Card title="Gestión de Ciudadano" bordered={false} style={{ width: "100%" }}>
                      <div className="contentBtnNuevo" style={{position:"relative"}}>
                        <Button type="primary"  className="btnNuevo" onClick={nuevo} >Nuevo</Button>
                      </div>
                      <Input.Group style={{    display: "flex",
                    float: "right",
                    margin: 0,
                    padding: 0}} compact>
                  <div className="contentOption" > 
                    <Select style={{minWidth:"180px",background:"red"}} defaultValue={tipoBusqueda} value={tipoBusqueda} onChange={(e)=>{ setTipoBusqueda(e);}}>
                        <Option value="dni">dni</Option>
                        <Option value="Nombres">Nombres</Option>
                        <Option value="apellidoPaterno">Apellido Paterno</Option>
                        <Option value="apellidoMaterno">Apellido Materno</Option>
                    </Select>
                    <Input style={{ width: '60%' }} defaultValue={inputSearch}  onChange={(e)=>{ setInputSearch(e.target.value);}}/>
                  </div>
                </Input.Group>
                  </Card>
                
                  <Table
                    className="contentTable"
                    scroll={{ x: 'calc(700px + 50%)',  y: 1500 }}
                    columns={columns}
                    dataSource={listadoDataCiudadano}
                    rowKey={(record) =>(record.numeroDocumento)}
                    pagination={false}
                    loading={loading}
                  />
                <Pagination  className="paginationTable" current={paginaActual} defaultCurrent={paginaActual} total={contarTotal}  onChange={(val,size)=>{changePagination(val,size)}} />
                          
                  </div> :
                null
            }
            {
              tipoVista==2 ? <FormularioUsuarioComponent atras={()=>{setTipoVista(1)}}/> : null
            }
            {
              tipoVista==3 ? <FormularioEditarCiudadano dataEdit={ciudadanoSeleccionado} atras={()=>{setTipoVista(1)}} refrescarListado={()=>{refrescarListado()}}/> : null
            }
          </div>
        </TabPane>
        <TabPane tab="Importación Masiva Ciudadanos" key="impt-2">
          <SincronizacionDatosComponent/>
        </TabPane>
        <TabPane tab="Importación Masiva Predios" key="impt-3">
          <SincronizacionDatosDirecciones/>
        </TabPane>
    </Tabs>

    </div>
    );
 
}
export default GestionUsuarios;