import * as React from 'react';
import {useState,useEffect} from 'react';
import { Table,Form, Input,Pagination } from 'antd';
import { DatePicker } from 'antd';
import {  Button ,Select} from 'antd';
import moment from 'moment';
import {  Col, Row } from 'antd';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../redux/actions';

import  {requestApi}  from '../../api/index';
import AccionarOrdenComponent from './AccionarOrden';
const  GestionOrden=()=> {
  const { Option } = Select;
  const { RangePicker } = DatePicker;
  const columnsOrden = [
    {
      title: 'Fecha Registro',
      dataIndex: 'registrationDate',
      key: 'registrationDate',
      width: "40%",
    },
    {
      title: 'Fecha Envio',
      dataIndex: 'dateSend',
      key: 'dateSend',
      width: "20%",
    },
    {
      title: 'Tipo Entrega',
      dataIndex: 'sending',
      key: 'sending', width: "20%",
    },
    //sending
    {
      title: 'Email',
      dataIndex: 'email',
      key: 'email',
      width: "20%",
    },
    {
      title: 'Numero Orden',
      dataIndex: 'orderNumber',
      key: 'orderNumber',
      width: "20%",
    },
    {
      title: 'Estado Orden',
      dataIndex: 'stateOrderDescription',
      key: 'stateOrderDescription',
      width: "20%",
    },
    {
      title: 'Estado Pago',
      dataIndex: 'statusPayment',
      key: 'statusPayment', width: "20%",
    },
    {
      title: 'Numero Documento',
      dataIndex: 'documentIdentity',
      key: 'documentIdentity', width: "20%",
    },
    {
      title: 'Cliente',
      dataIndex: 'nameFull',
      key: 'nameFull', width: "20%",
    },
    {
      title: 'Numero Celular',
      dataIndex: 'celPhone',
      key: 'celPhone', width: "20%",
    },
    {
      title: 'Numero Celular Representante Legal',
      dataIndex: 'phoneCorporation',
      key: 'phoneCorporation', width: "20%",
     
    },
    {
      title: 'Nombre Completo Representante Legal',
      dataIndex: 'numberDocumentCorportation',
      key: 'numberDocumentCorportation',
      sorter: false,
       width: "20%",
    },
    {
      title: 'Acción',
      dataIndex: 'id',
      sorter: false,
      render: data =><> 
       <Button  type="primary"  onClick={()=>{verOrden(data)}}>
        Editar
      </Button>
      </>
      ,
      width: '20%'
    },
  ];
  const [form] = Form.useForm();
  const [loading,setLoading] = useState(false);
  const dispatch = useDispatch();
  const [listadoDataOrden,setListadoDataOrden] = useState([]);
  const listRef = React.createRef();
  const [contarTotal, setContarTotal] = useState(1);
  const [paginaActual, setPaginaActual] = useState(1);
  const [cantidadContenido, setCantidadContenido] = useState(10);
  const [dataSeleccionada, setDataSeleccionado] = useState(null);
  const [fechaRegistroFiltro, setFechaRegistroFiltro] = useState(null);
  //const [data,setData] = useState([]);
  const atras=()=>{
    setDataSeleccionado(null);
  }
  const verOrden=async(reqId)=>{
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
      let respon=await requestApi(
        `order/seeOrder?id=`+reqId,
        {},
        "get"
      );
    
      setDataSeleccionado(respon.state ? respon.data[0] :null);
      console.log("respon.data",respon.data);
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
}
useEffect(()=>{
  let isSubscribed = true
  if(listRef?.current!=null){
    if (!isSubscribed) return;
    (async()=>{
      await listarOrden(paginaActual-1,cantidadContenido);
    })();
  }
  
  return () => isSubscribed = false;
},[]);
  const listarOrden = async(reqpaginaActual,reqcantidadContenido) => {
    setLoading(true);
    try{
      let reqFechaInicio=fechaRegistroFiltro==null || fechaRegistroFiltro=="" ? "" : moment(fechaRegistroFiltro[0]).format('DD-MM-YYYY') ;
      let reqFechaFin=fechaRegistroFiltro==null || fechaRegistroFiltro=="" ? "" : moment(fechaRegistroFiltro[1]).format('DD-MM-YYYY') ;
      let reqCorreo=form.getFieldValue("correo") ? form.getFieldValue("correo") : "";
      let documentoIdentidad=form.getFieldValue("documentoIdentidad") ? form.getFieldValue("documentoIdentidad") : "";
      let tipoEntrega=form.getFieldValue("tipoEntrega") ? form.getFieldValue("tipoEntrega") : "";
      let codigoOrden=form.getFieldValue("codigoOrden") ? form.getFieldValue("codigoOrden") : "";

      //codigoOrden
      let respon=await requestApi(
        `order/listOrder?reqOrderNumber=${codigoOrden}&paginaActual=${reqpaginaActual}&cantidadContenido=${reqcantidadContenido}&reqDate=${reqFechaInicio}&reqDateFin=${reqFechaFin}&email=${reqCorreo}&documentIdenty=${documentoIdentidad}&sending=${tipoEntrega}`,
        {},
        "get"
      );
      setListadoDataOrden(respon.state ? respon.data : []  );
      setContarTotal(respon.state ? respon.countData : 0);
    }catch(e){
      return e;
    }finally{
      setLoading(false);
    }
   
  };
  const changePagination=async(val,sizePagination)=>{
    
    setPaginaActual(val);
    setCantidadContenido(sizePagination);
    await listarOrden(val-1,sizePagination);

 }
 const buscarDatos=async()=>{
  await listarOrden(paginaActual-1,cantidadContenido);
 }
  return (
    <div ref={listRef}   className="contentPago">
      {
        dataSeleccionada==null ?
        <>
          <>
          <Form
            form={form}
            layout="vertical"
          >
          <Row>
            <Col style={{padding:8}} xs={{ span: 24 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 6 }}>
                  <Form.Item 
                    name="rangos" label="Rangos"
                  
                  >
                    <RangePicker  
                      value={fechaRegistroFiltro==null ? [] : [moment(fechaRegistroFiltro[0], 'YYYY-MM-DD'), moment(fechaRegistroFiltro[1], 'YYYY-MM-DD')]}
                      onChange={(value)=>{
                        if( value==null){
                          setFechaRegistroFiltro(null);
                          return false;
                        }
                        if(value!=undefined ){
                          setFechaRegistroFiltro(value);
                        }else{
                          setFechaRegistroFiltro(null);
                        }
                      }
                      }  />
                </Form.Item>
              </Col>
            <Col style={{padding:8}} xs={{ span: 24 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 6 }}>
                <Form.Item 
                  name="correo" label="Correo"
                
                >
                  <Input name="correoInput"
                      placeholder="" 
                />
              </Form.Item>
            </Col>
            <Col style={{padding:8}} xs={{ span: 24 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 6 }}>
                <Form.Item 
                  name="codigoOrden" label="Código Orden"
                
                >
                  <Input name=""
                      placeholder="" 
                />
              </Form.Item>
            </Col>
            <Col style={{padding:8}} xs={{ span: 24 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 6 }}>
                <Form.Item 
                  name="documentoIdentidad" label="Documento Identidad"
                
                >
                  <Input name=""
                      placeholder="" 
                />
              </Form.Item>
            </Col>
            <Col style={{padding:8}} xs={{ span: 24 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 6 }}>
              <Form.Item 
                name="tipoEntrega" 
                label="Tipo Entrega">
                  <Select   
                    placeholder={""}  
                    bordered={true}  
                    name={"tipoEntregaSelect"}
                    label={"Seleccione Tipo Entrega"}>
                    <Option value={null} >{'Todos'}</Option>
                    <Option value={1} >{'Domicilio'}</Option>
                    <Option value={2} >{'Recojo'}</Option>
                      
                  </Select>
              </Form.Item>
            </Col>
            <Col style={{padding:8}} xs={{ span: 24 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 6 }}>
              <Button style={{marginTop:30}} type="primary" onClick={()=>{buscarDatos()}}>Buscar</Button>
   
            </Col>           
          </Row>
          
          </Form>
          </>
          <Table scroll={{ x: 'calc(1500px + 50%)', y: 640 }}
          dataSource={listadoDataOrden} 
          columns={columnsOrden} 
          pagination={false}
          loading={loading} />
          <Pagination  className="paginationTable paginationGestionUsuario" 
          current={paginaActual}  
          total={contarTotal}  onChange={(val,size)=>{changePagination(val,size)}} />
        </> :
        <AccionarOrdenComponent dataSeleccionada={dataSeleccionada} atras={()=>{atras();}} />   
      } 
       
        
    </div>
    );
 
}
export default GestionOrden;