import React from 'react';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
import { Bar } from 'react-chartjs-2';
import faker from 'faker';

import {useState,useEffect} from 'react';
ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);



const labels = ['January', 'February', 'March', 'April', 'May', 'June', 'July'];


//labelPaginasMasUsadas={labelPaginasMasUsadas} valorPaginasMasUsadas={valorPaginasMasUsadas}
export function AppVerticarBar(props) {
const [render,setRender] = useState(false); 
const [dataDataValuesSet,setDataDataValuesSet] = useState(null); 
const [optionsData,setOptionsData] = useState(null); 

const options = {
  responsive: true,
  plugins: {
    legend: {
      position: 'top',
    },
    title: {
      display: false,
      text: 'Paginas Mas usadas',
    },
  },
};

useEffect(()=>{
    let isSubscribed = true;

    setDataDataValuesSet({
        labels:props.labelPaginasMasUsadas,
        datasets:  [

            {
              label: 'Paginas mas Usadas',
              data: props.valorPaginasMasUsadas,
              backgroundColor: 'rgba(53, 162, 235, 0.5)',
            },
          ]
          
    });
    setRender(true);
    return () => isSubscribed = false;
  },[props.labelPaginasMasUsadas]);

if(props.valorPaginasMasUsadas.length>0 && props.labelPaginasMasUsadas.length>0){
    return <Bar options={{
        responsive: true,
        plugins: {
          legend: {
            position: 'top',
          },
          title: {
            display: false,
            text: 'Paginas Mas usadas',
          },
        },
      }} data={dataDataValuesSet} />;

 }else{
    return <div></div>;
 }
   
}
